<template>
  <div class="personal">
    <div class="toubu">
      <span class="userImg"><img src="../../../static/img/my/my_avatar_def@2x.png"/></span>
      <p><span class="regLogin"><router-link to="login" tag="a" href="javascript:;">登录</router-link> / <router-link to="register" tag="a" href="javascript:;">注册</router-link></span></p>
    </div>
    <ul class="mui-table-view">
      <li class="mui-table-view-cell" @click="goLogin">
        <a href="javascript:;" class="mui-navigate-right">我的订单</a>
      </li>
      <li class="mui-table-view-cell muitab" @click="goLogin">
        <div  href="javascript:;" class="biaoqian">
          <span><img src="../../../static/img/my/my_icon_payment@2x.png" /></span>
          <p>待付款</p>
        </div>
        <div class="biaoqian">
          <span><img src="../../../static/img/my/my_icon_wait@2x.png" /></span>
          <p>待发货</p>
        </div>
        <div class="biaoqian">
          <span><img src="../../../static/img/my/my_icon_send@2x.png" /></span>
          <p>已收货</p>
        </div>
        <div class="biaoqian">
          <span><img src="../../../static/img/my/my_icon_comment@2x.png" /></span>
          <p>待评价</p>
        </div>
        <div class="biaoqian">
          <span><img src="../../../static/img/my/my_icon_aftermarket@2x.png" /></span>
          <p>售后服务</p>
        </div>
      </li>
    </ul>
    <ul class="mui-table-view zhongjian">
      <li class="mui-table-view-cell" @click="goLogin">
        <a class="mui-navigate-right youhuiquan">
          <span class="tupian1"><img src="../../../static/img/my/my_list_icon_ticket@2x.png" /></span>
          <span class="tupian2">我的优惠券</span>
        </a>
      </li>
    </ul>
    <ul class="mui-table-view">
      <ul class="mui-table-view zhongjian">
        <li class="mui-table-view-cell" @click="goLogin">
          <a class="mui-navigate-right youhuiquan">
            <span class="tupian1"><img src="../../../static/img/my/my_list_icon_collect@2x.png" /></span>
            <span class="tupian2">我的收藏</span>
          </a>
        </li>
        <li class="mui-table-view-cell" @click="goLogin">
          <a class="mui-navigate-right youhuiquan">
            <span class="tupian1"><img src="../../../static/img/my/my_list_icon_address@2x.png" /></span>
            <span class="tupian2">我的地址</span>
          </a>
        </li>
      </ul>
    </ul>
  </div>
</template>

<script type="es6">
  export default{
    data(){
      return {
        msg:'qq',
      }
    },
    methods:{
        goLogin:function(){
          var btnArray = ['是', '否'];
          mui.confirm('是否去登录？', '您还未登录', btnArray, function(e) {
            if (e.index == 0) {
              mui.openWindow({
                url:'login'
              })
            } else {
              console.log('点击了取消')
            }
          })
        }
    }
  }
</script>

<style type="text/css" scoped>
  body{font-size:.2rem }
  .userImg{width:1.4rem;height:1.4rem;display: inline-block;border-radius: 1.4rem;margin-bottom: 0.3rem}
  .userImg img{width:100%;height:100%}
  .toubu{width:100%;height:3.2rem;text-align:center; background: url("../../../static/img/my/my_avatar_bg@2x.png") no-repeat;background-size: 100%;  padding-top: 0.7rem;}
  .biaoqian{width:20%;text-align: center}
  span img{width:.7rem}
  .regLogin{border:1px solid #fff;display: inline-block;border-radius: 4px;color:#fff}
  .regLogin a{padding:0.5rem 0.1rem;color:#fff}
  .zhongjian{margin:.2rem 0}
  .mui-navigate-right{text-align:left}
  .tupian1{display: block;  float: left;}
  .tupian2{display: block;  float: left;margin-left: 4px;line-height: .7rem}
  .muitab{display: flex;justify-content: space-around}
</style>



