<template>
  <div id="login">
    <router-link to="/home" tag="a" class="mui-icon mui-icon-left-nav" style="position: absolute;top:.1rem;left:0.1rem;color:#33bdeb"></router-link>
    <div class="logIn-logoBox">
      <div class="logIn-logo"></div>
    </div>
    <div class="inputborder">
      <input type="text" v-model="phoneNumber" placeholder="手机号/邮箱" style="font-size: .3rem;border:none"/>
    </div>
    <div class="inputborder">
      <input type="password" v-model="passWord" placeholder="密码"  style="font-size: .3rem;border:none"/>
    </div>
    <div class="goToRegister-backPassWord">
      <router-link tag="a" to="register" class="goToRegister">立即注册</router-link>
      <router-link to="forgetPad" tag="a" class="backPassWord" style="color:#0062cc">忘记密码?</router-link>
    </div>
    <div class="mui-btn mui-btn-primary" style="width:100%;font-size: .3rem" @click="login">登录</div>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  import { Toast } from 'mint-ui';
  export default{
    data(){
      return {
        phoneNumber:this.$route.query.d1,
        passWord:''
      }
    },
    beforeCreate () {
      document.querySelector('body').setAttribute('style', 'background:#fff')
    },
    beforeDestroy () {
      document.querySelector('body').setAttribute('style', '')
    },
    mounted(){
      if(getCookie('username')){
        this.$router.push('/home')
      }
      window.localStorage.laiZi = 'login'
    },
    methods:{
      login(){
        if(this.phoneNumber == "" || this.passWord == ""){
          Toast({message: '手机或密码不能为空',position: 'bottom'});
        }else{
          let data = {'loginId':this.phoneNumber,'loginPwd':this.passWord}
          /*接口请求*/
          var url = Api.Apis + '/h5/h5login/userDefaultLogin'
          this.$http.post(url,data,{emulateJSON:true}//POST请求需要将emulateJSON设置为true
          ).then((data)=>{
            console.log(data)
            if (data.status == 200 && data.ok == true) {
              var datas = data.body
              if(datas.state=="success"){
                  /*console.log('用户名'+datas.userId)
                  mui.toast('登陆成功'+this.phoneNumber)*/
                setCookie('userId',datas.userId,1000*60)
                setTimeout(function(){
                  this.$router.push('/userLogin')
                }.bind(this),200)

              }else{
                Toast({message: datas.errMsg,position: 'bottom'});
              }
            }else{
              Toast({message: "网络错误",position: 'bottom'});
            }
          })
        }
      }
    }
  }
</script>

<style type="text/css" scoped>
  #login{padding:14% 10% 0 10%;}
  #login .goToRegister-backPassWord a{font-size: .25rem}
  #login .inputborder {border-bottom: 1px solid #c0c0c0;margin-bottom: .4rem}
  #login .inputborder input{margin:0;padding:0.15rem 0;height: 100%}
  .logIn-logoBox {
    -webkit-box-pack: center;
    -moz-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-orient: horizontal;
    -moz-box-orient: horizontal;
    -webkit-box-direction: normal;
    -moz-box-direction: normal;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    flex-direction: row;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    padding: 0.33rem 0.8rem 0 0.8rem;
    margin-bottom: 0.3rem;
  }
  .logIn-logoBox .logIn-logo {
    width: 1.96rem;
    height: 2.13rem;
    background: url("data:image/png;base64,") no-repeat;
    background-size: 100%;
    margin-bottom: 1rem;
  }
  .goToRegister-backPassWord{display:flex;margin-bottom: 1rem;justify-content: space-between;}
  .goToRegister{color:#8f8f94}
</style>



