<template>
  <div id="seekd">
     <header id="header" class="mui-bar mui-bar-nav" style="background:#fff;box-shadow: none">
      <a class="mui-btn mui-btn-blue mui-btn-link mui-pull-right" @click="seekGoods">搜索</a>
      <div class="search">
        <span class="mui-icon mui-icon-search"></span>
        <input type="text" class="to_search" value="日本进口面膜" v-model="goodsName" />
      </div>
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click.stop="goBack" style="color:#4595e5"></a>
    </header>
    <div class="orderList-null" v-show="!list.length">
      <div class="orderList-null-icon"></div>
      <div class="orderList-null-title">您搜索的商品还未上架，敬请期待！</div>
    </div>
    <div class="news_main mui-content" v-show="list.length">
      <!--<p v-show="!list.length" style="background: #fff;margin:0 auto">您搜索的商品还未上架，敬请期待！</p>-->
      <ul class="mui-table-view mui-grid-view" style="margin: 0;text-align: left">
        <li class="mui-table-view-cell mui-media mui-col-xs-6" v-for="(item,indenx) in list">
          <router-link :to="{path:'details',query:{ids:item.goodId}}" >
            <img class="mui-media-object" :src=item.goodPicUrl>
            <div class="mui-media-body">{{item.goodName}}</div>
            <p class="news_main_prices">￥{{item.goodMinPrice|substr}}</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type='es6'>
  import top_back from "../commom/top_back.vue";
  import Api from '../commom/API.js';
  import Vue from 'vue';
  import { Toast } from 'mint-ui';
  import {Loadmore} from 'mint-ui';
  export default {
    data(){
      return {
        scrollMode:"auto",
        goodsName:this.$route.query.gN,
        list:[],
        indenx:0,
        newList:[],
        allLoaded:'',
        allLoaded:false,
        bottomtext:"加载中请稍后",
        topStatus:'loading',
        titleName:'',
        liShi:[],
        tipShow:false
      }
    },
    components:{
      top_back,
      load_more:Loadmore
    },
    mounted(){
      this.getgoodsName();
      this.height()
    },
    methods:{
      goBack(){
          this.$router.go(-1);
      },
      height:function(){
        var gaoDu = document.querySelector("#seekd");
        gaoDu.style.backgroundColor = '#fff';
        gaoDu.style.height =  window.screen.availHeight + 'px';
      },
      getgoodsName(){
        let url = Api.Apis + '/h5/h5search/searchList';
        let data = {'goodName': this.goodsName,'index':0,'size':9999};
        console.log('AA',data)
        this.liShi = JSON.parse(localStorage.getItem('hN'));
        // 写入localStorage
        if(this.liShi==null){
          // this.liShi必须为数组。否则push方法报错。
            this.liShi=[];
        }
        this.liShi.push(this.goodsName);
        window.localStorage.setItem('hN',JSON.stringify(this.liShi));
        this.$http.post(url, data, {emulateJSON: true}
        ).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
                console.log(datas.totalCount);
                this.list = datas.goodBeanList;
            } else {
              Toast({message: datas.errMsg,position: 'bottom'});
            }
          }
        })
      },
      seekGoods(){
          this.getgoodsName();
      }
    },
  }
</script>
<style type="text/css" scoped>
  .search{
    position: absolute;
    top: 50%;
    left: 50%;
    padding-left: 0.3rem;
    margin-left: -35%;
    margin-top: -0.32rem;
    overflow: hidden;
    width: 70%;
    height: 0.64rem;
    border: 1px solid #f0f0f0;
    border-radius: 0.32rem;
    background: #fff;
    }
    .search .mui-icon,.search input{
      background: #EBEBEB;
    }
  .mui-icon-search{
      position: absolute;
      font-size: 0.3rem;
      left: 0;
      top: -10%;
      display: inline-flex;
      margin-left: 0.16rem;
      width: 0.32rem;
      height: 0.32rem;
      background-size: 100%;
  }
 .search input{
    padding: 0;
    padding-left: 0.4rem;
    font-size: 0.28rem;
    outline: none;
    border: none;
    width: 100%;
    height: 100%;
    margin: 0;
   background: #fff;
  }
  .mui-content{
    background: #fff;
    height:100%
  }
  .header{
    position: relative;
    height: 0.90rem;
    padding: 0.11rem 0.2rem;
  }
  .header a{
    position: absolute;
    left: 0.2rem;
    top: 50%;
    width: 0.48rem;
    height: 0.48rem;
    line-height: 0.48rem;
    margin-top: -0.24rem;
    margin-left: 0;
    color: #4595e5;
    background: #fff;
  }
  .header i{
    float: right;
    width: 10%;
    padding: 0;
    height: 0.68rem;
    font-size: 0.32rem;
    line-height: 0.68rem;
    text-align: center;
    font-style: normal;
    color: #007aff;
  }
  .mui-icon.mui-icon-search{
    position: absolute;
    width: 0.36rem;
    height: 0.36rem;
    top: 20%;
    left: 0.1rem;
    margin: 0;
    margin-top: -0.18rem;
    line-height: 0.36rem;
    background: #fff;
  }
</style>
<style lang='less' scoped>
  #seekd{
    .news_main{
      ul{
        margin: 0;
        padding: 0;
        background-color: #fff;
        text-align: left;
        li{
          padding: 15px;
          padding-right: 5px;
          text-align: left;
          .news_main_prices{
            font-size: 0.29rem;
            color: red;
          }
          img{
            height:2.6rem;
          }
          div{
            height: 0.3rem;
            line-height: 0.3rem;
            font-size: 0.28rem;
          }
          a{
            background-color: #ffffff;
          }
        }
      }
    }
    .orderList-null {
      width: 6rem;
      margin: 0 auto;
      padding-top: 4rem;
    }
    .orderList-null .orderList-null-icon {
      width: 3rem;
      height: 3rem;
      background: url("../../../static/img/default-page/common@3x.png") no-repeat;
      background-size: 100%;
      margin: 0 auto;
    }
    .orderList-null .orderList-null-title {
      width: 100%;
      margin-top: 0.2rem;
      font-size: 0.28rem;
      line-height: 0.28rem;
      color: #666;
      text-align: center;
    }

  }
</style>




