<template>
  <div id="regnext">
    <header class="mui-bar mui-bar-nav" style="border-shadow: none;background: #fff">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">输入密码</h1>
    </header>
    <div class="mui-content">
      <p style="margin-top: 0.2rem;text-align:left;padding-left: 3%">请输入您的新密码</p>
      <form class="mui-input-group" style="padding-right:5px">
        <div class="mui-input-row" style="display: flex;justify-content: space-between;align-items: center">
          <input type="password" class="mui-input-clear" placeholder="密码" v-model="passWord">
        </div>
        <div class="mui-input-row">
          <input type="password" class="mui-input-clear" placeholder="重复密码" v-model="quePaw">
        </div>
      </form>
      <div class="mui-content-padded" align="center">
        <button type="button" class="mui-btn mui-btn-blue" id="wanCheng" style="width:96%" @click="wanCheng">完成</button>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import { Toast } from 'mint-ui';
  import Api from '../commom/API.js';
  export default {
    data(){
      return {
        passWord:'',
        quePaw:'',
        phone:this.$route.query.d1,
        code:this.$route.query.d2
      };
    },
    methods: {
      wanCheng:function(){
        if(this.passWord==this.quePaw && this.passWord != ''){
          //验证验证码
          let url = Api.Apis + '/h5/h5password/forgetUserPassword';
          let data = {'phone': this.phone,'password':this.passWord,'code':this.code};
          this.$http.post(url, data, {emulateJSON: true}
          ).then(function (data) {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body;
              if (datas.state == "success") {
                this.$router.push({path:'/login',query:{d1:this.phone}})
              } else {
                Toast({message: datas.errMsg,position: 'bottom'});
              }
            }
          })
        }else if(this.passWord == ''){
          Toast({message: '密码不能为空！',position: 'bottom'});
        }else{
          Toast({message: '密码输入不一致！',position: 'bottom'});
        }
      },
      goBack:function () {
        this.$router.go(-1)
      }
    }
  }
</script>

<style scoped>

</style>



