<template>
  <div id="register">
    <header class="mui-bar mui-bar-nav" style="border-shadow: none;background: #fff">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">忘记密码</h1>
    </header>
    <div class="mui-content">
      <form class="mui-input-group" style="margin-top: 0.2rem;padding-right:5px">
        <div class="mui-input-row" style="display: flex;justify-content: space-between;align-items: center">
          <input type="number" placeholder="手机号" maxlength="11" v-model="phone">
          <button type="button" class="mui-btn" style="padding:0.1rem;width:44%" disabled v-show="this.phone.length!=11">点击获取验证码</button>
          <button type="button" class="mui-btn mui-btn-primary" style="padding:0.1rem;width:44%;display: none" @click="sendSMS" v-show="this.phone.length==11" id="yzm">点击获取验证码</button>
        </div>
        <div class="mui-input-row">
         <input type="number" class="mui-input-clear" placeholder="请输入验证码" v-model="yzmCode">
        </div>
      </form>
      <div class="mui-content-padded" align="center">
        <button type="button" class="mui-btn mui-btn-blue" id="regNext" style="width:96%" @click="regNext">下一步</button>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        phone:'',
        yzmCode:''
      }
    },
    methods: {
      // 点击获取验证码
      sendSMS(){
        let pattern = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,3,6,7,8])|(19[7]))\d{8}$/;
        if(!(pattern.test(this.phone))) {
          Toast({message: '手机号格式错误',position: 'bottom'});
            return;
        };
        // 倒计时
        var num = 60;
        var sendVerificationCode = document.getElementById("yzm");
        sendVerificationCode.disabled=true;
        function daojishi() {
          num--;
          sendVerificationCode.innerHTML = num + 's后重试';
          if (num < 1) {
            sendVerificationCode.innerHTML = "获取验证码";
            clearInterval(timer, 60000);
            sendVerificationCode.disabled=false;
          }
        };
        var timer = setInterval(daojishi, 1000);
        // 获取验证码
        let url = Api.Apis + '/h5/h5sendsms/sendSMSForForgetPwd';
        let data = {'phone': this.phone};
        this.$http.post(url, data, {emulateJSON: true}
        ).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            if (datas.state == "success") {
              Toast({message: datas.successMsg,position: 'bottom'});
            } else {
              Toast({message: datas.errMsg,position: 'bottom'});
              clearInterval(timer);
              sendVerificationCode.disabled=false;
            }
          }
        },function(err){
          Toast({message: '网络错误',position: 'bottom'});
          clearInterval(timer);
          sendVerificationCode.disabled=false;
        });
      },
      // 下一步
      regNext: function () {
        if (this.phone == '') {
          Toast({message: '手机号不能为空',position: 'bottom'});
          return;
        }
        if (this.yzmCode == '') {
          Toast({message: '验证码不能为空',position: 'bottom'});
          return;
        }
        //验证验证码
        let url = Api.Apis + '/h5/h5sendsms/checkSMSLogForForgetPwd';
        let data = {'phone':this.phone,'code':this.yzmCode};
        console.log('验证码数据类型：'+typeof(this.yzmCode),this.yzmCode);
        this.$http.post(url,data,{emulateJSON: true}).then(function (data) {
            console.log('if前面的',data);
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            if (datas.state == "success") {
              Toast({message: datas.successMsg,position: 'bottom'});
              this.$router.push({path:'/forgetPaswNext',query:{d1:this.phone,d2:this.yzmCode}})
            } else {
              Toast({message: datas.errMsg,position: 'bottom'});
            }
          }
        });
      },
      goBack:function () {
        this.$router.go(-1)
      },
    }
  }
</script>
<style scoped>

 </style>



