<template>
  <div class="toTaoBao">
   <div class="waiKuang">
      <div class="tittle">长按框内>拷贝</div>
      <textarea id="textAA" class="textArea">
       丁新星员工优惠券，使用￥丁新星员工专属优惠券￥抢先预览（长按复制整段文案，打开手机淘宝即可进入活动内容)
      </textarea>
   </div>
    <div class="btn" @click="copy">一键复制</div>
    <p class="shuoMi">复制/拷贝后，请打开手机淘宝购买！</p>
  </div>
</template>

<script type="es6">
  import Clipboard from 'clipboard';
  export default{
    data(){
      return {
        selected:'1'
      }
    },
    beforeCreate () {
      document.querySelector('body').setAttribute('style', 'background:#fff')
    },
    beforeDestroy () {
      document.querySelector('body').setAttribute('style', '')
    },
    methods:{
      copy(){
        var ele = document.getElementById("textAA");
        ele.select();
        document.execCommand("Copy");
      }
    },
  }
</script>

<style type="text/css" scoped>
  .waiKuang{border:2px dashed #f54c21;width:92%;margin: .7rem auto 0;position: relative;height:1.9rem}
  .textArea{font-size: .3rem;border: none;height:1.8rem;text-align: center;padding-top: .4rem;color: #f84c28;padding-left: 0;overflow: hidden}
  .tittle{position: absolute;top:-0.25rem;left: 33%;width:2.3rem;height:.55rem;line-height: .6rem;background: #f54c21;color:#fff}
  .btn{background: #f54c21;color:#fff;width:2.4rem;height:.8rem;margin: .22rem auto;line-height: .8rem;border-radius: .8rem;font-size: .35rem}
  .shuoMi{color:#138fcd}
</style>



