<template>
  <div class="changPhone">
    <header class="mui-bar mui-bar-nav" style="box-shadow: none">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">修改手机号</h1>
    </header>
    <div class="mui-content" v-if="yanShow">
      <p style="text-align: left;font-size: .3rem;margin:.3rem">请输入原来的手机号以完成验证</p>
      <div style="padding:0 .3rem">
        <ul class="mui-table-view">
          <li class="mui-table-view-cell" style="padding:0.2rem;font-size: 0.3rem">{{userPhoneShow}}</li>
        </ul>
        <ul class="mui-table-view" style="margin: .4rem 0">
          <input type="number" placeholder="请输入手机号" v-model="inpPhone" class="mui-input-clear" style="width:100%;border:none;margin-bottom: 0;text-align: center"/>
        </ul>
        <mt-button type="primary" style="width:100%" @click="yanZheng">验证</mt-button>
      </div>
    </div>

    <div class="mui-content" v-if="!yanShow">
      <p style="text-align: left;font-size: .3rem;margin:.3rem">请输入新手机号码，我们会发送验证码到手机上</p>
      <div style="padding:0 .3rem">
        <ul class="mui-table-view">
          <input type="number" placeholder="请输入手机号" v-model="inpNewPhone" class="mui-input-clear" style="width:100%;border:none;margin-bottom: 0;text-align: center"/>
        </ul>
        <ul class="mui-table-view" style="margin: .4rem 0">
          <input type="number" placeholder="请输入验证码" v-model="newYanma" class="mui-input-clear" style="width:100%;border:none;margin-bottom: 0;text-align: center"/>
        </ul>
        <mt-button disabled style="width:100%" v-if="inpNewPhone.length!=11">获取验证码</mt-button>
        <mt-button type="primary" style="width:100%" v-if="inpNewPhone.length==11" id="yzm" @click="getYan">获取验证码</mt-button>
       <!-- <button type="button" class="mui-btn mui-btn-primary" style="width:100%;" @click="getYan" v-if="inpNewPhone.length==11" id="yzm">获取验证码</button>-->
        <mt-button type="primary" style="width:100%;margin-top: .4rem" @click="bangDing">绑定</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        userID:'',
        inpPhone:'',
        userPhoneShow:'',
        userPhoneYan:'',
        inpNewPhone:'',
        newYanma:'',
        yanShow:true,
        nextGet:false
      }
    },
    mounted(){
      this.getInformation()
    },
    methods: {
      //返回上一级
      goBack:function () {
        this.$router.go(-1)
      },
      // 获取页面数据
      getInformation(){
        /*页面挂载获取保存的cookie值，渲染到页面上*/
        this.userID = getCookie('userId');
        /*如果cookie不存在，则跳转到登录页*/
        if (this.userID == "") {
          this.$router.push('/login')
        }else{
          let data = {'userId': this.userID};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5MyInfo/getUserInfo';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                this.userPhoneShow = datas.user.userPhone.substr(0, 3) + '****' + datas.user.userPhone.substr(7);
                this.userPhoneYan = datas.user.userPhone;
              }
            }
          })
        }
      },
      //验证
      yanZheng(){
          if(this.inpPhone == this.userPhoneYan){
            this.yanShow = false
          }else{
            Toast({message: '亲，输入的手机号码不一致哦！请输入原手机号码',position: 'bottom'})
          }
      },
      //发送验证码
      getYan(){
          this.nextGet = true;
          let pattern = /^((1[3,5,8][0-9])|(14[5,7])|(17[0,3,6,7,8])|(19[7]))\d{8}$/;
          if(!(pattern.test(this.inpNewPhone))) {
            Toast({message: '手机号格式错误',position: 'bottom'});
            return;
          };
          // 倒计时
          var num = 60;
          var sendVerificationCode = document.getElementById("yzm");
          sendVerificationCode.disabled=true;
          // 获取验证码
          let url = Api.Apis + '/h5/h5sendsms/sendSMSForRegister';
          let data = {'phone': this.inpNewPhone};
          this.$http.post(url, data, {emulateJSON: true}
          ).then(function (data) {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body;
              if (datas.state == "success") {
                Toast({message: datas.successMsg,position: 'bottom'});
                function daojishi() {
                  num--;
                  sendVerificationCode.innerHTML = num + 's后可重发';
                  if (num < 1) {
                    sendVerificationCode.innerHTML = "获取验证码";
                    clearInterval(timer, 60000);
                    sendVerificationCode.disabled=false;
                  }
                };
                var timer = setInterval(daojishi, 1000);
              } else {
                Toast({message: datas.errMsg,position: 'bottom'});
              }
            }
          });
        },
      //绑定手机号
      bangDing(){
        if (this.inpNewPhone.length == 0) {
          Toast({message: '手机号不能为空',position: 'bottom'});
          return;
        }
        if (this.newYanma.length == 0) {
          Toast({message: '密码不能为空',position: 'bottom'});
          return;
        }
        if(this.nextGet){
          let data = {'userId': this.userID,'phone':this.inpNewPhone,'code':this.newYanma};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5MyInfo/updatePhone';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                this.$router.push('/pZiliao')
              }
            }
          })
        }else{
          Toast({message: '请先获取验证码',position: 'bottom'});
        }
      }
    }
  }
</script>


<style type="text/css" scoped>

</style>



