<template>
  <div class="mycoupon">
    <header class="mui-bar mui-bar-nav" style="background: #fff">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595E5"></a>
      <h1 class="mui-title">地址管理</h1>
    </header>
    <nav class="mui-bar mui-bar-tab addMain">
      <router-link :to="{path:'/addAddress',query:{d3:uname}}" tag="button" type="button" class="mui-btn mui-btn-primary" style="width:96%;margin-bottom: 0.3rem">
        + 新增收货地址
      </router-link>
    </nav>
    <div class="mui-content">
      <div class="addMain1"></div>
      <ul class="mui-table-view" style="margin-bottom: .2rem" v-for="(item,index) in address">
        <li class="mui-table-view-cell" @click="xuanAdd(item.addrid)">
          <div class="addXinxi"><p style="font-size: 0.4rem">{{item.name}}</p><p style="font-size: 0.4rem">{{item.phone}}</p></div>
          <div class="xiangXi">{{item.address}}</div>
        </li>
        <li class="mui-table-view-cell addCaozuo">
          <div>
            <span class="xuanze" @click="ceshi($event,item.addrid)" :class="{'xuanzeBlue':item.isAvalible}"></span>
            <p style="margin-left: 1rem">设为默认地址</p>
          </div>
          <div class="bianShan">
            <router-link :to="{path:'/editAddress',query:{aid:item.addrid}}" tag="p" style="margin-right: 0.15rem"><span class="mui-icon mui-icon-compose"></span>编辑</router-link>
            <p @click="delThat(item.addrid)"><span class="mui-icon mui-icon-trash del"></span>删除</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  export default{
    data(){
      return {
        address: [],
        uname:'',
        ppt:'1',
        to:this.$route.query.to
      }
    },
    mounted(){
      this.addre()
    },
    methods: {
        // 选择地址
      xuanAdd(adid){
          if(window.localStorage.woLai=='0') {
            window.localStorage.addrId = adid;
            this.$router.push('/enterOrder')
          }
      },
        // 根据用户id获取地址信息
      addre(){
        this.uname = getCookie('userId')
        var data = {'userid': this.uname};
        var url = Api.Apis + '/h5/h5address/getUserAddresses';
        this.$http.post(url, data, {emulateJSON: true}).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            this.address = datas.adresses;
          }
        })
      },
      //返回上一级
      goBack: function () {
          if(window.localStorage.woLai=='0') {
            window.localStorage.addrId = 0;
            this.$router.push('/enterOrder')
          }else if(window.localStorage.woLai=='1'){
            this.$router.push('/userLogin')
          }
      },
      // 删除地址
      delThat: function (index) {
        console.log(index);
        var data = {'adrid': index};
        var url = Api.Apis + '/h5/h5address/deletUserAddress';
        this.$http.post(url, data, {emulateJSON: true}).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              this.addre()
            } else {
              console.log('删除失败')
            }
          }
        })
      },
      // 设为默认地址
      ceshi(e,addrid){
        var btns = document.getElementsByClassName('xuanze');
        for (let i = 0; i < btns.length; i++) {
          btns[i].style.backgroundImage = "url(../../../static/img/shopping-bag/common_icon_empty@2x.png)";
        }
        e.target.style.backgroundImage = "url(../../../static/img/shopping-bag/common_icon_chose@3x.png)";
        var data = {'adrid': addrid};
        var url = Api.Apis + '/h5/h5address/setDefault';
        this.$http.post(url, data, {emulateJSON: true}).then((data) => {
          if (data.status == 200 && data.ok == true) {
            console.log('成功')
          }
        })
      }
    }
  }
</script>

<style type="text/css">
    body{font-size: .2rem}
    .addXinxi , .addCaozuo , .bianShan{display: flex;justify-content: space-between;align-items:center}
    .addCaozuo{padding-top:0;padding-bottom: 0;padding-left: 0;height:.6rem}
    .addMain{box-shadow: none;display: flex;justify-content: center;}
    .addMain1{margin:0;height:.1rem;background: url(../../../static/img/warehouse/color-line.png) repeat-x top center;}
    .mui-radio input[type='radio']:checked:before {content: '\e442';}
    .xiangXi{
      text-align: left;
      margin-top:0.2rem;
      color:#999999;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .mui-checkbox input[type='checkbox']:before {
      font-size: 20px !important;
    }
    .xuanze{position: absolute;display: block;top:.1rem;left:.4rem;background: url(../../../static/img/shopping-bag/common_icon_empty@2x.png) ;background-size: 100% 100%;width:.4rem;height:.4rem}
    .xuanzeBlue{background-image: url(../../../static/img/shopping-bag/common_icon_chose@3x.png);background-size: 100% 100%;border: none;}
</style>



