import * as types from './types';
export default {
  [types.SHOW_FOOT]:(state)=>{
    state.blFoot=true;
  },
  [types.HIDE_FOOT]:(state)=>{
    state.blFoot=false;
  },
  [types.SHOW_LOADING]:(state)=>{
    state.blLoading=true;
  },
  [types.HIDE_LOADING]:(state)=>{
    state.blLoading=false;
  },
  [types.ALLCOUNT]:(state,n)=>{
    state.allcount=n;
  },
  [types.GOODCOUNT]:(state,n)=>{
    state.goodcount=n;
  },
  [types.MIDCOUNT]:(state,n)=>{
    state.midcount=n;
  },
  [types.BADCOUNT]:(state,n)=>{
    state.badcount=n;
  },
  [types.HASCOUNT]:(state,n)=>{
    state.hascount=n;
  },
}



