<template>
  <div class="myorder">
    <header class="mui-bar mui-bar-nav" style="background:#ffffff;">
      <router-link to="/userLogin" tag="a" class="mui-icon mui-icon-left-nav mui-pull-left" style="color:#4595e5;"></router-link>
      <h1 class="mui-title">我的订单</h1>
    </header>
    <div id="slider" class="mui-slider mui-content">
        <div id="sli">
          <router-link tag="a" to="/myorder/order/0">全部</router-link>
          <router-link tag="a" to="/myorder/order/1">待付款</router-link>
          <router-link tag="a" to="/myorder/order/2">待发货</router-link>
          <router-link tag="a" to="/myorder/order/3">待收货</router-link>
          <router-link tag="a" to="/myorder/order/4">待评价</router-link>
        </div>
        <div class="mui-slider-group">
          <router-view :key="key"></router-view>
        </div>
    </div>
  </div>
</template>

<script type="es6">
  export default{
    data(){
      return {
        overhang:[]
      }
    },
    computed: {
      key() {
        return this.$route.name !== undefined? this.$route.name + +new Date(): this.$route + +new Date()
      }
    }
  }
</script>

<style type="text/css" scoped>
  p{text-align: left;font-size: .25rem}
  .mui-media img{margin-top:.2rem}
  #sli{display: flex;align-items: center;width:100%}
  #sli a{display: block;padding:0.2rem 0;width:20%;border-bottom: .05rem solid rgba(0,0,0,0);font-size: .3rem}
  #sli .router-link-active{border-bottom: .05rem solid #4595e5;color: #4595e5}
</style>



