//自定义一个全局过滤器截取字符串
Vue.filter('substr',function(val){
  var str = val.toString(),
    newstr =  (str/100).toFixed(2);
  return newstr
});
//自定义一个倒计时时间格式过滤器
Vue.filter('countDown',function(val){
  let day = Math.floor(val/86400000);
  let hour= Math.floor((val/3600000)%24);
  let min= Math.floor((val/60000)%60);
  let sec= Math.floor((val/1000)%60);
  hour = hour < 10 ? "0" + hour : hour;
  min = min < 10 ? "0" + min : min;
  sec = sec < 10 ? "0" + sec : sec;
  let newTime = hour + ":" + min + ":" + sec;
  return newTime
});
//自定一个日期格式化过滤器
Vue.filter("formatTime",function(value,type){
  var dataTime="";
  var data = new Date();
  data.setTime(value);
  var year   =  data.getFullYear();
  var month  =  data.getMonth() + 1;
  var day    =  data.getDate();
  var hour   =  data.getHours();
  var minute =  data.getMinutes();
  var second =  data.getSeconds();
  month  =  month>9?month:'0'+month;
  day    =  day>9?day:'0'+day;
  hour   =  hour>9?hour:'0'+hour;
  minute =  minute>9?minute:'0'+minute;
  second =  second>9?second:'0'+second;
  if(type == "YMD"){
    dataTime =  year + "年"+ month + "月" + day + "日";
  }else if(type == "YMDHMS"){
    dataTime = year + "年"+month + "月" + day + "日 " +hour+ ":"+minute+":" +second;
  }else if(type == "HMS"){
    dataTime = hour + ":" + minute + ":" + second;
  }else if(type == "YM") {
    dataTime = year + "年" + month + "月";
  }else if(type == "Y-M") {
    dataTime = year + "-"+month + "-" + day + " " +hour+ ":"+minute+":" +second;
  }else if(type == "MM-SS") {
    dataTime = minute+"分" +second+"秒";
  }
  return dataTime;//将格式化后的字符串输出到前端显示
});
// 引入状态管理
