<template>
  <div class="mycoupon">
    <div class="XinXi" v-show="isShow">
      <header class="mui-bar mui-bar-nav" style="background: #fff">
        <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595E5"></a>
        <h1 class="mui-title">编辑收货地址</h1>
        <a class="mui-btn mui-btn-blue mui-btn-link mui-pull-right" @click="delAll">删除</a>
      </header>
      <nav class="mui-bar mui-bar-tab" style="box-shadow: none;display: flex;justify-content: center;">
        <button type="button" class="mui-btn mui-btn-primary" style="width:96%;margin-bottom: 0.3rem" @click="saveAdd">
          保存
        </button>
      </nav>
      <div class="mui-content">
        <ul class="mui-table-view" style="margin: 0;">
          <li class="mui-table-view-cell">
            <input type="text" value="" placeholder="姓名" v-model="userName"/>
          </li>
          <li class="mui-table-view-cell">
            <input type="text" value="" placeholder="手机号" v-model="phone"/>
          </li>
          <li class="mui-table-view-cell" @click="xuanAddr">
            <input type="text" class="address-position" disabled="disabled" placeholder="省市区" v-model="diQU">
          </li>
          <li class="mui-table-view-cell">
          <textarea  rows="3" placeholder="详细地址" v-model="smallAdd">
          </textarea>
          </li>
        </ul>
        <ul class="mui-table-view" style="margin-top: .2rem">
          <li class="mui-table-view-cell" style="display: flex;justify-content: space-between;align-items: center;padding-top:5px;padding-bottom:5px">
            <p>设为默认</p>
            <mt-switch v-model="isAvali"></mt-switch>
          </li>
        </ul>
      </div>
    </div>
    <div class="addXuan" v-show="!isShow">
      <header class="mui-bar mui-bar-nav" style="background: #fff">
        <h1 class="mui-title">选择地区</h1>
        <button class="mui-btn mui-btn-blue mui-btn-link mui-pull-right" @click="queDing">确定</button>
      </header>
      <div class="mui-content">
        <v-distpicker type="mobile" @selected="onSelected"></v-distpicker>
      </div>
    </div>
  </div>
</template>

<script>
  import VDistpicker from 'v-distpicker'
  import Api from '../commom/API.js';
  import { Toast } from 'mint-ui';
  export default {
    components: { VDistpicker },
    data(){
      return {
        isShow: true,
        addID:this.$route.query.aid,
        userName:'',
        phone:'',
        province:'', //省
        city:'',     //市
        district:'', //区
        smallAdd:'',  //详细地址
        diQU:'',
        isAvali:'',
        kaiGuan:true
      }
    },
    mounted(){
      this.addre()
    },
    methods: {
      xuanAddr(){
        this.isShow = false
      },
      queDing(){
        this.isShow = true
      },
      //渲染数据
      addre(){
        var data = {'adrid': this.addID};
        var url = Api.Apis + '/h5/h5address/getAddressById';
        this.$http.post(url, data, {emulateJSON: true}).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            console.log("数据",datas)
            this.userName = datas.address.name;
            this.phone = datas.address.phone;
            this.province = datas.address.province;
            this.city = datas.address.city;
            this.district = datas.address.area;
            this.diQU = this.province + ' ' + this.city + ' ' +  this.district;
            this.smallAdd = datas.address.addressDetail;
            if(datas.address.isAvalible){
              this.isAvali=true
            }else{
              this.isAvali=false
            }
          }
        })
      },
      //获取所选地址
      onSelected(data) {
        this.diQU = data.province.value + ' ' + data.city.value + ' ' + data.area.value;
        this.province = data.province.value;
        this.city = data.city.value;
        this.district = data.area.value;
      },
      // 保存修改后的数据
      saveAdd(){
        if(this.userName ==''){
          Toast({message: '姓名不能为空',position: 'bottom'});
          return;
        }
        if(this.phone ==''){
          Toast({message: '手机号不能为空',position: 'bottom'});
          return;
        }
        if(!(/^((1[3,5,8][0-9])|(14[5,7])|(17[0,3,6,7,8])|(19[7]))\d{8}$/.test(this.phone))) {
          Toast({message: '手机号格式错误',position: 'bottom'});
          return;
        }
        if(this.diQU ==''){
          Toast({message: '省市区不能为空',position: 'bottom'});
          return;
        }
        if(this.smallAdd ==''){
          Toast({message: '请填写详细地址',position: 'bottom'});
          return;
        }
        let url = Api.Apis + '/h5/h5address/editUserAddress';
        let data = {
          'adrid': this.addID,
          'adrName': this.userName,
          'adrPhone': this.phone,
          'adrProvince': this.province,
          'adrCity': this.city,
          'adrDistrict': this.district,
          'adrDetail': this.smallAdd,
          'isAvalible': this.isAvali?1:0
        };
        this.$http.post(url, data, {emulateJSON: true}
        ).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              this.$router.push('/address')
            } else {
              console.log('修改失败')
            }
          }
        });
      },
      goBack:function () {
        this.$router.go(-1)
      },
      // 删除地址
      delAll() {
        var data = {'adrid': this.addID};
        var url = Api.Apis + '/h5/h5address/deletUserAddress';
        this.$http.post(url, data, {emulateJSON: true}).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              console.log('删除成功');
              this.$router.push('/address')
            } else {
              console.log('删除失败')
            }
          }
        })
      }
    }
  }
</script>


<style type="text/css" scoped>
  .mui-table-view-cell input, .mui-table-view-cell textarea{margin:0;padding:0;border:0;}
  select{width:90%;margin:1%;font-size: 0.4rem;}
  #middlePopover{height:6rem;width:95%;overflow-y: scroll}
</style>



