<template>
  <span class='orange'>{{time}}</span>
</template>
<script>
  export default{
    data(){
      return {
        timer: '',
        tim:''
      };
    },
    props: ['endtime'],
    mounted(){
      this.daoji();
    },
    methods:{
      daoji(){
        this.tim = this.endtime;
        let TT = setInterval(function () {
          this.tim-=1000;
          if(this.tim<0){
            this.tim=0;
            clearInterval(TT);
          }
        }.bind(this),1000)
      },
      formate(time){
        let min = parseInt((time/1000)/60);
        let sec = parseInt(time/1000 - min * 60);
        min = min > 9 ? min : '0' + min;
        sec = sec > 9 ? sec : '0' + sec;
        return min + ':' + sec;
      }
    },
    computed:{
      'time': function(){
        return this.formate(this.tim);
      }
    },
  }
</script>
<style>
  .orange{
    color: #fff;
  }
</style>



