<template>
  <div id="classify">
    <div class="mui-slider-item mui-control-content mui-active ">
      <div class="assortconterFu clearfix">
        <router-link tag="div" :to="{path:'/classify_d',query:{yid:pids,eid:item.typeId,name:item.typeName}}" class="dl" v-for="(item,index,key) in showContent" :key="key">
          <div class="dt"><img :src="item.typePicUrl"/></div>
          <div class="dd">{{item.typeName}}</div>
        </router-link>
      </div>
      <div class="dianDi"></div>
    </div>

  </div>
</template>

<script type="es6">
  import Api from '../../components/commom/API.js'
  export default{
    data(){
      return {
        pids:'2',
        showContent:[]
      }
    },
    // 监听url的变化
    created(){
      this.pids = this.$route.params.aid;
      this.assortlistnn();
    },
    mounted(){

    },
    methods: {
      assortlistnn(){
        let url = Api.Apis + '/h5/h5goodtype/findTwoLevelGoodType';
        let data = {'typeid':this.pids};
        this.$http.post(url,data, {emulateJSON: true}
        ).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              this.showContent = datas.typeBeanList;
            } else {
              console.log('失败');
            }
          }
        });
      }
    }
  }
</script>

<style scoped>
.clearfix:after { display: block; clear: both; }
.clearfix { zoom: 1; }
.mui-control-content.mui-active{
  position: static !important;
}
  .assortconterFu{
    padding:0 5% 0 5%;
    background-color:#fff;
  }
  .assortconterFu .dl{
    float: left;
    width:30%;
    margin:0.2rem 1.2%;
  }
  .assortconterFu .dl .dt{width:100%;}
  .assortconterFu .dl .dd{width:100%;text-align: center;font-size: .28rem}
  .assortconterFu .dl .dt img{width:100%;height:100%}
  .dianDi{height:0.9rem}
</style>



