<template>
  <div id="classify">
    <header class="header mui-bar mui-bar-nav" style="display: flex;align-items: center;justify-content: center;background: #fff;box-shadow: none">
        <router-link to="/seek" tag="p" class="seeker"><span class="mui-icon mui-icon-search"></span><p>从这里出发，你会了解更多</p></router-link>
    </header>
    <div class="mui-content classifymain" >
      <!--列表-->
      <div class="assort">
        <div class="assFU">
          <router-link tag="a" :to="'/classify/goods/'+item.typeId" class="mui-active mui-ellipsis" v-for="(item,key) in list"  :key="key">
            {{item.typeName}}
          </router-link>
        </div>
      </div>
      <!--展示-->
      <div class="assortconter mui-slider-group" id="zhanshi">
        <router-view :key="key"></router-view>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  export default{
    data(){
      return {
        msg:'bwb',
        n:0,
        list:[]
      }
    },
    beforeCreate () {
      document.querySelector('body').setAttribute('style', 'background:#fff')
    },
    beforeDestroy () {
      document.querySelector('body').setAttribute('style', '')
    },
    mounted(){
      this.assortlist();
     /* wechatShare({
        title: 'Yuki动漫-分类',
        content: '不一样的二次元空间',
        link: `${window.location.origin}/classify`,
        logo: `${require('../../../static/img/1.jpg')}`,
      });*/
    },
    // 给router-view添加key使其子路由可以刷新
    computed: {
      key() {
        return this.$route.name !== undefined? this.$route.name + +new Date(): this.$route + +new Date()
      }
    },
    methods: {
      assortlist(){
        var url = Api.Apis + '/h5/h5goodtype/findOneLevelGoodType'
        this.$http.post(url).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            this.list = datas.data.typeBeanList;
            console.log(this.list)
          }
        })
      }
    }
  }
</script>

<style scoped>
  .blanks{
    position: fixed;
    width: 100%;
    height: 100%;
    background: #fff;
  }
  .seeker{display: flex;justify-content: center;align-items: center;width:95%;height:74%;background: #e5e5e5;border:1px solid #999;border-radius: 2rem}
  .assort{width:24%;position: fixed;background-color:#ffffff;height:100%}
  .classifymain{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
  }
  .assort .assFU{position: absolute;top:0;left:0;border-right:1px solid #6d6d72;width:100%;padding:0.3rem 0 0.2rem 0;}
  .assort a{margin-top: .6rem;display: block;font-size: .3rem}
  .assortconter{
    position: absolute;
    width:76%;
    right: 0;
  }
  .router-link-active{color: #4595e5}
  .mui-bar .mui-icon {
    font-size: 15px;
    position: relative;
    z-index: 20;
    padding-top: 10px;
    padding-bottom: 10px;
  }
</style>



