<template>
  <div id="apps">
    <topsearch></topsearch>
    <!-- 轮播图部分开始-->
    <div class="banner_lunbo">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="(item,key) in list" :key="key">
          <!-- 跳转至详情页-->
          <!--<router-link :to="{path:'/details',query:{ids:item.targetId}}"><img :src=item.picUrl alt="item.title"/></router-link>-->
          <a><img :src=item.picUrl alt="item.title" @click="goWhere(item.dispatcherType,item.targetId,item.dispatcherUrlApp)"/></a>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!-- 轮播图部分结束-->
    <!--今日限购-->
    <div class="limitoday" v-if="limitodayShow">
      <h2 class="recommend" @click="goFlashSale"> 今日限时购<span class="news_recommend_icon"></span></h2>
      <router-link tag="ul"  :to="{path:'details',query:{ids:goodId}}" class="clearfix limitoday_content">
          <li class="fl">
            <p class="limitTitle">距离结束还有</p>
            <p class="overTime">
              {{lefttimer | countDown}}
            </p>
            <p class="limitmsg">{{limitmsg}}</p>
            <p class="limitPrice">
              <span>￥{{newPrice | substr}}</span><i>￥{{oldPrice | substr}}</i>
            </p>
          </li>
          <li class="fr">
            <span><img :src="goodPicUrl" alt=""/> </span>
          </li>
      </router-link>
    </div>
    <!-- 新品推荐部分开始-->
    <div class="news_recommend">
      <!-- 跳转至新品推荐-->
      <router-link :to="{path:'new_recommend',query:{isNews:'true'}}"><h2 class="recommend">新品推荐 <span class="news_recommend_icon"></span></h2></router-link>

      <div class="imgshare_top" @load="loaded">
       <div id="wrapper" class="clearfix">
           <div id="scroller">
            <ul class="clearfix" id="scroller_main" style="margin-top:.7rem">
              <li class="active lisAll" v-for="(item,indenx,key) in listnews"  :key="key">
                <!-- 跳转至商品详情页-->
                <router-link :to="{path:'/details',query:{ids:item.goodId}}">
                  <img :src="item.goodPicUrl" alt=""/>
                  <span>{{item.goodName}}</span>
                  <p>￥{{item.goodMinPrice | substr}}</p>
                </router-link>
              </li>
            </ul>
          </div>
        </div>
      </div>

    </div>
    <!-- 新品推荐部分j结束-->

    <!--人气推荐部分开始-->
    <div class="people_recommend">
      <router-link :to="{path:'new_recommend',query:{isNews:'false'}}" class="clearfix"><h2 class="recommend">人气推荐 <span class="news_recommend_icon"></span></h2>
      </router-link>
      <div class="people_recommend_main">
      <ul class="mui-table-view mui-table-view-chevron">
        <li class="mui-table-view-cell mui-media" v-for="(item,key) in recommend_list" :key="key">
          <!-- 跳转至商品详情页-->
          <router-link :to="{path:'/details',query:{ids:item.goodId}}" class="mui-navigate-right">
            <img class="mui-media-object mui-pull-left" :src="item.goodPicUrl">
            <div class="mui-media-body">
              {{item.goodName}}
              <p class='mui-ellipsis'>￥{{item.goodMinPrice|substr}}</p>
            </div>
          </router-link>
        </li>
      </ul>
      </div>
    </div>
    <!-- 人气推荐部分结束-->

  <!-- 猜你喜欢部分开始-->
    <guess_likes :guesslist='guess_list'></guess_likes>

  <!-- 猜你喜欢部分结束-->
  </div>
</template>
<script type='es6'>
  import IScroll from '../../assets/scroll/iscroll.js'
  import '../../assets/scroll/iscroll.css'
  import Api from '../../components/commom/API.js'
  import  topsearch from '../commom/top_search.vue'
  import guessLikes from '../commom/guess_you_like.vue'
  import  countDown from '../commom/countDown.vue'
  export default {
    data(){
      return {
        list: [],
        listnews: [],
        recommend_list:[],
        guess_list:[],
        toUrl:'',
        limitmsg:'',
        goodsDetail:[],
        oldPrice:'',
        newPrice:'',
        goodPicUrl:'',
        goodId:'',
        lefttimer:'',
        limitodayShow:false,
        //guesslikes:1
      }
    },
    components: { topsearch,guess_likes:guessLikes,countDown },
    mounted(){
      this.getsbanner();
      this.getlimitGoods();
      this.daojishi();
      this.loaded();
     /* wechatShare({
        title: 'Yuki动漫',
        content: '不一样的二次元空间',
        link: `${window.location.origin}/indenx`,
        logo: `${require('../../../static/img/1.jpg')}`,
      });*/
    },
    methods: {
      //获取页面数据---限时购
      getlimitGoods(){
        var url = Api.Apis + '/h5/timegood/findTimeGoodListByTime';
        this.$http.post(url,{time:'today'},{emulateJSON: true}).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            console.log(datas);
            this.goodsDetail = datas.timeGoodBeanList;
            if(this.goodsDetail){
              this.limitodayShow = true;
              this.goodId = this.goodsDetail[0].goodId;
              this.limitmsg = this.goodsDetail[0].goodName;
              this.newPrice = this.goodsDetail[0].newPrice;
              this.oldPrice = this.goodsDetail[0].oldPrice;
              this.goodPicUrl = this.goodsDetail[0].goodPicUrl;
              this.lefttimer = datas.endTime - datas.thisTime;
            }else{
              console.log('没有今日限时抢购');
            }
          }
        })
      },
      // 倒计时
      daojishi(){
        var TT = setInterval(function () {
          this.lefttimer-=1000;
          if(this.lefttimer<0){
            this.lefttimer=0;
            clearInterval(TT)
          }
        }.bind(this),1000)
      },
      //获取页面数据
      getsbanner(){
        var url = Api.Apis + '/h5/h5index/indexPage';
        this.$http.post(url).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body
            this.list = datas.data.banners
            this.listnews = datas.data.newGoodBeanList;
            this.recommend_list=datas.data.popularGoodBeanList;
            this.guess_list=datas.data.guessGoodList;
            console.log("shs",datas)
          }
        })
      },
      //判断轮播图图片访问位置
      goWhere(state,goodId,imgUrl){
        if(state == 1){
          window.location.href = imgUrl
        }else{
            this.$router.push({path:'/details',query:{ids:goodId}})
        }
      },
      //滚动条部分
      loaded: function () {
        var myScroll = new IScroll('#wrapper', {
          eventPassthrough: true,
          scrollX: true,
          scrollY: false,
          preventDefault: false
        });
      },
      // 进入限时购详情
      goFlashSale(){
        window.localStorage.ti = 'today';
        this.$router.push('/flashSale')
      }
    }
  }
</script>
<style>
  .limitoday .fl p.overTime span{
    font-weight: normal;
    font-style: normal;
    margin-left: 2px;
    margin-right: 2px;
  }
</style>
<style type="text/css" scoped>
  .clearfix:after { content: ""; display: block; clear: both; }
  .clearfix { zoom: 1; }
  img{
    width: 100%;
    height: 100%;
  }
  .fl{
    float: left;
  }
  .fr{
    float: right;
  }
  #apps .people_recommend ul li{
    padding-top: 11px !important;
  }
  .limitoday{
    background-color: #ffffff;
    padding: 0 0.3rem;
    margin-bottom: 0.18rem;
  }
  .limitoday_content{
    padding-top: 0.6rem;
    padding-bottom: 0.4rem;
    text-align: left;
  }
  .limitoday p{
    font-size: 0.3rem;
    color: #000;
  }
  .limitoday .fl{
    width: 3.5rem;
  }
  .limitoday .fl p.overTime{
    height: 0.6rem;
    width: 2.3rem;
    font-size: 0.35rem;
    color: #000;
    font-weight: 900;
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
    border: 1px solid #ccc;
    border-radius: 0.08rem;
    text-align: center;
    line-height:0.6rem;
  }
  .limitoday .fl p.limitmsg{
    margin-top: 0.3rem;
    margin-bottom: 0.3rem;
    height: 1rem;
    line-height: 0.5rem;
    overflow: hidden;
  }
  .limitoday .fl p.limitPrice{
    height: 0.5rem;
  }
  .limitoday .fl p.limitPrice span{
    margin-right: 0.2rem;
    font-size: 0.4rem;
    color: #ff0000;
    line-height: 0.5rem;
  }
  .limitoday .fl p.limitPrice i{
    margin-right: 0.2rem;
    font-size: 0.24rem;
    color: #ccc;
    line-height: 0.5rem;
    font-style: normal !important;
    text-decoration: line-through;
  }
  .limitoday .fr span{
    display: block;
    padding: 0.24rem;
    width: 3.32rem;
    height: 3.32rem;
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 0.08rem;
  }
</style>
<style scoped lang="less">
  #apps {
  /*轮播图部分开始*/
  .banner_lunbo {
    height: 4.2rem;

  a {
    display: block;

  img {
    display: block;
    vertical-align: bottom;
    width: 100%;
    height: 100%;
  }

  }
  }
  /*轮播图部分结束*/

  /*推荐公共标题部分*/
  .recommend {
    margin: 0;
    font-size: 0.32rem;
    border-bottom: 1px solid #E6E6E6;
    line-height: 0.94rem;
    text-align: center;
  }

  .news_recommend_icon {
    position: absolute;
    right: 0;
    margin: 0.25rem;
    display: inline-block;
    width: 0.48rem;
    height: 0.48rem;
    background: url(../../../static/img/home_icon_next_circle@3x.png);
    background-size: cover;
    vertical-align: middle;
  }

/*新品推荐部分开始*/
  .news_recommend {
    background-color: #ffffff;
    padding: 0 0.3rem;
    font-weight: 600;

  a {
    display: block;
  }

  .imgshare_top {
    height: 4.53rem;

  #wrapper {
    height: 100%;
  }

  #scroller {
    width: 28.38rem;
    li {
    line-height: initial;
    margin: 0 5px;
    padding: 0 10px;
    height: 3.28rem;
    width: 2.58rem;

  img {
    display: block;
    width: 2.18rem;
    height: 2.18rem;
  }

  span {
    display: block;
    max-width: 2.6rem;
    height: 0.76rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin: 0 0.16rem;
    font-size: 0.28rem;
    color: #333333;
    line-height: 0.76rem;
    text-align: left;
  }

  p {
    line-height: .25rem;
    font-size: 0.24rem;
    color: red;
    font-weight: 200;
    text-align: left;
  }

  }
  }
  }
  }
  /*人气推荐部分*/
  .people_recommend{
    margin-top: 0.18rem;
    padding: 0 0.3rem;
    background-color: #ffffff;
  .mui-table-view:before{
     height: 0;
  }
  ul{
    li{
    padding-top: 0.45rem;
  a{
    display: block;
  img{
    display: block;
    width: 2.0rem;
    max-width: none;
    height: inherit;
  }
  .mui-media-body{
    color: #000 !important;
    text-overflow: ellipsis;
    text-align: left;
    font-size: 0.3rem;
  p{
    margin-top: 0.7rem;
    color: red;
    font-size: 0.24rem;
  }
  }
  &::after{
     font-size: 0.34rem
   ;
   }

  }
    }
  }
  }
  /*人气推荐部分结束*/
  }
</style>



