<template>
  <div id="details">
    <div class="zhengchang" v-show="lunboShow">
      <div clss="hao"  v-show="!iframeIsShow">
        <!-- 头部返回部分-->
        <top_back></top_back>
        <!-- 轮播图部分开始-->
        <div class="details_banner" @click="lunbShow">
          <mt-swipe :auto="3000">
            <mt-swipe-item v-for="(item,index,key) in list"  :key="key">
              <img class="preview-img" :src="item.src"/>
            </mt-swipe-item>
          </mt-swipe>
          <p class="showCountDown" v-if="timeShow"><span>限时抢购中</span><span>距离结束还剩{{lefttimer | countDown}}</span></p>
        </div>
        <!-- 轮播图部分结束-->
        <!-- 商品信息展示部分开始-->
        <div class="details_main">
          <h2 class="details_main_title">
            <p><i class="licon">￥</i><i class="bicon">{{showsPrice}}</i><span>{{goodTag}}</span></p>
            <p>{{goodName}}</p>
          </h2>
          <div>促销：<span>{{promotion?promotion:"无"}}</span></div>
          <div @click="gobuys">选择款式 <span> > </span></div>
        </div>
        <!-- 商品信息展示部分结束-->
        <!-- 评论组件模块-->
        <div id="comment" v-if="commentIsShow">
          <div class="comment_title">
            <strong>评论：<span>{{allCount}}</span></strong>
            <span @click="goApp">查看全部&nbsp;></span>
          </div>
          <div class="comment_main">
            <h2 class="">
              <strong><img :src="userAvatar" alt=""/></strong>
              <span>{{userName}}</span>
              <span>{{createTime | formatTime('YMDHMS')}}</span>
            </h2>
            <div class="comment_main_texts">
              <p>{{commentText}}</p>
              <ul class="clearfix" v-if="isShow">
                <li><img :src="skuPic1" alt=""/></li>
              </ul>
              <div>
                <p>类别：<span>全新现货</span></p>
              </div>
            </div>
          </div>
        </div>
        <!--上拉加载更多信息开始-->
        <div class="details_more">
          <h4 v-show="!iframeIsShow" @click="jiaMore">点击加载更多</h4>
        </div>
        <!--上拉加载更多信息结束-->
      </div>

      <!-- 点击选择款式开始-->
      <div class="details_gobuys" v-if="gobuysIsShow">
        <div class="details_gobuys_main">
          <div class="details_gobuys_imgs" >
            <img :src="classimgs" alt=""/>
            <div>
              <p>价格：{{prices|substr}}</p>
              <p>{{classifys}}</p>  <!--根据选择改变显示的值-->
            </div>
            <span @click="closegobuys">X</span>
          </div>
          <!-- 类型-->
          <!--一-->
          <div class="details_gobuys_class1" v-for="(items,indenx) in propertyList_0">
            <p>{{items.propertyName}}</p>
            <p ref="gobuy_classify">
              <button class="btnA" v-for="(item,index) in items.valueList" @click.stop="change_classify0(items.propertyid,item.valueid,$event)">{{item.valueName}}</button>
            </p>
          </div>
          <!--二-->
          <div class="details_gobuys_class1" v-for="(items,indenx) in propertyList_1">
            <p>{{items.propertyName}}</p>
            <p>
              <button class="btnB" v-for="(item,index) in items.valueList" @click="change_classify1(items.propertyid,item.valueid,$event)">{{item.valueName}}</button>
            </p>
          </div>
          <!--三-->
          <div class="details_gobuys_class1" v-for="(items,indenx) in propertyList_2">
            <p>{{items.propertyName}}</p>
            <p ref="gobuy_classify">
              <button class="btnC" v-for="(item,index) in items.valueList" @click="change_classify2(items.propertyid,item.valueid,$event)">{{item.valueName}}</button>
            </p>
          </div>
          <!--四-->
          <div class="details_gobuys_class1" v-for="(items,indenx) in propertyList_3">
            <p>{{items.propertyName}}</p>
            <p>
              <button class="btnD" v-for="(item,index) in items.valueList" @click="change_classify3(items.propertyid,item.valueid,$event)">{{item.valueName}}</button>
            </p>
          </div>
          <!--五-->
          <div class="details_gobuys_class1" v-for="(items,indenx) in propertyList_4">
            <p>{{items.propertyName}}</p>
            <p>
              <button class="btnE" v-for="(item,index) in items.valueList" @click="change_classify4(items.propertyid,item.valueid,$event)">{{item.valueName}}</button>
            </p>
          </div>
          <div class="details_gobuys_counts">
            <p>数量</p>
            <button @click="deletes">-</button>
            <span>{{counts}}</span>
            <button @click="adds">+</button>
            <em v-show="stockNum">库存：({{stockNum}})</em>
          </div>
        </div>
      </div>
      <!-- 点击选择款式结束-->
      <!--加载更多信息开始-->
      <div class="buhao" v-show="iframeIsShow" style="position: fixed;height:100%;width: 100%;left:0;top:0;">
        <!-- <div style="position:fixed;top:-1rem;height:100%;width:100%;background: #fff;"></div> -->
        <header class="mui-bar mui-bar-nav" style="position:absolute;top:0;left: 0;background: #fff;box-shadow: none;z-index:99999999;top:0;left: 0;">
          <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="fanHui" style="color:#4595e5"></a>
        </header>
        <div class="wrapper">
          <iframe :src="iframesrc" frameborder="0" scrolling="yes" id="EE"></iframe>
          <div style="width:100%;height:1rem;"></div>
        </div>
      </div>
      <!--加载更多信息结束-->
      <!-- 加入购物袋开始-->
      <div class="details_shopCar" :class="{'shopcar_isShow':details_shopCar}">
        <div><span class="service" @click="goService"></span></div>
        <div><span class="gobuys" @click="goBuy"></span></div>
        <div><span class="gocollect" @click="goColl" :class="{'gocollectBlue':collShow}"></span></div>
        <div @click="promptly_gobuys"><span class="promptly_gobuys">立即购买</span></div>
        <div @click="add_gobuys"><span class="add_gobuys">加入购物车</span></div>
      </div>
    </div>
    <div class="lunboye" v-show="!lunboShow" @click="lunbShowF" style="background: rgba(0,0,0,.8);width:100%;height:100%;position: fixed;top:0;left:0;display: flex;align-items: center;justify-content: center">
      <div class="details_banner">
        <mt-swipe :auto="0" :show-indicators="false">
          <mt-swipe-item v-for="(item,index,key) in list"  :key="key">
            <img class="preview-img" :src="item.src"/>
          </mt-swipe-item>
        </mt-swipe>
      </div>
    </div>

  </div>
</template>
<script type='es6'>
  import Api from '../commom/API.js';
  import top_back from '../commom/top_back.vue';
  import { Toast } from 'mint-ui';
  import {setCookie,getCookie} from '../commom/cookie.js'
  export default {
    data(){
      return {
        ids: this.$route.query.ids,
        timeShow: false,
        userid: '',
        list: [],
        lists: [],
        nowprice: "",
        goodName: "",
        promotion: "",
        goodTag: "",
        userName: "",
        allCount: "",
        createTime: 0,
        commentText: "",
        userAvatar: "",
        skuPic1: "",
        isShow: false,
        iframesrc: '',
        iframeheight: 200,
        commentIsShow: false,
        iframeIsShow: false,
        gobuysIsShow: false,
        details_shopCar: false,
        propertyList_0: [],
        propertyList_1: [],
        propertyList_2: [],
        propertyList_3: [],
        propertyList_4: [],
        propertyList: [],
        classimgs: "",
        classifys: "", //样式列表
        prices: 0,
        counts: 1,
        newsSkuList: [],
        skuidText: "",
        skuidText_01: "",
        skuidText_02: "",
        skuidText_03: "",
        skuidText_04: "",
        skuidText_05: "",
        stockNum: 0,
        skuid: 0,
        addrid: 0,
        xuanze1: false,
        xuanze2: false,
        xuanze3: false,
        xuanze4: false,
        xuanze5: false,
        len: '',
        lunboShow: true,
        collShow: false,
        jiaGe:[],
        maxPrice:'',
        minPrice:'',
        showsPrice:'',
        lefttimer:''
      }
    },
    components: {
      top_back
    },
    mounted(){
      this.getUserID();
      this.findGoodDetail();
    },
    methods: {
      //进入购物车
      goBuy(){
        if (this.userid == '') {
          this.$router.push('/login')
        } else {
          this.$router.push('/shopingCar')
        }
      },
      //切换大图轮播
      lunbShow(){
        this.lunboShow = false
      },
      //切换详情页面
      lunbShowF(){
        this.lunboShow = true
      },
      //加载更多
      jiaMore(){
        this.iframeIsShow = true
      },
      // 返回商品详情页
      fanHui(){
        this.iframeIsShow = false
      },
      // 获取用户ID
      getUserID(){
        this.userid = getCookie('userId');
      },
      //获取页面数据
      findGoodDetail(){
        this.iframesrc = Api.Apis + '/h5/h5good/findGoodDetailH5Page/' + this.ids;
        var that = this;
        var url = Api.Apis + '/h5/h5good/findGoodDetail';
        this.$http.post(url,
          {goodid: this.ids, userid: this.userid, sortText: "asc"},
          {emulateJSON: true}
        ).then(function (data) {
          var datas = data.body.data;
          //获取数据
          if (data.status == 200 && data.statusText == "OK") {
            console.log("详情",datas);
            if(datas.isTimeGood){
              this.timeShow = true;
              this.lefttimer = datas.endTime - datas.thisTime;
              this.daojishi()
            }
            this.nowprice = datas.goodDetailBean.skuList[0].nowPrice;
            this.goodName = datas.goodDetailBean.goodName;
            this.promotion = datas.orderSale;
            this.goodTag = datas.goodDetailBean.goodTag;
            this.classimgs = datas.goodDetailBean.skuList[0].skuPic;
            // 循环找出最高最低价格
            datas.goodDetailBean.skuList.forEach((value, index) => {
                this.jiaGe.push(value.nowPrice)
            });
            this.maxPrice = Math.max.apply(null, this.jiaGe);
            this.minPrice = Math.min.apply(null, this.jiaGe);
            if(this.maxPrice == this.minPrice){
              this.showsPrice = (this.maxPrice/100).toFixed(2)
            }else{
              this.showsPrice = (this.minPrice/100).toFixed(2) + "~" + (this.maxPrice/100).toFixed(2)
            }
            //获取收藏状态
            this.collShow = datas.goodDetailBean.userCollectionState
            //提取第一个类型
            this.propertyList_0.push(datas.goodDetailBean.propertyList[0]);
            //console.log(datas.goodDetailBean.propertyList[1])
            //提取第二个类型做判断
            if (datas.goodDetailBean.propertyList[1] !== undefined) {
              this.propertyList_1.push(datas.goodDetailBean.propertyList[1])
            }
            //提取第三个类型做判断
            if (datas.goodDetailBean.propertyList[2] !== undefined) {
              this.propertyList_2.push(datas.goodDetailBean.propertyList[2])
            }
            //提取第四个类型做判断
            if (datas.goodDetailBean.propertyList[3] !== undefined) {
              this.propertyList_3.push(datas.goodDetailBean.propertyList[3])
            }
            //提取第五个类型做判断
            if (datas.goodDetailBean.propertyList[4] !== undefined) {
              this.propertyList_4.push(datas.goodDetailBean.propertyList[4])
            }
            this.len = datas.goodDetailBean.propertyList.length;
            this.stockNum = datas.goodDetailBean.skuList[0].stockNum;
            this.classifys = datas.goodDetailBean.skuList[0].skuDesc;
            this.prices = datas.goodDetailBean.skuList[0].nowPrice;
            var thats = this;
            //将获取的商品详情重新提取，
            datas.goodDetailBean.skuList.forEach(function (value) {
              thats.newsSkuList[value.skuidText] = value
            });
            //使用vue-preview插件，将数据重新提取赋值
            datas.goodDetailBean.picList.forEach(function (item) {
              var obj = {};
              obj.src = item;
              that.lists.push(obj)
            });
            this.lists.forEach(function (value) {
              var img = document.createElement("img");
              img.src = value.src;
              value.w = img.width;
              value.h = img.height;
            });
            this.list = this.lists;
            //  提取取评论信息；
            if (datas.singleComment) {
              this.userName = datas.singleComment.userName;
              this.createTime = datas.singleComment.createTime;
              this.commentText = datas.singleComment.commentText;
              this.userAvatar = datas.singleComment.userAvatar;
              this.allCount = datas.allcount;
              this.commentIsShow = true
            }
            //获取数据中的页面
            if (datas.goodDetailBean.goodDescUrl) {
              this.iframesrc = datas.goodDetailBean.goodDescUrl
            }
          }
        }, function (error) {
          //alert("数据请求失败")
          Toast({
            message: '数据加载失败......',
            position: 'middle',
            duration: 6000
          });
        })
      },
      //倒计时
      daojishi(){
        var TT = setInterval(function () {
          this.lefttimer-=1000;
          if(this.lefttimer<0){
            this.lefttimer=0;
            clearInterval(TT)
          }
        }.bind(this),1000)
      },
      //类型一事件
      change_classify0(ids1, ids, e){
        this.xuanze1 = true;
        this.skuidText_01 = ids1 + ":" + ids + "/";
        this.skuidText = this.skuidText_01.toString() + (this.skuidText_02.toString() ? this.skuidText_02 : "") + (this.skuidText_03.toString() ? this.skuidText_03 : "") + (this.skuidText_04.toString() ? this.skuidText_04 : "") + (this.skuidText_05.toString() ? this.skuidText_05 : "");
        console.log('一', this.skuidText)
        var btns = document.getElementsByClassName('btnA');
        console.log("btns", btns)
        for (let i = 0; i < btns.length; i++) {
          btns[i].style.borderColor = "#d8d8d8";
          btns[i].style.color = "#9d9d9d";
        }
        e.target.style.borderColor = "#007aff";
        e.target.style.color = "#007aff";
        if (this.newsSkuList.indexOf(this.skuidText) && this.newsSkuList[this.skuidText] !== undefined) {
          console.log(this.skuidText);
          console.log(this.newsSkuList[this.skuidText]);
          this.prices = this.newsSkuList[this.skuidText].nowPrice;
          this.classifys = this.newsSkuList[this.skuidText].skuDesc;
          this.classimgs = this.newsSkuList[this.skuidText].skuPic;
          this.stockNum = this.newsSkuList[this.skuidText].stockNum;
          this.skuid = this.newsSkuList[this.skuidText].skuid
        }
      },
      //类型二事件
      change_classify1(ids1, ids, e){
        this.xuanze2 = true;
        this.skuidText_02 = ids1 + ":" + ids + "/";
        this.skuidText = this.skuidText_01 + this.skuidText_02 + (this.skuidText_03.toString() ? this.skuidText_03 : "") + (this.skuidText_04.toString() ? this.skuidText_04 : "") + (this.skuidText_05.toString() ? this.skuidText_05 : "");
        console.log('二', this.skuidText)
        var btns = document.getElementsByClassName('btnB');
        for (var i = 0; i < btns.length; i++) {
          btns[i].style.borderColor = "#d8d8d8";
          btns[i].style.color = "#9d9d9d";
        }
        e.target.style.borderColor = "#007aff";
        e.target.style.color = "#007aff";
        if (this.newsSkuList.indexOf(0, this.skuidText) && this.newsSkuList[this.skuidText] !== undefined) {
          console.log(this.skuidText);
          console.log(this.newsSkuList[this.skuidText]);
          this.prices = this.newsSkuList[this.skuidText].nowPrice;
          this.classifys = this.newsSkuList[this.skuidText].skuDesc;
          this.classimgs = this.newsSkuList[this.skuidText].skuPic;
          this.stockNum = this.newsSkuList[this.skuidText].stockNum;
          this.skuid = this.newsSkuList[this.skuidText].skuid
        }
      },
      //类型三事件
      change_classify2(ids1, ids, e){
        this.xuanze3 = true;
        this.skuidText_03 = ids1 + ":" + ids + "/";
        this.skuidText = this.skuidText_01 + this.skuidText_02 + this.skuidText_03 + (this.skuidText_04.toString() ? this.skuidText_04 : "") + (this.skuidText_05.toString() ? this.skuidText_05 : "");
        console.log('三', this.skuidText)
        var btns = document.getElementsByClassName('btnC');
        for (var i = 0; i < btns.length; i++) {
          btns[i].style.borderColor = "#d8d8d8";
          btns[i].style.color = "#9d9d9d";
        }
        e.target.style.borderColor = "#007aff";
        e.target.style.color = "#007aff";
        if (this.newsSkuList.indexOf(1, this.skuidText) && this.newsSkuList[this.skuidText] !== undefined) {
          console.log(this.skuidText);
          console.log(this.newsSkuList[this.skuidText]);
          this.prices = this.newsSkuList[this.skuidText].nowPrice;
          this.classifys = this.newsSkuList[this.skuidText].skuDesc;
          this.classimgs = this.newsSkuList[this.skuidText].skuPic;
          this.stockNum = this.newsSkuList[this.skuidText].stockNum;
          this.skuid = this.newsSkuList[this.skuidText].skuid
        }
      },
      //类型四事件
      change_classify3(ids1, ids, e){
        this.xuanze4 = true;
        this.skuidText_04 = ids1 + ":" + ids + "/";
        this.skuidText = this.skuidText_01 + this.skuidText_02 + this.skuidText_03 + this.skuidText_04 + (this.skuidText_05.toString() ? this.skuidText_05 : "");
        console.log('四', this.skuidText)
        var btns = document.getElementsByClassName('btnD');
        for (var i = 0; i < btns.length; i++) {
          btns[i].style.borderColor = "#d8d8d8";
          btns[i].style.color = "#9d9d9d";
        }
        e.target.style.borderColor = "#007aff";
        e.target.style.color = "#007aff";
        if (this.newsSkuList.indexOf(2, this.skuidText) && this.newsSkuList[this.skuidText] !== undefined) {
          console.log(this.skuidText);
          console.log(this.newsSkuList[this.skuidText]);
          this.prices = this.newsSkuList[this.skuidText].nowPrice;
          this.classifys = this.newsSkuList[this.skuidText].skuDesc;
          this.classimgs = this.newsSkuList[this.skuidText].skuPic;
          this.stockNum = this.newsSkuList[this.skuidText].stockNum;
          this.skuid = this.newsSkuList[this.skuidText].skuid
        }
      },
      //类型五事件
      change_classify4(ids1, ids, e){
        this.xuanze5 = true;
        this.skuidText_05 = ids1 + ":" + ids + "/";
        this.skuidText = this.skuidText_01 + this.skuidText_02 + this.skuidText_03 + this.skuidText_04 + this.skuidText_05;
        console.log('五', this.skuidText)
        var btns = document.getElementsByClassName('btnE');
        for (var i = 0; i < btns.length; i++) {
          btns[i].style.borderColor = "#d8d8d8";
          btns[i].style.color = "#9d9d9d";
        }
        e.target.style.borderColor = "#007aff";
        e.target.style.color = "#007aff";
        if (this.newsSkuList.indexOf(3, this.skuidText) && this.newsSkuList[this.skuidText] !== undefined) {
          console.log(this.skuidText);
          console.log(this.newsSkuList[this.skuidText]);
          this.prices = this.newsSkuList[this.skuidText].nowPrice;
          this.classifys = this.newsSkuList[this.skuidText].skuDesc;
          this.classimgs = this.newsSkuList[this.skuidText].skuPic;
          this.stockNum = this.newsSkuList[this.skuidText].stockNum;
          this.skuid = this.newsSkuList[this.skuidText].skuid
        }
      },
      //加
      adds(){
        this.counts++;
        //大于改商品库存是提醒并等于最大库存数
        if (this.counts > this.stockNum) {
          this.counts = this.stockNum == null ? this.stockNum = 0 : this.stockNum;
          Toast({
            message: '亲，库存不够了哦^_^',
            position: 'middle',
            duration: 3000
          });
        }

      },
      //减
      deletes(){
        this.counts--;
        if (this.counts <= 0) {
          this.counts = 0
        }
      },
      //加入购物车
      add_gobuys(){
        //判断是否已登录
        if (this.userid == '') {
          this.$router.push('/login')
          return;
        }
        // 样式是否已选
        if (this.len == '5') {
          if (this.xuanze1 && this.xuanze2 && this.xuanze3 && this.xuanze4 && this.xuanze5) {
            // 数量是否为零
            if (this.counts == '0') {
              this.gobuys();
              Toast({message: '请选择数量', position: 'bottom'});
              return;
            } else if (this.counts > this.stockNum) {
              Toast({
                message: '亲，库存不够了了哦^_^',
                position: 'middle',
                duration: 3000
              });
              return;
            } else {
              //满足条件执行
              //提交购物车提前判断下当前传入的参数是否有效
              if (this.counts && this.skuid) {
                var url = Api.Apis + "/h5/h5goodcart/addUserGoodCart",
                  files = {skuid: this.skuid, count: this.counts, userid: this.userid}
                //files={skuid:this.skuid,count:this.counts}
                this.$http.post(url, files, {emulateJSON: true}).then(function (data) {
                  //提交购物车成功
                  if (data.body.state === "success") {
                    //提示信息
                    Toast({
                      message: '已添加至购物车',
                      iconClass: 'icon icon-success'
                    });
                  }
                })
              }
            }
          } else {
            this.gobuys();
            Toast({message: '请选择款式或颜色', position: 'bottom'});
            return;
          }
        } else if (this.len == '4') {
          if (this.xuanze1 && this.xuanze2 && this.xuanze3 && this.xuanze4) {
            // 数量是否为零
            if (this.counts == '0') {
              this.gobuys();
              Toast({message: '请选择数量', position: 'bottom'});
              return;
            } else if (this.counts > this.stockNum) {
              Toast({
                message: '亲，库存不够了了哦^_^',
                position: 'middle',
                duration: 3000
              });
              return;
            } else {
