      //添加收藏
      goColl(){
        if (this.userid == '') {
          this.$router.push('/login')
          return;
        }
        this.collShow = !this.collShow;
        if (this.collShow) {
          let data = {'userId': this.userid, 'goodId': this.ids};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5collection/addCollection';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log('成功', datas)
                Toast({message: "收藏成功", position: 'bottom'});
              }
            }
          })
        } else {
          let data = {'userId': this.userid, 'goodId': this.ids};
