<template>
  <div id="classify">
    <header class="header mui-bar mui-bar-nav" style="display: flex;align-items: center;justify-content: center;background: #fff;box-shadow: none">
        <router-link to="/seek" tag="p" class="seeker"><span class="mui-icon mui-icon-search"></span><p>从这里出发，你会了解更多</p></router-link>
    </header>
    <div class="mui-content classifymain" >
      <!--列表-->
      <div class="assort">
        <a class="mui-active mui-ellipsis assortAA" v-for="(item,key) in list"  :key="key" @click="assortcontain(item.typeId)">
          {{item.typeName}}
        </a>
      </div>
      <!--展示-->
      <div class="zhanshi">
        <div class="assortconterFu clearfix">
          <router-link tag="div" :to="{path:'/classify_d',query:{yid:pid,eid:item.typeId,name:item.typeName}}" class="dl" v-for="(item,index,key) in showContent" :key="key">
            <div class="dt"><img :src="item.typePicUrl"/></div>
            <div class="dd">{{item.typeName}}</div>
          </router-link>
        </div>
        <div class="dianDi"></div>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../../components/commom/API.js'
  export default{
    data(){
      return {
        msg:'bwb',
        n:0,
        list:[],
        showContent:[],
        pid:'1',
      }
    },
    beforeCreate () {
      document.querySelector('body').setAttribute('style', 'background:#fff')
    },
    beforeDestroy () {
      document.querySelector('body').setAttribute('style', '')
    },
    mounted(){
      this.assortlist();
    },
    methods: {
      // 导航栏数据
      assortlist(){
        var url = Api.Apis + '/h5/h5goodtype/findOneLevelGoodType';
        this.$http.post(url).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            this.list = datas.data.typeBeanList;
            this.assortcontain(1);
            console.log(this.list)
          }
        })
      },
      // 展示区域数据
      assortcontain(pids){
        this.pid = pids;
        let url = Api.Apis + '/h5/h5goodtype/findTwoLevelGoodType';
        let data = {'typeid':pids};
        this.$http.post(url,data, {emulateJSON: true}
        ).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              this.showContent = datas.typeBeanList;
            } else {
              console.log('失败');
            }
          }
        });
      },
    }
  }
</script>

<style scoped>
  .seeker{display: flex;justify-content: center;align-items: center;width:95%;height:74%;background: #e5e5e5;border:1px solid #999;border-radius: 2rem}
  .classifymain{overflow: hidden;background: #fff}
  .assort{width:1.6rem;text-align: center;border-right: 1px solid #8f8f94;padding:.4rem 0 .4rem;float: left}
  .assort a{margin-top: .6rem;display: block;font-size: .3rem}
  .assort a:hover{color:#007aff}
  .zhanshi{width:5.5rem;float: right}
  .assortconterFu{padding:0 5% 0 5%;}
  .assortconterFu .dl{float: left;width:30%;margin:0.2rem 1.2%;}
  .assortconterFu .dl .dt{width:100%;}
  .assortconterFu .dl .dd{width:100%;text-align: center;}
  .assortconterFu .dl .dt img{width:100%;height:100%}
  .dianDi{height:0.9rem}
</style>



