<template>
  <div id="app">
    <loading v-if="getLoading"></loading>
    <router-view></router-view>
    <tabbar v-if="getFoot"></tabbar>
  </div>
</template>

<script type="es6">
  import tabbar from './components/commom/tabbar.vue'
  //状态管理
  import {mapActions,mapGetters} from 'vuex';

  export default {
  name: 'app',
  data(){
  return {
//    isShow:true
    }
  },
    watch:{
      $route(to){
          //console.log('to' + to.path)
        this.changeRoute(to.path)
      }
    },
    mounted(){
     // console.log(this.$route.path)
      this.changeRoute(this.$route.path)
    },
    components: {
        tabbar
    },
    computed:mapGetters(
      ['getFoot','getLoading']
    ),
  //添加监听路由路径、
  /*watch:{
      $route(){
        if(this.$route.path==="/home" || this.$route.path==="/classify/" || this.$route.path==="/My" || this.$route.path==="/userLogin"
         || this.$route.path==="details/"){
          this.isShow=true
        }else{
          this.isShow=false
        }
      }
  },*/
    methods:{
        //根据路由地址来动态显隐底部导航
      changeRoute(path){
        path=path.substring(1);
        if(path=='home'||path.indexOf('classify/goods')!=-1||path=='My'||path=='userLogin'){
          this.$store.dispatch('showFoot')
        }else{
          this.$store.dispatch('hideFoot')
        }
      }
    }
}
</script>
<style scoped lang="less">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



