<template>
  <div class="enter">
    <header class="mui-bar mui-bar-nav" style="background: #fff">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">填写订单</h1>
    </header>
    <nav class="mui-bar mui-bar-tab">
      <div class="suBmit" @click="subMit">提交订单</div>
      <div class="heji">合计: <span style="color:red">￥ {{summation | substr}}</span></div>
    </nav>
    <div class="mui-content">
      <div class="addMain1"></div>
      <ul class="mui-table-view" v-show="addressLen!=0">
        <li class="mui-table-view-cell" style="text-align: left" @click="toAddress">
          <div>
            <p style="margin-bottom: .2rem"><span class="mui-icon mui-icon-person" style="font-size: .4rem"></span>{{userName}}   {{phone}}    <span class="moren" v-show="isMoren">默认</span></p>
            <p><span class="mui-icon mui-icon-location" style="font-size: .4rem"></span>{{address}}</p>
          </div>
          <span class="mui-icon mui-icon-arrowright"></span>
        </li>
      </ul>
      <ul class="mui-table-view" v-show="addressLen==0">
        <li class="mui-table-view-cell" style="text-align: left" @click="toAddress">
          <div>
            <p><span class="mui-icon mui-icon-location" style="font-size: .4rem"></span>请添加收货地址</p>
          </div>
          <span class="mui-icon mui-icon-arrowright"></span>
        </li>
      </ul>

      <ul class="mui-table-view" style="margin: .3rem 0">
        <li class="mui-table-view-cell" v-for="item in goodsList">
          <div class="goodsImg">
            <img :src="item.skuPic" />
          </div>
          <div class="goodsInfo">
            <p class='mui-ellipsis'>{{item.name}}</p>
            <p class='mui-ellipsis'>{{item.skuText.join(' 、')}}</p>
            <p><span>价格：￥ {{item.price | substr}}</span><span>X {{item.count}}</span></p>
          </div>
        </li>
      </ul>

      <ul class="mui-table-view">
        <li class="mui-table-view-cell">
          <span>商品总价</span>
          <span>￥ {{totalPrice | substr}}</span>
        </li>
      </ul>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell">
          <span>运费</span>
          <span>￥ {{goodsFreight}}</span>
        </li>
      </ul>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell" @click="changeYuhui">
          <span>优惠<span class="btnYouhui" v-if="youhuiName!='kong'">{{youhuiName}}</span></span>
          <span class='mui-ellipsis'>{{youhuiInfo}}</span>
        </li>
      </ul>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell">
          <span>买家留言</span>
          <!--<span style="color:#8f8f94">选填：对本次交易的说明</span>-->
          <span>
            <input type="text" placeholder="选填：对本次交易的说明" v-model="leaveMessage" style="font-size: .2rem;margin: 0;padding:0;height:100%;border: none;text-align: center"/>
          </span>
        </li>
      </ul>
    </div>

    <mt-popup v-model="popupVisible" closeOnClickModal="false" style="width:100%;height:7rem" position="bottom">
      <div style="padding:8px 6px;font-size: .35rem;border-bottom: 1px solid #8f8f94;text-align: left">选择优惠</div>
      <div>
        <div class="mui-input-row mui-radio mui-left" style="border-bottom: 1px solid #f7f7f7" v-if="manJianName!=null">
          <label style="font-size: .3rem;text-align: left">{{manJianName}}</label>
          <input name="palyA" type="radio" style="width:100%" @click="manjianYou">
        </div>
        <div class="mui-input-row mui-radio mui-left" style="border-bottom: 1px solid #f7f7f7"  v-for="(item,index) in couponsList">
          <label style="font-size: .3rem;text-align: left">{{item.couponName}}</label>
          <input name="palyA" type="radio" style="width:100%" @click="youhuiquanYu(item.id)">
        </div>
      </div>
      <div class="yuhuiQueding" @click="yuhuiQueding">确定</div>
    </mt-popup>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  export default{
    data(){
      return {
        userName:'',
        phone:'',
        address:'',
        isMoren:'',
        goodsList:[],
        goodsFreight:'',
        coupon:'',
        discount:'',
        leaveMessage:'',
        summation:'',
        totalPrice:'',
        orderStateID:'', // 满减ID
        couponID:'', // 优惠券ID
        userID:window.localStorage.userID,
        skuId:window.localStorage.skuID,
        addID:window.localStorage.addrId,
        nuM:window.localStorage.num,
        laiZi:window.localStorage.for,
        addressIDS:'',
        addressLen:'',
        manJianName:'',
        manJianT:'',
        manJianB:'',
        couponName:'',
        couponT:'',
        couponB:'',
        popupVisible:false,
        youhuiName:'',
        youhuiInfo:'',
        couponsList:[],
        order:'',
        receiveName:'',
        receivePhone:'',
        receiveAddress:''
      }
    },
    mounted(){
      this.orderINfo()
    },
    methods: {
      // 获取页面详情
      orderINfo(){
        // 判断页面是从哪个页面过来的
        if(this.laiZi=='car'){
          // 页面来自购物车
          var data = {'userId': this.userID,'skuid':window.localStorage.skuID,'addrId':this.addID,'couponId':'-2'};
          var url = Api.Apis + '/h5/h5order/toCheck';
          this.$http.post(url,data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                  console.log('来自购物车数据',datas)
                //买家信息
                this.addressLen=datas.addresses.length;
                if(this.addressLen!=0) {
                  this.userName = datas.orderBean.address.name;
                  this.phone = datas.orderBean.address.phone;
                  this.address = datas.orderBean.address.address;
                  this.addressIDS = datas.orderBean.address.addrid;
                  this.isMoren = datas.orderBean.address.isAvalible;
                }
                //商品信息
                this.goodsList = datas.orderBean.orderItem;
                // 商品优惠券信息
                this.couponsList = datas.coupons;
                //商品优惠信息
                console.log('商品优惠信息',datas.saleBean);
                if(datas.saleBean){
                  this.manJianName = datas.saleBean.salesName; // 满减优惠名
                  this.manJianB = datas.saleBean.saleReduce; //减多少
                }
                this.couponName = datas.orderBean.sales.couponsName; //优惠券名
                this.couponT = datas.orderBean.sales.couponsMeet;
                this.couponB = datas.orderBean.sales.couponsReduce;

                if(this.couponName != null || datas.saleBean) {
                  if (this.manJianB > this.couponB) {
                    this.youhuiName = this.manJianName;
                    this.youhuiInfo = ' 减 ' + this.manJianB
                  } else {
                    this.youhuiName = this.couponName;
                    this.youhuiInfo = '满 ' + this.couponT + ' 减 ' + this.couponB
                  }
                }else{
                  this.youhuiName = 'kong';
                  this.youhuiInfo = '无商品折扣'
                }
                //运费  合计
                console.log(datas.orderBean)
                this.goodsFreight = datas.orderBean.order.freight;// 运费
                this.summation = datas.orderBean.order.needPay; // 合计
                this.totalPrice = datas.orderBean.order.totalPriceNow; // 当前价格
                this.orderStateID = datas.orderBean.sales.ordersaleid; // 优惠满减ID
                this.couponID = datas.orderBean.sales.couponId // 优惠券ID
              }else{
                console.log(datas.errMsg)
              }
            }
          })
        }else if(this.laiZi=='qing'){
            // 页面来自商品详情
          var data = {'userId': this.userID,'skuid':this.skuId,'addrId':this.addID,'num':this.nuM,'couponId':'-2'};
          console.log('传入的数据',data)
          var url = Api.Apis + '/h5/h5order/toCheckOrderNew';
          this.$http.post(url,data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                  console.log('来自商品详情数据',datas);
                //买家信息
                this.addressLen=datas.addresses.length;
                if(this.addressLen!=0){
                  this.userName = datas.orderBean.address.name;
                  this.phone = datas.orderBean.address.phone;
                  this.address = datas.orderBean.address.address;
                  this.addressIDS = datas.orderBean.address.addrid;
                  this.isMoren = datas.orderBean.address.isAvalible;
                }

                this.order = datas.orderBean.order;
                console.log("order",this.order)
                this.receiveAddress = this.order.receiveAddress;
                this.receivePhone = this.order.receivePhone;
                this.receiveName = this.order.receiveName;

                // 商品优惠券信息
                this.couponsList = datas.coupons;
                //商品信息
                this.goodsList = datas.orderBean.orderItem;
                console.log('goodList',this.goodsList)
                //商品优惠信息
                console.log('商品优惠信息',datas.saleBean);
                if(datas.saleBean){
                  this.manJianName = datas.saleBean.salesName; // 满减优惠名
                  this.manJianB = datas.saleBean.saleReduce; //减多少
                }
                this.couponName = datas.orderBean.sales.couponsName; //优惠券名
                this.couponT = datas.orderBean.sales.couponsMeet;
                this.couponB = datas.orderBean.sales.couponsReduce;

                if(this.couponName != null || datas.saleBean) {
                  if (this.manJianB > this.couponB) {
                    this.youhuiName = this.manJianName;
                    this.youhuiInfo = ' 减 ' + this.manJianB
                  } else {
                    this.youhuiName = this.couponName;
                    this.youhuiInfo = '满 ' + this.couponT + ' 减 ' + this.couponB
                  }
                }else{
                  this.youhuiName = 'kong';
                  this.youhuiInfo = '无商品折扣'
                }
                //运费  合计
                console.log(datas.orderBean);
                this.goodsFreight = datas.orderBean.order.freight;// 运费
                this.summation = datas.orderBean.order.needPay; // 合计
                this.totalPrice = datas.orderBean.order.totalPriceNow; // 当前价格
                this.orderStateID = datas.orderBean.sales.ordersaleid; // 优惠满减ID
                this.couponID = datas.orderBean.sales.couponId // 优惠券ID
              }else{
                console.log('失败')
              }
            }
          })
        }
      },
      //返回上一级
      goBack: function () {
        if(this.laiZi=='car') {
          this.$router.push('/shopingCar')
        }else if(this.laiZi=='qing'){
          this.$router.push('/home')
        }else if(this.laiZi=='xq'){
          this.$router.push('/myorder_xq')
        }
      },
      //跳转到地址编辑页
      toAddress(){
        window.localStorage.woLai = "0";
        this.$router.push('/address')
      },
      //提交订单
      subMit(){
        if(this.laiZi=='car') {
          console.log("来自购物车");
          if(this.orderStateID==null){
            this.orderStateID=0;
          }
          if(this.couponID==null){
            this.couponID=0;
          }
          if(this.addressIDS == ''){
            alert("请填写收货信息");
            return;
          }
          var data = {
            'userId': this.userID,
            'addrId':this.addressIDS,
            'totalPriceNow':this.totalPrice, // 现在的总价
            'freight':this.goodsFreight,
            'needPay': this.summation,  // 应付金额
            'skuid':0, // 类型id
            'num':0,  // 购买数量
            'cartid':window.localStorage.cartids,
            'saleorderid':this.orderStateID,// 满减id
            'remarkText':this.leaveMessage, // 买家留言
            'receiveName':this.userName,
            'receivePhone':this.phone,
            'receiveAddress':this.address,
            'couponId':this.couponID, // 优惠券ID
          };
          console.log("111",data)
          var url = Api.Apis + '/h5/h5order/subOrderNew';
          this.$http.post(url,data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log("提交成功", data);
                window.localStorage.orderCode = datas.orderBean.order.ordercode;
                this.$router.push('/play')
              } else {
                console.log("提交失败")
              }
            }
          })
        }else{
          console.log("来自商品详情");
          if(this.orderStateID==null){
            this.orderStateID=0;
          }
          if(this.couponID==null){
            this.couponID=0;
          }
          if(this.addressIDS == ''){
            alert("请填写收货信息");
            return;
          }
          var data = {
            'userId': this.userID,
            'addrId':this.addressIDS,
            'totalPriceNow':this.totalPrice, // 现在的总价
            'freight':this.goodsFreight,
            'needPay': this.summation,  // 应付金额
            'skuid':window.localStorage.skuID,
            'num':window.localStorage.num,
            'cartid':0,
            'saleorderid':0,// 满减id
            'couponId':this.couponID, // 优惠券ID
            'remarkText':this.leaveMessage,
            'receiveName':this.userName,
            'receivePhone':this.phone,
            'receiveAddress':this.address,

          };
          console.log("来自商品详情",data);
          var url = Api.Apis + '/h5/h5order/subOrderNew';
          this.$http.post(url,data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log("提交成功", datas.orderBean.order.ordercode);
                window.localStorage.orderCode = datas.orderBean.order.ordercode;
                this.$router.push('/play')
              } else {
                console.log("提交失败")
              }
            }
          })
        }
      },
      //选择优惠
      changeYuhui(){
        if(this.couponsList.length!=0){
          this.popupVisible = true
        }
      },
      //选择优惠确定
      yuhuiQueding(){
        this.popupVisible = false
      },
      //满减优惠
      manjianYou(){
        //来自购物车
        if (this.laiZi == 'car') {
          var data = {'userId': this.userID, 'skuid': window.localStorage.skuID, 'addrId': this.addID, 'couponId': 0};
          var url = Api.Apis + '/h5/h5order/toCheck';
          this.$http.post(url, data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log('来自购物详情数据', datas)
                //商品优惠信息
                console.log(datas.orderBean.sales);
                this.manJianName = datas.orderBean.sales.salesName; //满减名
                this.manJianT = datas.orderBean.sales.salesMeet;
                this.manJianB = datas.orderBean.sales.salesReduce;
                this.youhuiName = this.manJianName;
                this.youhuiInfo = '满 ' + this.manJianT + ' 减 ' + this.manJianB;
                //运费  合计
                this.summation = datas.orderBean.order.needPay; // 合计
                this.orderStateID = datas.orderBean.sales.ordersaleid; // 优惠满减ID
                this.couponID = datas.orderBean.sales.couponId // 优惠券ID
              } else {
                console.log(datas.errMsg)
              }
            }
          })
        } else if (this.laiZi == 'qing') {
          // 页面来自商品详情
          var data = {
            'userId': this.userID,
            'skuid': this.skuId,
            'addrId': this.addID,
            'num': this.nuM,
            'couponId': 0
          };
          console.log('来自商品详情满减数据', data)
          var url = Api.Apis + '/h5/h5order/toCheckOrderNew';
          this.$http.post(url, data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log('来自购物详情数据', datas)
                //商品优惠信息
                console.log(datas.orderBean.sales);
                this.manJianName = datas.orderBean.sales.salesName; //满减名
                this.manJianT = datas.orderBean.sales.salesMeet;
                this.manJianB = datas.orderBean.sales.salesReduce;
                this.youhuiName = this.manJianName;
                this.youhuiInfo = '满 ' + this.manJianT + ' 减 ' + this.manJianB;
                //运费  合计
                this.summation = datas.orderBean.order.needPay; // 合计
                this.orderStateID = datas.orderBean.sales.ordersaleid; // 优惠满减ID
                this.couponID = datas.orderBean.sales.couponId // 优惠券ID
              } else {
                console.log(datas.errMsg)
              }
            }
          })
        }
      },
      //优惠券优惠
      youhuiquanYu(id){
        this.orderStateID = id;
        //来自购物车
        if (this.laiZi == 'car') {
          var data = {'userId': this.userID, 'skuid': window.localStorage.skuID, 'addrId': this.addID, 'couponId': id};
          var url = Api.Apis + '/h5/h5order/toCheck';
          this.$http.post(url, data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log('来自ididiid数据', datas)
                //商品优惠信息
                console.log(datas.orderBean.sales);
                this.couponName = datas.orderBean.sales.couponsName; //优惠券名
                this.couponT = datas.orderBean.sales.couponsMeet;
                this.couponB = datas.orderBean.sales.couponsReduce;
                this.youhuiName = this.couponName;
                this.youhuiInfo = '满 ' + this.couponT + ' 减 ' + this.couponB;
                //运费  合计
                console.log(datas.orderBean)
                this.summation = datas.orderBean.order.needPay; // 合计
                this.orderStateID = datas.orderBean.sales.ordersaleid; // 优惠满减ID
                this.couponID = datas.orderBean.sales.couponId // 优惠券ID
              } else {
                console.log(datas.errMsg)
              }
            }
          })
        } else if (this.laiZi == 'qing') {
          // 页面来自商品详情
          var data = {
            'userId': this.userID,
            'skuid': this.skuId,
            'addrId': this.addID,
            'num': this.nuM,
            'couponId': id
          };
          console.log('传入的数据', data)
          var url = Api.Apis + '/h5/h5order/toCheckOrderNew';
          this.$http.post(url, data, {emulateJSON: true}).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log('来自购物详情数据', datas)
                //商品优惠信息
                console.log(datas.orderBean.sales);
                this.couponName = datas.orderBean.sales.couponsName; //优惠券名
                this.couponT = datas.orderBean.sales.couponsMeet;
                this.couponB = datas.orderBean.sales.couponsReduce;
                this.youhuiName = this.couponName;
                this.youhuiInfo = '满 ' + this.couponT + ' 减 ' + this.couponB;
                //运费  合计
                console.log(datas.orderBean);
                this.summation = datas.orderBean.order.needPay; // 合计
                this.orderStateID = datas.orderBean.sales.ordersaleid; // 优惠满减ID
                this.couponID = datas.orderBean.sales.couponId // 优惠券ID
              } else {
                console.log(datas.errMsg)
              }
            }
          })
        }
      }
    }
  }
</script>

<style type="text/css">
  .heji{
    float:right;
    height:100%;
    display:flex;
    align-items: center;
    justify-content: center;
  }
  .suBmit{
    float: right;
    margin-left: 5% ;
    height:100%;
    width:35%;
    font-size: .2rem;
    background: #4595e5;
    color:#fff;
    display:flex;
    align-items: center;
    justify-content: center;
  }
  .moren{
    border:1px solid #4595e5;
    border-radius: 5px;
    padding:0 1px;
    color:#4595e5;
    margin-left: 5px;
  }
  .addMain1{
    margin:0;
    height:.1rem;
    background: url(../../../static/img/warehouse/color-line.png) repeat-x top center;
  }
  .btnYouhui{border:1px solid #4595e5;padding:2px 5px;margin-left: 10px;border-radius: 4px;color: #4595e5}
  .enter .mui-content ul li{display: flex;justify-content: space-between;align-items: center}
  .goodsImg{width:30%;height:2.2rem;overflow: hidden}
  .goodsImg img{width:100%;height: 100%}
  .goodsInfo{width:65%;display: flex;flex-direction: column;justify-content: center}
  .goodsInfo p{display:flex;justify-content: space-between;width: 100%}
  .mui-radio input[type='radio']:checked:before {content: '\e442';}
  .mui-radio input[type='radio']:before {font-size: 20px !important;}
  .mui-radio input[type='radio'], .mui-checkbox input[type='checkbox'] {
    position: absolute;
    top: 8px;
    right: 20px;
    display: inline-block;
    width: 28px;
    height: 26px;
    border: 0;
    outline: 0 !important;
    background-color: transparent;
    -webkit-appearance: none;
  }
  .yuhuiQueding{width:100%;line-height: .8rem;height:.8rem;background: #007aff;color:#fff;font-size: .3rem;text-align: center;position: fixed;bottom: 0}
</style>



