<template>
  <div class="mycollect">
    <header class="mui-bar mui-bar-nav">
      <a class="mui-icon mui-icon-back mui-pull-left" @click="goBack" style="color:#4595E5"></a>
      <h1 class="mui-title">我的收藏</h1>
      <a class="mui-btn mui-btn-blue mui-btn-link mui-pull-right"  @click="bianji">{{msg}}</a>
    </header>
    <nav class="mui-bar mui-bar-tab collFooter" v-if="navShow">
      <div style="display: flex;">
        <span class="xuanza" @click="allChecked" :class="{'xuanzeBlue':!flage}"></span>
        <p style="font-size: .3rem;" v-if="allShow">全选</p>
        <p style="font-size: .3rem;" v-if="!allShow">已选({{checkCount}})</p>
      </div>
      <a class="delall" @click="delAll">删除所选</a>
    </nav>
    <div class="mui-content collMain">
      <div class="collectShop" v-for="(item,index) in myCollect">
        <!--<span class="xuanze"></span>-->
        <span class="xuanze" @click="isGobuys(item,$event,item.goodId)" :class="{'xuanzeBlue':item.checked}" v-if="navShow"></span>
        <div @click="goXQ(item.goodId,item.sale,item.stockNum)" class="neirong">
          <img :src="item.goodPicUrl">
          <p class="biaoti">{{item.goodName}}</p>
          <p class="goodsPrice">￥ {{item.nowPrice |substr}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  import { Toast } from 'mint-ui';
  export default{
    data(){
      return {
        isShow: false,
        userID:'',
        allShow: true,
        checkCount:0,
        flage:true,
        msg: '编辑',
        myCollect: [],
        navShow:false,
        collLenth:'',
        goodList:[],
        chk:0
      }
    },
    mounted(){
      this.getMyCollect()
    },
    methods: {
      //编辑样式
      bianji: function () {
        this.isShow = !this.isShow;
        if (!this.isShow) {
          this.msg = '编辑';
          this.navShow=false
        } else {
          this.msg = '取消';
          this.navShow=true
        }
      },
      //点击进入商品详情
      goXQ(id,xiajia,kucun){
        if(!this.navShow){
            console.log("在编辑")
            if(xiajia){
                console.log("未下架")
                if(kucun!=0){
                  console.log("有库存")
                  this.$router.push({path:'/details',query:{ids:id}})
                }else{
                  Toast({
                    message: '亲，库存不够了哦^_^',
                    position: 'middle',
                    duration: 3000
                  });
                }
            }else{
              Toast({
                message: '亲，这个商品已经下架了哦^_^',
                position: 'middle',
                duration: 3000
              });
            }
        }
      },
      //返回上一级
      goBack: function () {
        this.$router.go(-1)
      },
      //渲染数据
      getMyCollect(){
        /*页面挂载获取保存的cookie值，渲染到页面上*/
        this.userID = getCookie('userId');
        /*如果cookie不存在，则跳转到登录页*/
        if (this.userID == "") {
          this.$router.push('/login')
        } else {
          let data = {'userId': this.userID};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5collection/myCollection';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                this.myCollect = datas.goodBeans;
                this.collLenth = datas.goodBeans.length;
                console.log(datas)
              }
            }
          })
        }
      },
      //点击选择
      isGobuys(i,e,goodID){
        //事先注册checked
        if (typeof i.checked === 'undefined') {
          //注册checked属性
          this.$set(i, "checked", true)
        } else {
          i.checked = !i.checked
        }
        if(i.checked){
          //选中
          this.allShow = false
          this.chk = this.checkCount
          this.chk += 1
          this.goodList.push(goodID)
          console.log('单点选中',this.goodList)
          this.goodList = [...new Set(this.goodList)]
        }else{
          //取消选中
          this.chk = this.checkCount
          this.chk -= 1
          //删除数组中指定的元素
          Array.prototype.indexOf = function(val) {
            for (var i = 0; i < this.length; i++) {
              if (this[i] == val) return i;
            }
            return -1;
          };
          Array.prototype.remove = function(val) {
            var index = this.indexOf(val);
            if (index > -1) {
              this.splice(index, 1);
            }
          };
          this.goodList.remove(goodID);
          console.log('单点取消',this.goodList)
        }
        this.checkCount = this.chk
        if (this.chk == this.collLenth) {
            this.flage = !this.flage;
          } else {
            this.flage = true;
          }
      },
      //全选与取消
      allChecked(){
        this.flage = !this.flage;
        if(this.flage){
          //全选 未选=》取消
          console.log("全选")
          this.allShow = true;
          this.goodList = []
          this.checkCount = 0
          this.myCollect.forEach((value, index) => {
            value.checked = false
          })
        }else{
          //已选 =》全选
          console.log("取消")
          this.checkCount = this.collLenth
          this.allShow = false
          this.myCollect.forEach((value, index) => {
              this.goodList.push(value.goodId)
              this.goodList = [...new Set(this.goodList)]
              value.checked = true
          })
        }
        console.log("取消",this.goodList)
      },
      //删除所选
      delAll(){
        if(this.goodList.length == 0 ){
          Toast({message: '请选择要删除的商品',position: 'bottom'});
        }else{
          let data = {'userId': this.userID,'goodId': this.goodList.join(',')};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5collection/deleteCollection';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                Toast({message: '删除成功',position: 'bottom'});
                this.checkCount = 0;
                this.getMyCollect()
              }
            }
          })
        }
      }
    }
  }
</script>

<style type="text/css" scoped>
  .collMain{width:100%;margin-top: 0.2rem;overflow: hidden;padding-left: .22rem}
  .collectShop{margin:0 .2rem .2rem 0   ;float:left;width:47%;position: relative;padding: 0.1rem 0.2rem 0.1rem .2rem;background: #fff;}
  .xuanze{position: absolute;display: block;top:.1rem;right:.1rem;background: url(../../../static/img/shopping-bag/common_icon_empty@2x.png);background-size: 100% 100%;width:.4rem;height:.4rem}
  .xuanza{width:.4rem;height:.4rem;display:block;margin:0 .3rem 0 .2rem;background: url(../../../static/img/shopping-bag/common_icon_empty@2x.png);background-size: 100% 100%;}
  .xuanzeBlue{background-image: url(../../../static/img/shopping-bag/common_icon_chose@3x.png);background-size: 100% 100%;border: none;}
  .neirong, .neirong img{width:100%}
  .neirong img{height:3rem}
  .collFooter{display: flex;justify-content: space-between;align-items: center;box-shadow: none}
  .delall{background: #4595e5;height:100%;width:30%;color:#fff;font-size: .35rem;display: flex;align-items: center;justify-content: center}
  .biaoti{font-size: .3rem;text-align: left;width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}
  .collMain{width:100%;margin-top: 0.2rem;overflow: hidden;padding-left: .22rem}
  .goodsPrice{color:red;text-align: left;font-size: .3rem;margin-top: .1rem}
</style>



