<template>
  <div id="seeek">
      <header class="header clearfix" ref="header">
        <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595E5"></a>
          <div class="search">
            <span class="mui-icon mui-icon-search"></span>
            <input type="text" class="to_search" v-model="ipt" >
          </div>
          <i @click="seek">搜索</i>
      </header>
    <div class="mui-content">
      <!--历史搜索-->
          <div class="mui-card-content-inner" style="padding:0.15rem;width:100%" v-show="liShi.length!=0">
            <p class="liShi clearfix"><span style="float: left;">历史搜索</span><span class=" recoveryicon" style="float: right;" @click="delAll"></span></p>
            <div class="seekShop">
              <router-link tag="a" :to="{path:'seek_d',query:{gN:item}}"  class="seekShopName" v-for="(item,key) in liShi" :key="key">{{item}}</router-link>
            </div>
          </div>

      <!--热门搜索-->
          <div class="mui-card-content-inner" style="padding:0.15rem;width:100%">
            <p class="liShi hotlish"><span>热门搜索</span></p>
            <div class="seekShop seekShophot">
              <router-link tag="a" :to="{path:'seek_d',query:{gN:item.keyWord}}" class="seekShopName" v-for="(item,key) in reMen" :key="key">{{item.keyWord}}</router-link>
            </div>
          </div>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../../components/commom/API.js'
  import { Toast } from 'mint-ui';
  export default{
    data(){
      return {
        ipt:'日本进口面膜',
        liShi:[],
        reMen:[]
      }
    },
    mounted(){
        this.hotSeek();
        this.height()
        this.hData()
    },
    methods:{
        // 读取写入localStorage的数据
      hData(){
        this.liShi = [...new Set(JSON.parse(localStorage.getItem('hN')))];
        if(this.liShi==null){
          this.liShi=[];
        }
        console.log(this.liShi)
      },
      height:function(){
        var gaoDu = document.querySelector("#seeek");
        gaoDu.style.backgroundColor = '#fff';
        gaoDu.style.height =  window.screen.availHeight + 'px';
      },
      delAll:function () {
        window.localStorage.removeItem('hN');
        this.liShi=[]
      },
      seek:function () {
          if(this.ipt==''){
            Toast({message: '搜索条件不能为空',position: 'bottom'});
          }else{
            this.$router.push({path:'/seek_d',query:{gN:this.ipt}})
          }
      },
      goBack:function () {
        this.$router.go(-1)
      },
      hotSeek(){
        var url = Api.Apis + '/h5/h5search/findHotSearchLogList'
        this.$http.post(url).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            this.reMen = datas.data.searchLogList;
            this.ipt = this.reMen[0].keyWord
          }
        })
      }
    },
  }
</script>
<style scoped>
  .clearfix:after { content: ""; display: block; clear: both; }
  .clearfix { zoom: 1; }
  .liShi{
    text-align: left;
  }
  .liShi span{
    font-size: 0.28rem;
    line-height: 0.32rem;
    margin-bottom: 0.2rem;
  }
  .header{
    position: relative;
    height: 0.90rem;
    padding: 0.11rem 0.2rem;
  }
  .header a{
    position: absolute;
    left: 0.2rem;
    top: 50%;
    width: 0.48rem;
    height: 0.48rem;
    line-height: 0.48rem;
    margin-top: -0.24rem;
    margin-left: 0;
    color: #4595e5;
  }
  .mui-icon.mui-icon-search{
    position: absolute;
    width: 0.36rem;
    height: 0.36rem;
    top: 50%;
    left: 0.3rem;
    margin: 0;
    margin-top: -0.18rem;
    line-height: 0.36rem;
  }
 .recoveryicon{
    width: 0.48rem;
    height: 0.48rem;
    background: url(data:image/png;base64,) no-repeat center center;
    background-size: 100% 100%;
 }
  .seekShop{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    width:100%
  }
  .seekShop a{
    display: block;
    font-size: 0.26rem;
    line-height: 0.48rem;
    color: #666;
    text-align: left;
    padding: 0 0.16rem;
    border: 1px solid #808080;
    border-radius: 3px;
    margin:0 0.3rem 0.3rem 0;
  }
  .search{
    position: absolute;
    top: 50%;
    left: 50%;
    padding-left: 0.3rem;
    margin-left: -30%;
    margin-top: -0.32rem;
    overflow: hidden;
    width: 60%;
    height: 0.64rem;
    border: 0.01rem solid #f0f0f0;
    border-radius: 0.32rem;
    background: #EBEBEB;
    }
    .search .mui-icon,.search input{
      background: #EBEBEB;
    }
  .mui-icon-search{
      position: absolute;
      font-size: 0.3rem;
      left: 0;
      top: -10%;
      display: inline-flex;
      margin-left: 0.16rem;
      width: 0.32rem;
      height: 0.32rem;
      background-size: 100%;

  }
 .search input{
    padding: 0;
    padding-left: 0.4rem;
    font-size: 0.28rem;
    outline: none;
    border: none;
    width: 100%;
    height: 100%;
    margin: 0;
  }
  .mui-content{
    background: #fff;
    height:100%
  }
  .header i{
    float: right;
    width: 10%;
    padding: 0;
    height: 0.68rem;
    font-size: 0.32rem;
    line-height: 0.68rem;
    text-align: center;
    font-style: normal;
    color: #007aff;
  }

  .hotlish{
    margin-bottom: 0.3rem;
  }
</style>





