<template>
  <div v-if="!chainurl">
      <header class="mui-bar mui-bar-nav" style="background: #fff;">
        <router-link to="/play" class="mui-icon mui-icon-left-nav mui-pull-left" style="color:#4595e5"></router-link>
        <h1 class="mui-title">支付失败</h1>
      </header>
      <div class="mui-content">
        <div class="playeorrbg">
          <div class="playeorrImg"></div>
          <h1 class="eorrTip">啊哦～订单支付失败了</h1>
          <mt-button type="primary" class="eorrBtn" @click="goOrder">查看订单</mt-button>
        </div>
      </div>
    </div>
</template>
<script>
  export default {
    data(){
      return {
      }
    },
    mounted(){
    },
    methods:{
      goOrder(){
          this.$router.push('/myorder')
      }
    }
  }
</script>
<style type="text/css" scoped>
  .playeorrbg{
    width:90%;height:8rem;
    padding-top: 1rem;
    background: url("../../../static/img/warehouse/Bg@3x.png") no-repeat;
    background-size:100% 100%;
    margin:1.5rem auto
  }
  .playeorrImg{
    width:55%;height:3.5rem;
    background: url("../../../static/img/default-page/shibai@3x.png") no-repeat;
    background-size:100% 100%;
    margin: 0 auto;
  }
  .eorrTip{
    color:#4595e5;
    font-size: .35rem;
    text-align: center;
    width:100%;
    margin: .3rem 0 .6rem 0;
  }
  .mint-button {
    margin: 0 auto;
    color: inherit;
    display: block;
    font-size: 16px;
    height: .75rem;
    outline: 0;
    width: 80%;
  }
  .mint-button--primary{
    color:#fff
  }
</style>


