<template>
  <div class="pZiliao">
    <header class="mui-bar mui-bar-nav">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">设置</h1>
    </header>
    <div class="mui-content">
      <ul class="mui-table-view">
        <router-link to="pZiliao" tag="li" class="mui-table-view-cell">
          <a class="mui-navigate-right">
            <p class="ziliaogeshi"><span>个人资料</span></p>
          </a>
        </router-link>
      </ul>
      <div style="display: flex;justify-content: center;margin-top:.3rem">
        <button type="button" class="mui-btn mui-btn-danger" style="width:95%" @click="quit">
          退出登录
        </button>
      </div>
    </div>
  </div>
</template>

<script type="es6" scoped>
  import { setCookie,getCookie,delCookie} from '../commom/cookie.js'
  export default{
    data(){
      return {
      }
    },
    methods: {
      quit(){
        /*删除cookie*/
        delCookie('userId');
        this.$router.push('/home')
      },
      goBack:function () {
        this.$router.push('/userLogin')
      }
    }
  }
</script>

<style type="text/css">
  .ziliaogeshi{width:94%;display: flex;justify-content: space-between;}
</style>



