export default {
  getFoot(state){
    return state.blFoot;
  },
  getLoading(state){
    return state.blLoading;
  },
  allcount(state){
  	return state.allcount
  },
  goodcount(state){
  	return state.goodcount
  },
  midcount(state){
  	return state.midcount
  },
  badcount(state){
  	return state.badcount
  },
  hascount(state){
  	return state.hascount
  }
}



