<template>
  <div id="appraise">
      <header class="mui-bar mui-bar-nav" style="box-shadow: none;">
        <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goback"></a>
        <h1 class="mui-title">评价</h1>
      </header>
      <div class="appraise_header">
        <ul class="clearfix" ref="appraisepart">
          <li>
            <a href="javascript:;" @click.stop="loadPageListall">全部 ({{allcount}})</a>
          </li>
          <li >
            <a href="javascript:;" @click.stop="loadPageListgood">好评 ({{goodcount}})</a>
          </li>
          <li >
            <a href="javascript:;" @click.stop="loadPageListmiddle">中评 ({{midcount}})</a>
          </li>
          <li>
            <a href="javascript:;" @click.stop="loadPageListbad">差评 ({{badcount}})</a>
          </li>
          <li >
            <a href="javascript:;" @click.stop="loadPageListpic">有图 ({{hascount}})</a>
          </li>
        </ul>
      </div>
      <div class="appraiseNew">最新评论</div>
      <div class="main-body" :style="{'-webkit-overflow-scrolling': scrollMode}">
        <v-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
        <div class="pretermission" style="font-size: 0.4rem;color:#000;line-height:2rem;" v-if="!blank">亲，沙发还没有被占领哦！</div>
        <div id="appraise_deatil">
          <ul  v-if="blank" class="list">
            <li class="comment_main" v-for="(item,index) in proCopyright"  :key="index">
              <h2 >
                <strong><img :src="item.userAvatar" alt=""/></strong>
                <span class="comment_main_name">{{item.userName}}</span>
                <span class="comment_main_time">{{item.createTime | formatTime('YMDHMS')}}</span>
              </h2>
              <div class="comment_main_texts">
                <p>{{item.commentText}}</p>
                <ul class="clearfix" v-if="item.haspic==1?false:true">
                  <li  v-for="(val,index) in item.picNum" :key="index" ><img :src='item[`skuPic${val}`]' alt=""/></li>
                </ul>
                <div>
                  <p>{{item.skuText}}</p>
                </div>
              </div>
            </li>
          </ul>
        </div>
        </v-loadmore>
      </div>
  </div>
</template>
<script >
  import Api from '../commom/API.js';
  import {Loadmore} from 'mint-ui';
  export default {
    data(){
      return {
        goodid:this.$route.query.selected,
        allcount: 0,
        badcount: 0,
        goodcount: 0,
        midcount: 0,
        badcount: 0,
        hascount: 0,
        blank:false,
        pageNo:0,
        pageSize:10,//一页多少条
        proCopyright:[],
        allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
        scrollMode:"auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
        totalpage:1,//总共多少页
        loading:false,
        bottomText: '',
        level: 0,
        haspic:0
      }
    },
    mounted(){
      this.loadPageListall()
    },
    watch:{
      $route(to){
        var path=to.path.substring(1);
        if(path.indexOf(this.$route.params.id)!=-1){
          this.loadPageList()
        }
      }
    },
    methods: {
      addactive(){
        console.log(this.$refs.appraisepart.children)
        for(var i = 0;i<this.$refs.appraisepart.children.length;i++){
          this.$refs.appraisepart.children[i].className= ''
        }
        // this.$refs.appraisepart.children.forEach(function(item,index){
        //   item.className = ''
        // })
      },
      loadPageListall(){
        this.level = 0;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[0].className="active";
        this.loadPageList()
      },
      loadPageListgood(){
        this.level = 3;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[1].className="active";
        this.loadPageList()
      },
      loadPageListmiddle(){
        this.level = 2;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[2].className="active";
        this.loadPageList()
      },
      loadPageListbad(){
        this.level = 1;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[3].className="active";
        this.loadPageList()
      },
      loadPageListpic(){
        this.level = 0;
        this.haspic = 2;
        this.addactive();
        this.$refs.appraisepart.children[4].className="active";
        this.loadPageList()
      },
      loadBottom:function() {
        // 上拉加载
        this.more();// 上拉触发的分页查询
        this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位
      },
      loadPageList:function (){
      // 查询数据
        var _this = this;
        var url =  Api.Apis + '/h5/h5goodcomment/getComment';
        this.$http.post(url,{'haspic':this.haspic,'index':this.pageNo,'goodid':this.$route.query.selected,'level':this.level},{emulateJSON: true}).then(function(res){
          var bigData = res.data;
           _this.allcount=bigData.allcount;
          _this.goodcount=bigData.goodcount;
          _this.midcount=bigData.midcount;
          _this.badcount=bigData.badcount;
          _this.hascount=bigData.hascount;
          if(bigData.comments.length ==0){
            _this.blank = false;
            return false
          }else{
            _this.blank = true;
            _this.proCopyright = bigData.comments;
            _this.totalpage = bigData.maxIndex;
            if(this.totalpage == 1){
              this.allLoaded = true;
            }
            this.$nextTick(function () {
            // 是否还有下一页，加个方法判断，没有下一页要禁止上拉
              this.scrollMode = "touch";
              this.isHaveMore();
            });
          }
        },function(err){
          console.log("error:"+err)
        })
      },
      more:function (){
        // 分页查询
        if(this.totalpage == 1){
          this.pageNo = 0;
          this.allLoaded = true;
        }else{
          this.pageNo = parseInt(this.pageNo) + 1;
          this.allLoaded = false;
        }
        console.log(this.pageNo);
        this.$http.post(url,{'haspic':this.haspic,'index':this.pageNo,'goodid':this.$route.query.selected,'level':this.level},{emulateJSON: true}).then(function(res){
            this.proCopyright = this.proCopyright.concat(res.data.comments);
            console.log(this.proCopyright);
            this.isHaveMore();
        },function(err){
            console.log("error:"+err)
          })
      },
      isHaveMore:function(){
        // 是否还有下一页，如果没有就禁止上拉刷新
        //this.allLoaded = false; //true是禁止上拉加载
        if(this.pageNo == (this.totalpage-1)){
          this.allLoaded = true;
        }
      },
     goback(){
      this.$router.go(-1);
     }
    },
    components: {
      'v-loadmore':Loadmore
    }
  }

</script>
<style  scoped>
  .clearfix:after { content: ""; display: block; clear: both; }
  .clearfix { zoom: 1; }
  #appraise{
    background: #fff !important;
  }
  .mui-bar.mui-bar-nav,.appraiseNew,.appraise_header ul li{
    background-color: #e5e5e5;
  }
  .mui-bar .mui-title{
    font-size: 0.44rem;
  }
  a.mui-icon{
    color: #007aff;
  }
  .appraiseNew{
    height: 1.2rem;
    padding-top: 0.6rem;
    padding-left: 0.3rem;
    color: #999;
    font-size: 0.35rem;
    text-align: left;
  }
  .appraise_header{
    width: 100%;
    margin-top: 1rem;
    padding-left: 0.2rem;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
    height: 2.1rem;
  }
  .appraise_header ul li{
    float: left;
    width: 26%;
    height: 0.7rem;
    margin-right: 0.36rem;
    margin-bottom: 0.36rem;
    font-size: 0.34rem;
    text-align: center;
    line-height: 0.7rem;
    color: #000;
  }
  .appraise_header ul li a{
    width: 100%;
    display: inline-block;
  }
  .appraise_header ul li.active a{
    color: #fff;
    background: #4595e5;
  }
  #appraise_deatil{
    padding: 0 0.32rem;
    background: #fff;
  }
  .comment_main h2{
    height: 1.1rem;
    line-height: 1.11rem;
    font-weight: normal;
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
  }
  .comment_main h2 strong{
    display: inline-block;
    float: left;
    width: 0.7rem;
    height: 0.7rem;
  }
  .comment_main h2 strong img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    vertical-align: middle;
    border: 0;
  }
  .comment_main h2 span{
    float: left;
    font-size: 0.4rem;
    color: #999999;
    padding-left: 0.2rem;
    font-size: 0.26rem;
    line-height: 1.11rem;
  }
  .comment_main h2 span.comment_main_name{
    width: 1.1rem;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }
  .comment_main h2 span.comment_main_time{
    float: right;
  }
  .comment_main .comment_main_texts p{
    text-align: left;
    line-height: 0.42rem;
    font-size: 0.27rem;
  }
  .comment_main .comment_main_texts div p{
    color: #999;
    text-align: left;
    line-height: 0.42rem;
    font-size: 0.27rem;
  }
  .comment_main_texts li{
    float: left;
    width: 1.5rem;
    height: 1.5rem;
    margin-right: 0.1rem;
  }
  .comment_main_texts li img{
    display: block;
    width: 100%;
    height: 100%;
  }
</style>





