<template>
  <div id="news_recommend">

    <header class="mui-bar mui-bar-nav">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goback" style="color:#4595e5;"></a>
      <h1 class="mui-title">个人头像选择</h1>
    </header>
    <div class="img-list" ref="img-list">
      <img :src="userImg" />
    </div>
    <a href="javascript:;" id="upload" @click="handlerUploadUserImg"></a>
    <button @click="uploadToServer"  type="button" id="lastUp">上传</button>
    <input ref="upload-input" class="upload-btn" @change="upload($event)" type="file" accept="image/*" id="choose" >
  </div>
</template>

<script>
  import { setCookie, getCookie } from "../commom/cookie.js";
  import Api from "../commom/API.js";
  import { Toast } from 'mint-ui';
  export default {
    data () {
      return {
        userID:'',
        fileObj: null,
        userImg: '',
        process: 0
      }
    },
    mounted () {
      this.getInformation()
    },
    methods: {
      goback(){
         this.$router.go(-1)
      },
      getInformation(){
        this.userID = getCookie("userId");
      },
      handlerUploadUserImg() {
        this.uploadUserImg(this.$refs["upload-input"]);
      },
    //上传头像
      uploadUserImg(that) {
        var ipt = that;
        ipt.click();
      },
    //上传
      upload($event) {
        var _this = this;
        // _this.fileObj = $event.target.files[0];
        var file = $event.target.files[0];
        var reader = new FileReader();
        var url = reader.readAsDataURL(file);
        reader.onload = function(e) {
           var canvas=document.createElement("canvas");  
           var ctx=canvas.getContext("2d"); 
           var result = this.result;
           var image = new Image();
                image.src = result;
              image.onload=function(){ 
                var cw=image.width;  
                var ch=image.height;  
                var w=image.width;  
                var h=image.height;  
                canvas.width=w;  
                canvas.height=h;  
                if(cw>400&&cw>ch){  
                    w=400;  
                    h=(400*ch)/cw;  
                    canvas.width=w;  
                    canvas.height=h;  
                }  
                if(ch>400&&ch>cw){  
                    h=400;  
                    w=(400*cw)/ch;  
                    canvas.width=w;  
                    canvas.height=h;  
                }  
                ctx.drawImage(image,0,0,w,h);  
                var cropStr = canvas.toDataURL("image/jpeg",0.7);
                _this.userImg = cropStr;
                _this.fileObj = _this.dataURItoBlob(cropStr);
                _this.$refs["img-list"].style.display="block"; 
                _this.process = 0;   
            }  
          };
        },
        dataURItoBlob(dataUrl){
          var byteString = atob(dataUrl.split(',')[1]);
          var ab = new ArrayBuffer(byteString.length);
          var ia = new Uint8Array(ab);
          for (var i = 0; i < byteString.length; i++) {
              ia[i] = byteString.charCodeAt(i);
          }
          return new Blob([ab], { type: 'image/jpeg' });
        },
    //点击上传
      uploadToServer() {
        var _this = this
        var formData = new FormData();
        if (!this.fileObj) {
          return
        }else{
          formData.append("img", _this.fileObj);
          formData.append("userId", this.userID);
          this.$http
            .post(Api.Apis + "/h5/h5MyInfo/updateUserAvatar", formData,{
              emulateJSON: true
            })
            .then(data => {
              _this.$router.push({path:'/pZiliao'})
          });
        }
      },
    }
  }
</script>

<style scoped>
  *{
    margin: 0;
    padding: 0;
  }
  .upload-btn {
    opacity: 0;
  }
  li{
    list-style-type: none;
  }
  a,input{
    outline: none;
    -webkit-tap-highlight-color:rgba(0,0,0,0);
  }
  #choose{
    display: none;
  }
  canvas{
    width: 100%;
    border: 1px solid #000000;
  }
  #upload{
    display: block;
    margin: 0.1rem;
    height: 0.6rem;
    text-align: center;
    line-height: 0.6rem;
    border-radius: 0.05rem;
  }
  .touch{
    background-color: #ddd;
  }
  .img-list{
    position: relative;
    left: 50%;
    margin-left: -2rem;
    margin-top: 2rem;
    width: 4rem;
    height: 4rem;
    display: none;
  }
  .img-list img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  .size{
    position: absolute;
    width: 100%;
    height: 0.15rem;
    line-height: 0.15rem;
    bottom: -0.18rem;
    text-align: center;
    font-size: 0.13rem;
    color: #666;}
  .pic-list{
    margin: 0.1rem;
    line-height: 0.18rem;
    font-size: 0.13rem;
  }
  .pic-list a{
    display: block;
    margin: 0.1rem 0;
  }
  .pic-list a img{
    vertical-align: middle;
    max-width: 0.3rem;
    max-height: 0.3rem;
    margin: -0.04rem 0 0 0.1rem;
  }
  #lastUp{
    width: 90%;
    background: #009bdb;
    color: #fff; 
    height:0.8rem;
    line-height: 0.8rem;
    border-radius: 0.4rem; 
    font-size:0.32rem; 
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    text-align: center;
  }
  #upload{
    position: relative;
    position: relative;
    left: 50%;
    margin-top: 1rem;
    margin-left: -1rem;
    width: 2rem;
    height: 2rem;
    display:block;
    background: url(../../../static/img/add.png) no-repeat; 
    background-size: 100% 100%;
  }
  #progressNumber{
    height:0.5rem;
    background:#e74c3c;
    width:0;
    border-radius:0.2rem;
    background-image:linear-gradient(-45deg,rgba(255,255,255,.2) 25%,transparent 25%,transparent 50%,rgba(255,255,255,.2) 50%,rgba(255,255,255,.2) 75%,transparent 75%,transparent);
    -webkit-transition:width .8s ease; -moz-transition:width .8s ease; 
    transition:width .8s ease;
  }
</style>



