<template>
  <div class="mycoupon">
    <header class="mui-bar mui-bar-nav" style="box-shadow: none">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack"  style="color:#4595e5"></a>
      <h1 class="mui-title">修改昵称</h1>
      <a class="mui-btn mui-btn-blue mui-btn-link mui-pull-right" @click="saveNick">保存</a>
    </header>
    <div class="mui-content">
      <ul class="mui-table-view" style="padding: 0">
        <input type="text" placeholder="请输入新昵称" class="mui-input-clear" maxlength="10" v-model="niCheng" style="width:100%;border:none;margin-bottom: 0"/>
      </ul>
      <p style="text-align: left;margin-left: 4%;font-size: .3rem;">昵称长度不能超过10个字哦</p>
    </div>
  </div>
</template>

<script>
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        niCheng:'',
        userID:''
      }
    },
    mounted(){
      this.amendNick()
    },
    methods: {
      goBack:function () {
        this.$router.go(-1)
      },
      amendNick(){
        /*页面挂载获取保存的cookie值，渲染到页面上*/
        this.userID = getCookie('userId');
        /*如果cookie不存在，则跳转到登录页*/
        if (this.userID == "") {
          this.$router.push('/login')
        }else{
          let data = {'userId': this.userID};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5MyInfo/getUserInfo';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                this.userPhone = datas.user.userPhone.substr(0, 3) + '****' + datas.user.userPhone.substr(7);
                this.niCheng = datas.user.userNickName;
                if(datas.user.userPhone === datas.user.userNickName){
                  this.niCheng = this.userPhone
                }
              }
            }
          })
        }
      },
      saveNick(){
        if (this.niCheng == "") {
          Toast({message: '昵称不能为空',position: 'bottom'});
        }else{
          let data = {'userId': this.userID,'nickName': this.niCheng};
          console.log(data)
          /*接口请求*/
          var url = Api.Apis + '/h5/h5MyInfo/updateNcikName';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log("修改正常")
                this.$router.push('/pZiliao')
              }
            }
          })
        }
      }
    }
  }
</script>


<style type="text/css" scoped>

</style>



