<template>
  <div class="page-loadmore">
    <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
      <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">
        <!--没有订单时显示页面-->
        <div class="orderList-null" v-show="tipsShow">
          <div class="orderList-null-icon"></div>
          <div class="orderList-null-title">没有任何订单哦</div>
        </div>
        <ul class="mui-table-view" style="margin-bottom:.34rem" v-for="(item,index) in list">
          <li class="">
            <div class="dingdan"><span style="float: left;">订单编号:{{item.order.ordercode}}</span><span style="color:#4595e5;float: right;">{{item.order.state}}</span></div>
            <router-link tag="a" :to="{path:'/myorder_xq',query:{orids:item.order.orderid}}" class="orderCon">
             <div v-for="item in item.orderItem">
               <img class="orderCon_image" :src="item.skuPic">
               <div class="mui-media-body">
                 <p class='mui-ellipsis'>{{item.name}}</p>
                 <p class="design">{{item.skuText.join(' 、')}}</p>
                 <p class="sdingdan dingdan clearfix"><span style="float:left;">￥ {{item.unitPrice|substr}}</span><span style="float:right;">X {{item.count}}</span></p>
               </div>
             </div>

            </router-link>
            <p style="text-align: right;height: 0.71rem;padding-left:0.32rem;padding-right: 0.32rem;line-height: 0.71rem;">共{{item.totalNumber}}件 应付金额：<b style="color:red">￥ {{item.order.needPay|substr}}</b>(含运费￥ {{item.order.freight}})</p>

            <!--根据交易状态显示操作动作-->
            <p style="text-align: right;padding:0 .3rem .15rem 0" v-show="item.order.orderStateId=='1'">
              <router-link tag="button" to="/downApp" type="button" class="mui-btn">取消订单</router-link>
              <button type="button" class="mui-btn mui-btn-primary" @click="goPlay(item.order.ordercode,item.order.leftTime)">支付 <counter :endtime=item.order.leftTime></counter></button>
            </p>
            <p style="text-align: right;padding:0 .3rem .15rem 0" v-show="item.order.orderStateId=='2'">
              <router-link tag="button" to="/downApp" type="button" class="mui-btn">取消订单</router-link>
              <button type="button" class="mui-btn mui-btn-primary" @click="tipSendGoods">提醒发货</button>
            </p>
            <p style="text-align: right;padding:0 .3rem .15rem 0" v-show="item.order.orderStateId=='3'">
              <button type="button" class="mui-btn mui-btn-primary" @click="payment">确认收货</button>
            </p>
            <p style="text-align: right;padding:0 .3rem .15rem 0" v-show="item.order.orderStateId=='4'">
              <button type="button" class="mui-btn mui-btn-primary mui-btn-outlined" style="width:1.2rem" @click="payment">评价</button>
            </p>
          </li>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>

<style>
  .mint-loadmore-top span {
    -webkit-transition: .2s linear;
    transition: .2s linear
  }
  .mint-loadmore-top span {
    display: inline-block;
    vertical-align: middle
  }
  .page-loadmore-wrapper {
    overflow: scroll
  }
  .mint-loadmore-bottom span {
    display: inline-block;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    vertical-align: middle
  }
</style>
<style type="text/css" scoped>
  .clearfix:after { content: ""; display: block; clear: both; }
  .clearfix { zoom: 1; }
  p{text-align: left;font-size: .25rem}
  .mui-media img{margin-top:.2rem}
  .orderList-null {
    width: 3rem;
    margin: 3rem auto 0 auto;
  }

  .orderList-null .orderList-null-icon {
    width: 3rem;
    height: 3rem;
    background: url("http://yukicomic-pic.oss-cn-hangzhou.aliyuncs.com/yukicomicNew/m.yuki/icon_order@3x.png") no-repeat;
    background-size: 100%;
  }

  .orderList-null .orderList-null-title {
    width: 100%;
    margin-top: 0.2rem;
    font-size: 0.28rem;
    line-height: 0.28rem;
    color: #666;
    text-align: center;
  }
  .orderCon{
    display: block;
    width: 100%;
    background: #f0f0f0;
    padding: 0.24rem 0.32rem;
    background: #fafafa;;
  }
  .dingdan{height:.8rem;padding: 0 0.32rem;}
  .dingdan span{
    font-size: 0.26rem;
    line-height: 0.8rem;
  }
  .design{font-size: .26rem;color:#999;}
  .mui-table-view .mui-media{
    padding-top: 0;
  }
  a.orderCon{
    margin: 0;
  }
  .orderCon_image{
    float: left;
    width: 1.6rem;
    height: 1.6rem;
    margin-right: 0.2rem;
  }
  .sdingdan.dingdan{
    padding:0;
  }

  input[type='submit'], input[type='reset'], button, .mui-btn {
    font-size: 14px;
    line-height: 1.42;
    margin-bottom: 0;
    padding: 3px 6px;
    cursor: pointer;
  }

</style>
<script type="text/babel">
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js';
  import counter from '../commom/countDown.vue';
  export default {
    data() {
      return {
        list: [],
        allLoaded: false,
        bottomStatus: '',
        wrapperHeight: 0,
        //wrapperHeight: 0,
        translate: 0,
        moveTranslate: 0,
        listA:[],
        ides:'',
        tipsShow:false,
        maxIndex:'',
        lastValue:1
      };
    },
    components: {counter},
    mounted() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
      this.order()
    },
    created(){
      this.ides = this.$route.params.aid;
    },
    methods: {
      //获取页面数据
      order(){
        /*页面挂载获取保存的cookie值*/
        var thats = this;
        let uname = getCookie('userId');
        let data = {'index': 0,'size':10,'state':this.ides,'userid':uname};
        /*接口请求*/
        var url = Api.Apis + '/h5/h5order/myOrder';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              if(datas.ordersBean==0){
                this.tipsShow = true;
                this.allLoaded = true;
              }else{
                this.maxIndex = datas.maxIndex;
                this.list = datas.ordersBean;
                console.log('数据',datas)
                if(this.maxIndex == 1){
                  this.allLoaded = true;
                }
              }
            }else{
              console.log('失败')
            }
          }
        })
      },
      //取消订单
      cancel:function () {
        this.$router.push('/downApp')
      },
      //确认收货、评价
      payment:function () {
        this.$router.push('/downApp')
      },
      //返回上一级
      goBack:function () {
        this.$router.go(-1)
      },
      //支付
      goPlay(orderCode,leftTime){
          if(leftTime>0){
            window.localStorage.orderCode = orderCode;
            this.$router.push('/play')
          }else{
            alert("订单已失效");
            this.$router.go(0)
          }
      },
      //提醒发货
      tipSendGoods(){
        alert("已提醒卖家发货")
      },
      //下拉加载
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      loadBottom() {
        setTimeout(() => {
          if (this.lastValue < this.maxIndex) {
            var thats = this;
            let uname = getCookie('userId');
            let data = {'index': this.lastValue,'size':10,'state':this.ides,'userid':uname};
            /*接口请求*/
            var url = Api.Apis + '/h5/h5order/myOrder';
            this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
            ).then((data) => {
              if (data.status == 200 && data.ok == true) {
                var datas = data.body.data;
                if (datas.state == "success") {
                  datas.ordersBean.forEach((value)=>{thats.list.push(value)});
                  thats.lastValue ++ ;
                }
              }
            });
          } else {
            this.allLoaded = true;
          }
          this.$refs.loadmore.onBottomLoaded();
        }, 1500);
      }
    }

  };
</script>



