<template>
  <div id="loadingwrapper">
    <div class="loading-mask"></div>
    <div class="loading-box-s">
      <div class="loader-cicle"></div>
      <p class="loading-text">数据加载中</p>
    </div>
  </div>
</template>

<script type="es6">
  export default{
    data(){
      return {
        msg: '加载中...'
      }
    }
  }
</script>

<style type="text/css" scoped>
  #loadingwrapper .loading-mask {
    background: rgba(0,0,0,0.2);
    position: fixed;
    z-index: 1000;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
  }

  #loadingwrapper .loading-box-s {
    position: fixed;
    top: 40%;
    left: 50%;
    z-index: 5000;
    width: 7.6em;
    min-height: 7.6em;
    margin-left: -3.8em;
    text-align: center;
    color: #000;
    background: rgba(0,0,0,0);
    border-radius: 5px;
  }

  #loadingwrapper .loader-cicle {
    position: relative;
    width: 50px;
    height: 50px;
    margin-top: 15px;
    margin-bottom: 15px;
    border-radius: 50%;
    display: inline-block;
    vertical-align: middle;
    border: 7px double #0062cc;
    -webkit-animation: ball-turn 1s linear infinite;
    animation: ball-turn 1s linear infinite;
  }

  #loadingwrapper .loading-text{
    font-size: .3rem;
    color: #333333;
  }

  #loadingwrapper .loader-cicle::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    background: #cf2d28;
    border-radius: 50%;
    bottom: 0;
    right: 30px;
  }

  #loadingwrapper .loader-cicle::after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    background: #2ac845;
    border-radius: 50%;
    left: 30px;
    top: 0;
  }

  @keyframes ball-turn {
    0% {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
</style>



