import * as type from './types';
export default {
  showFoot:({commit,state})=>{
    commit(type.SHOW_FOOT)
  },
  hideFoot:({commit,state})=>{
    commit(type.HIDE_FOOT)
  },
  showLoading:({commit,state})=>{
    commit(type.SHOW_LOADING)
  },
  hideLoading:({commit,state})=>{
    commit(type.HIDE_LOADING)
  },
  Fallcount:({commit},n)=>{
    commit(type.ALLCOUNT,n)
  },
  Fgoodcount:({commit},n)=>{
    commit(type.GOODCOUNT,n)
  },
  Fmidcount:({commit},n)=>{
    commit(type.MIDCOUNT,n)
  },
  Fbadcount:({commit},n)=>{
    commit(type.BADCOUNT,n)
  },
  Fhascount:({commit},n)=>{
    commit(type.HASCOUNT,n)
  }
}



