<template>
  <div class="enter">
    <header class="mui-bar mui-bar-nav" style="background: #fff">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" style="color:#4595e5" @click="goBack"></a>
      <h1 class="mui-title">订单详情</h1>
    </header>
    <nav class="mui-bar mui-bar-tab" style="box-shadow: none;display: flex;align-items: center">
      <span style="display: flex;align-items: center" @click="goService">
        <span class="kefu"></span>
        <strong style="font-size: .3rem;margin-left: .15rem;color: #333333">联系客服</strong>
      </span>
     <!-- 待付款-->
      <div class="btnwai" v-if="orderState==1">
        <router-link tag="div" to="/downApp" class="btn btn1">取消订单</router-link>
        <div class="btn btn2" @click="goPlay">立即支付</div>
      </div>
      <!-- 待发货-->
      <div class="btnwai"  v-if="orderState==2">
        <div class="btn btn2"  @click="tipSendGoods">提醒发货</div>
      </div>
     <!-- 待收货-->
      <div class="btnwai" v-if="orderState==3">
        <router-link tag="div" to="/downApp" class="btn btn2">确认收货</router-link>
        <router-link tag="div" to="/downApp" class="btn btn2">查看物流</router-link>
      </div>
     <!-- 待评价-->
      <div class="btnwai" v-if="orderState==4">
       <router-link tag="div" to="/downApp" class="btn btn2">去评价</router-link>
      </div>
    </nav>
    <div class="mui-content">
      <div class="playstate">
        <p class="playstatetext">{{orderStateText}}</p>
      </div>
      <ul class="mui-table-view">
        <li class="mui-table-view-cell" style="text-align: left">
          <div>
            <p style="margin-bottom: .2rem"><span class="mui-icon mui-icon-person" style="font-size: .4rem"></span>{{userName}}   {{phone}}</p>
            <p><span class="mui-icon mui-icon-location" style="font-size: .4rem"></span>{{address}}</p>
          </div>
        </li>
      </ul>
      <ul class="mui-table-view" v-if="leaveMessage">
        <li class="mui-table-view-cell" style="text-align: left">
          <div>
            <p><span class="mui-icon mui-icon mui-icon-chat" style="font-size: .3rem"></span> 买家留言:</p>
            <p>{{leaveMessage}}</p>
          </div>
        </li>
      </ul>
      <ul class="mui-table-view" style="margin: .3rem 0">
        <li class="mui-table-view-cell" v-for="item in goodsList">
          <div class="goodsImg">
            <img :src="item.skuPic" />
          </div>
          <div class="goodsInfo">
            <p class='mui-ellipsis'>{{item.name}}</p>
            <p class='mui-ellipsis'>{{item.skuText.join(' 、')}}</p>
            <p><span>价格：￥ {{item.price|substr}}</span><span>X {{item.count}}</span></p>
          </div>
        </li>
      </ul>

      <ul class="mui-table-view">
        <li class="mui-table-view-cell">
          <span>订单总价</span>
          <span>￥ {{orderPrice | substr}}</span>
        </li>
        <li class="mui-table-view-cell">
          <span>商品总价</span>
          <span>￥ {{goodPrice | substr}}</span>
        </li>
        <li class="mui-table-view-cell">
          <span>运费</span>
          <span>￥ {{freight}}</span>
        </li>
      </ul>
      <ul class="mui-table-view" style="margin-top:.3rem">
        <li class="mui-table-view-cell" style="text-align: left">
          <div>
            <p style="margin-bottom: .2rem"><span class="danhaoGe">订单编号：</span>{{orderNumber}}</p>
            <p><span class="danhaoGe">下单时间：</span>{{orderTime | formatTime('Y-M')}}</p>
          </div>
        </li>
      </ul>
    </div>
    <!-- 猜你喜欢开始-->
    <guesslikes :guesslist='guess_list'></guesslikes>
    <div class="dianDi"></div>
  </div>
</template>

<script type="es6">
  import guesslikes from '../commom/guess_you_like.vue'
  import Api from '../commom/API.js';
  export default{
    data(){
      return {
        guess_list: [],
        userName:'',
        phone:'',
        address:'',
        leaveMessage:'',
        orderPrice:'',
        goodPrice:'',
        freight:'',
        orderNumber:'',
        orderTime:'',
        orderState:'',
        orderStateText:'',
        goodsList:[],
        orids: this.$route.query.orids,
      }
    },
    components: {guesslikes},
    mounted(){
      this.getOrderINfo()
    },
    methods: {
      // 获取页面详情
      getOrderINfo(){
        let data = {'orderid': this.orids};
        /*接口请求*/
        var url = Api.Apis + '/h5/h5order/myOrderDetail';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              this.guess_list = datas.guessGoodList;
              //订单信息
              var orderxingxi = datas.orderBean.order;
              this.userName = orderxingxi.receiveName;
              this.phone = orderxingxi.receivePhone;
              this.address = orderxingxi.receiveAddress;
              this.leaveMessage = orderxingxi.remarkText;
              this.orderPrice = orderxingxi.totalPriceNow;
              this.goodPrice = orderxingxi.needPay;
              this.freight = orderxingxi.freight;
              this.orderNumber = orderxingxi.ordercode;
              this.orderTime = orderxingxi.createTime;

              this.orderState = orderxingxi.orderStateId;
              if(this.orderState == 1){
                this.orderStateText = '等待买家付款';
              }else if(this.orderState == 2){
                this.orderStateText = '等待买家收货';
              }else{
                this.orderStateText = orderxingxi.state;
              }
              /*this.orderState = 4*/
                //商品信息
             this.goodsList = datas.orderBean.orderItem;
             console.log(datas)
            }
          }
        })
      },
      //返回上一级
      goBack(){
          this.$router.go(-1)
      },
      //去支付
      goPlay(){
        window.localStorage.for = 'xq';
        window.localStorage.orderCode = this.orderNumber;
        this.$router.push('/play')
      },
      //跳转客服
      goService(){
        /*window.location.href = "mqqwpa://im/chat?chat_type=wpa&uin=2753982986&version=1&src_type=web&web_src=oicqzone.com";*/
        this.$router.push("/downApp")
      },
      //提醒发货
      tipSendGoods(){
        alert("已提醒卖家发货")
      },
    }
  }
</script>

<style type="text/css" scoped>
  .enter .mui-content ul li{display: flex;justify-content: space-between;align-items: center}
  .goodsImg{width:30%;height:2.2rem;overflow: hidden}
  .goodsImg img{width:100%;height: 100%}
  .goodsInfo{width:65%;display: flex;flex-direction: column;justify-content: center}
  .goodsInfo p{display:flex;justify-content: space-between;width: 100%}
  .playstate{height:1.3rem;background: url(../../../static/img/my/my_orderdetail_bg@3x.png);background-size: 100% 100%;margin-bottom: .3rem;display: flex;align-items: center}
  .playstatetext{margin-left: .3rem;font-size: .3rem;color: #fff}
  .kefu{width:.6rem;height:.6rem;background: url(../../../static/img/warehouse/toolbar_icon_kefu@3x.png);display: block;margin-left: .3rem;background-size: 100% 100%}
  .btnwai{overflow: hidden;width:3.4rem;margin-left:1.5rem}
  .btn{width:1.5rem;height:.6rem;line-height:.55rem;font-size: .3rem;border-radius: 6px;margin-left: .2rem}
  .btn1{border:2px solid #999;color:#333;float: left}
  .btn2{border:2px solid #4595e5;color:#4595e5;float: right}
  .mui-bar-tab ~ .mui-content {
     padding-bottom: 0;
  }
  .dianDi{height:0.9rem}
  .danhaoGe{margin-right: .3rem}
</style>



