<template>
    <div id="guess_you_likes">
      <div class="guess_likes">
        <h2 class="guess_likes_title">猜你喜欢</h2>
        <div class="guess_likes_main">
          <div class="Detailcontent">
            <ul class="Detailcontent_Ul clearfix">
              <li class="Detailcontent_Li" v-for="item in guesslist">
                  <router-link :to="{path:'/details',query:{ids:item.goodId}}">
                    <img :src="item.goodPicUrl">
                    <div>{{item.goodName}}</div>
                    <p>￥{{item.goodMinPrice|substr}}</p>
                  </router-link>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
</template>
<script type='es6'>
     export default {
       props:['guesslist'],
     data(){
         return {
         }
     },
       mounted(){
       },
       methods:{
       }
   }

</script>
<style scoped>
.clearfix:after { content: ""; display: block; clear: both; }
.clearfix { zoom: 1; }
 .guess_likes{
    padding: 0 0.1rem;
  }
  .guess_likes_title{
    width: 100%;
    height: 0.96rem;
    font-size: 0.32rem;
    line-height: 0.96rem;
    color: #333;
    text-align: center;
    background: #efeff4;
    margin: 0;
  }
  .Detailcontent_Li{
    float: left;
    width: 3.44rem;
    height: 4.56rem;
    border-radius: 0.06rem;
    background: #ffffff;
    margin-bottom: 0.2rem;
  }
  .Detailcontent_Li a,.Detailcontent_Li img{
    display: block;
    width: 3.44rem;
    height: 3.44rem;
  }
  .Detailcontent_Li{
    margin-left: 0.1rem;
    margin-right: 0.1rem;
  }
.Detailcontent_Li div{
  max-width: 3.12rem;
  height: 0.6rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0 0.16rem;
  font-size: 0.28rem;
  color: #333333;
  line-height: 0.6rem;
}

.Detailcontent_Li p{
    -webkit-box-orient: horizontal;
    -moz-box-orient: horizontal;
    -webkit-box-direction: normal;
    -moz-box-direction: normal;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    flex-direction: row;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    max-width: 3.12rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 0.28rem;
    color: #e54560;
    margin: 0 0.16rem;
  }
</style>



