<template>
  <div class="userlogin">
    <ul class="litableview">
      <div class="toubu">
        <p style="color:#fff;text-align: right;width:94%"><router-link style="color:#fff;font-size: 0.32rem;" to="setting" tag="span">设置</router-link></p>
          <router-link tag="span" to="paizhao" class="userImg"><img :src="userImg"/></router-link>
          <p style="color:#fff;font-size: 0.32rem;">{{niCheng}}</p>
          <p style="color:#fff;font-size: 0.25rem;" @click="vipDetail(vipLevelName)">
            <img :src= vipLevelImg style="width:.25rem;margin-right: .15rem" />{{vipLevelName}}
          </p>
      </div>
      <router-link to="myorder" tag="li" class="mycell">
        <div class="mycell_inner">
          <p href="javascript:;" >我的订单</p>
          <span></span>
        </div>
      </router-link>
      <li class="mui-table-view-cell muitab">
        <router-link to="/myorder/order/1" tag="div" class="biaoqian">
          <span class="rednum" v-show="qpA!=0">{{qpA}}</span>
          <span><img src="../../../static/img/my/my_icon_payment@2x.png" /></span>
          <p>待付款</p>
        </router-link>
        <router-link to="/myorder/order/2" tag="div" class="biaoqian">
          <span class="rednum" v-show="qpB!=0">{{qpB}}</span>
          <span><img src="../../../static/img/my/my_icon_wait@2x.png" /></span>
          <p>待发货</p>
        </router-link>
        <router-link to="/myorder/order/3" tag="div" class="biaoqian">
          <span class="rednum" v-show="qpC!=0">{{qpC}}</span>
          <span><img src="../../../static/img/my/my_icon_send@2x.png" /></span>
          <p>待收货</p>
        </router-link>
        <router-link to="/myorder/order/4" tag="div" class="biaoqian">
          <span class="rednum" v-show="qpD!=0">{{qpD}}</span>
          <span><img src="../../../static/img/my/my_icon_comment@2x.png" /></span>
          <p>待评价</p>
        </router-link>
        <router-link tag="div" to="/downApp" class="biaoqian">
          <span><img src="../../../static/img/my/my_icon_aftermarket@2x.png" /></span>
          <p>售后服务</p>
        </router-link>
      </li>
    </ul>
    <div class="cblank"></div>
    <ul class=" zhongjian">
      <router-link to="/mycoupencc" tag="li" class="ntableview ">
        <div class="ntableview_inner">
          <i class="juaninner"></i>
          <p href="javascript:;" >我的优惠卷</p>
          <span></span>
        </div>
      </router-link>
    </ul>
    <div class="cblank"></div>
    <ul >
      <ul class=" zhongjian">
        <router-link to="/mycollect" tag="li" class="ntableview">
          <div class="ntableview_inner">
            <i></i>
            <p href="javascript:;" >我的收藏</p>
            <span></span>
          </div>
        </router-link>
        <li class="ntableview" @click="toAddress">
          <div class="ntableview_inner ntableview_lastinner">
            <i class="addressicon"></i>
            <p href="javascript:;" >我的地址</p>
            <span></span>
          </div>
        </li>
      </ul>
    </ul>

    <mt-popup
      v-model="popupVisible"
      class="pop"
      position="right">
      <p style="color: #fff;font-size: .35rem;text-align: center">会员等级说明</p>
      <table>
        <tr class="tt">
          <td>等级</td>
          <td>条件</td>
          <td>折扣</td>
        </tr>
        <tr v-for="item in vipList" class="tt" :class="{'ttb':item.vip}">
          <td><img :src=item.vipLogo style="width:.25rem;margin-right: .15rem" />{{item.vipName}}</td>
          <td>{{item.instruction.split(',')[1]}}</td>
          <td>{{item.vipDiscount}}</td>
        </tr>
      </table>
      <p @click="closeVipDetail" class="closeVipDetail">关闭</p>
    </mt-popup>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  export default{
    data(){
      return {
        userImg: '../../../static/img/my/my_avatar_def@2x.png',
        userPhone: '用户名',
        userId:'',
        niCheng:'',
        vipLevelImg:'',
        vipLevelName:'',
        qpA:'',
        qpB:'',
        qpC:'',
        qpD:'',
        popupVisible:false,
        vipList:[]
      }
    },
    mounted(){
      this.login();
    },
    methods: {
      toAddress(){
        window.localStorage.woLai = "1";
        this.$router.push('/address')
      },
      login(){
        /*页面挂载获取保存的cookie值，渲染到页面上*/
        let uname = getCookie('userId');
        this.userId=uname;
        /*如果cookie不存在，则跳转到登录页*/
        if (this.userId == "") {
          this.$router.push('/login')
        }else{
          window.localStorage.laiZi = '';
          let data = {'userId': this.userId};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5MyInfo/getUserInfo';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                  console.log("个人资料",datas)
                this.userPhone = datas.user.userPhone.substr(0, 3) + '****' + datas.user.userPhone.substr(7);
                this.niCheng = datas.user.userNickName;
                if(datas.user.userPhone === datas.user.userNickName){
                  this.niCheng = this.userPhone
                }
                this.vipLevelImg = datas.user.vipLogo;
                this.vipLevelName = datas.user.vipName;
                this.userImg = datas.user.userAvatar;
                this.qpD = datas.user.waitCommentOrderCount; // 待评价
                this.qpA = datas.user.waitPayOrderCount; // 待付款
                this.qpC = datas.user.waitReceiveOrderCount; // 待收货
                this.qpB = datas.user.waitSendOrderCount // 待发货
              }
            }
          })
        }
      },
      vipDetail(vipLevelName){
        this.popupVisible = true;
        /*页面挂载获取保存的cookie值，渲染到页面上*/
        let data = {'vipName': vipLevelName};
        /*接口请求*/
        var url = Api.Apis + '/h5/comicH5VIP/getVipList';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            if (datas.state == "success") {
              console.log(datas.vipList);
              this.vipList = datas.vipList
            }
          }
        })
      },
      closeVipDetail(){
        this.popupVisible = false;
      }
    }
  }
</script>

<style type="text/css" scoped>
  body{font-size:.2rem }
  .userImg{width:1.5rem;height:1.5rem;display: inline-block;border-radius: 1.5rem;margin-bottom: 0.1rem;overflow: hidden}
  .userImg img{width:100%;height:100%}
  .toubu{width:100%;height:3.7rem;text-align:center; background: url("data:image/png;base64,") no-repeat;background-size:100% 100%;  padding-top: 0.4rem;}
  .biaoqian{width:20%;text-align: center;position: relative}
  .biaoqian .mui-badge{position: absolute;top:0;right: 10%}
  .biaoqian span img{width:.5rem;vertical-align: bottom}
  /*.zhongjian{margin:.2rem 0;}*/
  .mui-navigate-right{text-align:left}
  .tupian1{display: block;  float: left;}
  .tupian1 img{width:0.5rem;vertical-align: bottom}
  .tupian2{display: block;  float: left;margin-left: 5%}
  .muitab{display: flex;justify-content: space-around;padding:.1rem}
  .mui-table-view-cell.muitab{
    padding: 0;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
  }
  .ntableview{
    height: 0.95rem;
    padding-left: 0.32rem;
    padding-right: 0.32rem;
    border-bottom: 1px solid #f0f0f0;
    border-top: none;
    width:100%
  }
.ntableview_inner{
  height: 0.95rem;
}
.ntableview p,.ntableview i{
  float: left;
}
.ntableview i{
  position: relative;
  float: left;
  width: 0.48rem;
  height: 0.48rem;
  top: 50%;
  right: 0;
  margin-top: -0.24rem;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100%;
}
.ntableview_inner i.juaninner{
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100% 100%;
}
.ntableview_inner i.addressicon{
  background: url(data:image/png;base64,) no-repeat center center;
    background-size: 100% 100%;
}
.ntableview p{
  font-size: 0.28rem;
  color: #333;
  line-height: 0.95rem;
  margin-left: 5%;
}
.litableview{
  background: #fff;
}
.mycell{
  height: 0.95rem;
  padding-left: 0.32rem;
  padding-right: 0.32rem;
}
.mycell_inner{
  height: 0.95rem;
  border-bottom: 1px solid #f0f0f0;
}
.mycell_inner p{
  float: left;
  font-size: 0.28rem;
  color: #333;
  line-height: 0.95rem;
   vertical-align: middle;
}
.mycell_inner span,.ntableview span{
  position: relative;
  float: right;
  width: 0.48rem;
  height: 0.48rem;
  top: 50%;
  right: 0;
  margin-top: -0.24rem;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100%;

}
  .mycell_inner span{
    margin-right: 0.32rem;
  }
  body{font-size:.2rem }
  .userlogin{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
  }
  .cblank{
    width: 100%;
    height: 0.2rem;
    background: #f0f0f0;
  }
  .biaoqian p{
    font-size: 0.24rem;
    color: #666;
    line-height: 0.56rem;
  }
  .ntableview_lastinner{
    border-bottom: none;
  }
  .rednum{
    position: absolute;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    top: -0.3rem;
    right: 15%;
    line-height: 0.6rem;
    text-align: center;
    background: #e04a69;
    font-size: 0.3rem;
    color: #fff;
    -moz-transform:scale(0.6);
    -webkit-transform:scale(0.6);
    -o-transform:scale(0.6);
  }
  .pop{width:80%;height:100%;background: rgba(0,0,0,.8);padding-top:.5rem}
  .table{}
  .table li{display: flex;justify-content: space-around;margin-bottom: .5rem}
  .table li p{color:#fff}
  table{margin: .2rem auto}
  tr td{padding: .3rem}
  .tt{color: #fff;}
  .ttb{color:red}
  .closeVipDetail{width:80%;height:.6rem;line-height: .6rem;display: block;background: #007aff;color: #fff;text-align: center;margin: 1rem auto;border-radius: 4px}
</style>



