<template>
  <div class="containe">
    <div id="playeorr" v-show="!isSh">
      <div v-html="chainurl" v-show="false"></div>
        <div class="main">
          <header>支付进行中</header>
          <section>
            <!--背景和提示信息-->
            <div class="bg-info">
              <div class="succeed-bg"></div>
              <div class="zfchenggong">支付君正在赶来中...</div>
              <!-- <div class="shuoming">我们会尽快安排工作人员发货</div> -->
            </div>
            <!--按钮-->
            <div class="btns">
              <a href="javascript:;" ref="btnT" @click="zhifu"><div class="chakan btn">{{btnMassg}}</div></a>
              <div class="jixu btn" @click="goPlay">重新选择支付方式</div>
            </div>
          </section>
        </div>

    </div>
    <div v-if="isSh" style="position: fixed; top:0; left:0;background: rgba(0,0,0,0.8);height:100%;width:100%">
      <img src="../../../static/img/weixinZhifu.png" style="width:90%;height:90%"/>
    </div>
    <div v-if="isShWei" style="position: fixed; top:0; left:0;background: #fff;height:100%;width:100%">
      <img :src="weiXinImg" style="margin:2rem auto .2rem" />
      <p style="width:100%;text-align: center;font-size: .35rem">请长按图片，识别图中二维码</p>
    </div>
  </div>
</template>
<script>
  import Api from '../commom/API.js';
  export default {
    data(){
      return {
        chainurl:'',
        ty:this.$route.query.ty,
        isSh:false,
        isShWei:false,
        btnMassg:'确认支付',
        orderCode:this.$route.query.orc,
        userId:this.$route.query.use,
        weiXinImg:''
      }
    },
    mounted(){
      this.getPlay()
    },
    beforeCreate(){
      window.addEventListener('pageshow', function( e ){
        if (e.persisted) {
          window.location.reload()
        }
      })
      window.addEventListener('pagehide', function(e) {
        var $body = $(document.body);
        $body.children().remove();
        setTimeout(function() {
          $body.append("<script type='text/javascript'>window.location.reload();<\/script>");
        });
      });
    },
    methods: {
      getPlay(){
        let data = {'ordercode': this.orderCode, 'payType': this.ty, 'userId': this.userId};
        /*接口请求*/
        var url = Api.Apis + '/h5/h5pay/subPayment';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              console.log('支付成功', datas);
              this.chainurl = datas.chainurl;
              this.weiXinImg = datas.imgUrl
              /*alert("支付成功")*/
            } else {
              console.log('失败')
            }
          }
        })
      },
      goPlay(){
        this.$router.push('/play')
      },
      goOrder(){
        this.$router.push('/myorder')
      },
      zhifu(){
        this.btnMassg = '重新支付'
        if (this.ty == "ali") {
          if (this.isWeiXin()) {
            this.isSh = true
          } else {
            this.isSh = false
            document.body.innerHTML = this.chainurl;
            //通过innerHTML添加的script代码不会自动执行，需要我们手动来执行
            var scripts = document.body.getElementsByTagName("script");
            for(var i=0;i<scripts.length;i++){ //一段一段执行script
              eval(scripts[i].innerHTML);
            }
          }
        } else if (this.ty = "wx") {
          if (this.isWeiXin()) {
            this.isShWei = true;
          } else {
            window.location.href = this.chainurl
            }
          }
        },
      isWeiXin(){
        var ua = window.navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == 'micromessenger') {
          return true;
        } else {
          return false;
        }
      }
    }
  }
</script>
<style type="text/css" scoped>
.main {
  width: 7.5rem;
}
header {
  width: 100%;
  height: .98rem;
  background: #20A4E9;
  font-size: .35rem;
  color: #FFFFFF;
  text-align: center;
  line-height: .98rem;
}
section {
  width: 100%;
}
section .bg-info {
  width: 100%;
  margin-bottom: 1.3rem;
}
section .bg-info .succeed-bg {
  width: 5.6rem;
  height: 4.5rem;
  background: url("../../../static/img/paying.png") no-repeat;
  background-size: 100%;
  margin: 0.8rem auto 0.4rem auto;
}
section .bg-info .zfchenggong {
  width: 100%;
  font-size: .4rem;
  color: #3a3c3e;
  text-align: center;
  margin-bottom: .1rem;
}
section .bg-info .shuoming {
  width: 100%;
  text-align: center;
  font-size: .28rem;
  color: #999999;
}
.btns {
  width: 100%;
}
.btns .btn {
  width: 5.14rem;
  height: .8rem;
  font-size: .3rem;
  text-align: center;
  line-height: .8rem;
  border-radius: .3rem;
}
.btns .chakan {
  color: #23aff7;
  border: 2px solid #23aff7;
  margin: 0 auto 0.35rem auto;
}
.btns .jixu {
  color: #ff9205;
  border: 2px solid #ff9205;
  margin: auto;
}
  .playeorrbg{
    width:90%;height:8rem;
    padding-top: 1rem;
    background: url("../../../static/img/warehouse/Bg@3x.png") no-repeat;
    background-size:100% 100%;
    margin:1.5rem auto
  }
  .playeorrImg{
    width:55%;height:3.5rem;
    background: url("../../../static/img/default-page/shibai@3x.png") no-repeat;
    background-size:100% 100%;
    margin: 0 auto;
  }
  .eorrTip{
    color:#4595e5;
    font-size: .35rem;
    text-align: center;
    width:100%;
    margin: .3rem 0 .6rem 0;
  }
  .mint-button {
    margin: 0 auto;
    color: inherit;
    display: block;
    font-size: 16px;
    height: .75rem;
    outline: 0;
    width: 80%;
  }
  .mint-button--primary{
    color:#fff
  }
</style>



