<template>
  <div class="play">
    <header class="mui-bar mui-bar-nav" style="background-color: #fff;box-shadow:none;border-bottom: 1px solid #d9d9d9">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack"></a>
      <h1 class="mui-title">支付方式</h1>
    </header>
    <nav class="mui-bar mui-bar-tab" style="box-shadow: none;display: flex;justify-content: center;">
       <button type="button" class="mui-btn mui-btn-primary" style="width:96%;margin-bottom: 0.3rem;height:.8rem" @click="playQQ">
        确定
      </button>
    </nav>

    <div class="mui-content">
      <div class="pay">
        <div id="orderInfo" class="orderInfo">
          <div class="orderInfo-needPay">应付金额：￥  {{qian|substr}}</div>
          <div class="orderInfo-state">
            <div class="orderInfo-state-left">
              <img src="../../../static/img/warehouse/flc_icon_pay_orderok@3x@3x.png" alt="" class="successSubOrder">
            </div>
            <div class="orderInfo-state-right">
              <p>订单提交成功！去付款咯~</p>
              <p>预计五天内发货</p>
              <p>请在{{timer | formatTime('MM-SS')}}内完成支付，超时后将取消订单</p>
            </div>
          </div>

          <div class="orderInfo-orderInfo">
            <p><span>订  单  号 :</span> {{danhao}}</p>
            <p><span>收货信息 :</span> {{userName}}  {{phone}}</p>
            <p>{{add}}</p>
            <p><span>商品名称 :</span> {{goodsName}}</p>
          </div>

          <div class="payment">
            <div class="chose-payment">选择支付方式</div>
            <ul class="mui-table-view">
              <li class="mui-table-view-cell" @click="Ali">
                <div class="aliPay-left">
                  <img src="../../../static/img/warehouse/flc_icon_alipay@3x.png" alt="" class="pay-icon">
                  <span>支付宝支付</span>
                </div>
                <span class="palyBtn" :class="{'palyBtnBlu':aliShow}"></span>
              </li>
              <li class="mui-table-view-cell" @click="weiXin">
                <div class="aliPay-left">
                  <img src="../../../static/img/warehouse/flc_icon_wechatpay@3x.png" alt="">
                  <span>微信支付</span>
                </div>
                <span class="palyBtn" :class="{'palyBtnBlu':wxShow}"></span>
              </li>
              <li class="mui-table-view-cell" @click="jingDong">
                  <div class="aliPay-left">
                    <img src="../../../static/img/warehouse/jdIcon.png" alt="" class="pay-icon">
                    <span>京东支付</span>
                  </div>
                  <span class="palyBtn" :class="{'palyBtnBlu':jdShow}"></span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Api from '../commom/API.js';
  import { Toast } from 'mint-ui';
  import {setCookie,getCookie} from '../commom/cookie.js'
	export default {
    data(){
      return {
        qian:'',
        timer:'',
        danhao:'',
        userName:'',
        phone:'',
        add:'',
        goodsName:'',
        orderID:this.$route.query.oid,
        typeLL:'',
        userId:'',
        aliShow:false,
        wxShow:false,
        jdShow:false,
      }
    },
    mounted(){
      this.daojishi();
      this.checkPay();
		},
    methods: {
      // 获取订单详情
      checkPay(){
        this.userId = getCookie('userId');
        let data = {'ordercode': window.localStorage.orderCode};
        var urlst = Api.Apis + '/h5/h5order/checkPayState';
        this.$http.post(urlst, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
              this.getOrderCode()
          }else{
              this.$router.push("/home")
          }

        })
      },
      getOrderCode(){
       	/*接口请求*/
        var url = Api.Apis + '/h5/h5order/getOrderByCode';
        let data1 = {'ordercode': window.localStorage.orderCode};
        this.$http.post(url, data1, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              console.log("成功",datas)
              this.qian = datas.orderBean.needPay;
              this.add = datas.orderBean.address;
              this.phone = datas.orderBean.phone;
              this.userName = datas.orderBean.receiveName;
              this.goodsName = datas.orderBean.goodInfo;
              this.danhao = datas.orderBean.ordercode;
              this.timer = datas.orderBean.leftTime;
            } else {
              console.log("失败了")
            }
          }
        })
      },
      // 监听选择的支付方式
      //支付宝
      Ali(){
        this.aliShow = true;
        this.wxShow = false;
        this.jdShow = false;
        this.typeLL='ali'
      },
      //微信
      weiXin(){
        this.aliShow = false;
        this.wxShow = true;
        this.jdShow = false;
        this.typeLL='wx'
      },
      //京东
      jingDong(){
        this.aliShow = false;
        this.wxShow = false;
        this.jdShow = true;
        this.typeLL='jd'
      },
      // 支付
      playQ(){
          console.log(this.typeLL);
        if(this.timer=='0'){
          Toast({message: "订单已失效",position: 'bottom'});
          return;
        };
        if(this.typeLL==''){
          Toast({message: "请选择支付方式",position: 'bottom'});
          return;
        };
        if(this.typeLL == "jd"){
          window.location.href = Api.Apis + "/h5/h5pay/jdpay?ordercode="+this.danhao;
        }else{
          let data = {'ordercode':this.danhao,'payType':this.typeLL,'userId':this.userId};
          /*接口请求*/
          var url = Api.Apis + '/h5/h5pay/subPayment';
          this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
          ).then((data) => {
            if (data.status == 200 && data.ok == true) {
              var datas = data.body.data;
              if (datas.state == "success") {
                console.log('支付成功',datas.chainurl)
                window.localStorage.chainurl = datas.chainurl;

                this.$router.push({path:'/aliplay',query:{ty:this.typeLL}})

              }else{
                console.log('失败')
              }
            }
          })
        }
      },
      // 实验
      playQQ(){
        console.log(this.typeLL);
        if (this.timer == '0') {
          Toast({message: "订单已失效", position: 'bottom'});
          return;
        }
        ;
        if (this.typeLL == '') {
          Toast({message: "请选择支付方式", position: 'bottom'});
          return;
        }
        ;
        if (this.typeLL == "jd") {
          window.location.href = Api.Apis + "/h5/h5pay/jdpay?ordercode=" + this.danhao;
        } else {
          this.$router.push({path: '/aliplay', query:{ty:this.typeLL,orc:this.danhao,use:this.userId}})
        }
      },

      //倒计时
      daojishi(){
        var TT = setInterval(function () {
           this.timer-=1000;
           if(this.timer<0){
                this.timer=0;
                this.$router.push('/home');
               clearInterval(TT)
           }
         }.bind(this),1000)
      },
      //返回上一级
      goBack(){
          if(window.localStorage.for == 'car'){
            this.$router.push('/shopingCar')
          }else{
            this.$router.go(-1)
          }

      },
    }
  }
</script>

<style type="text/css" scoped>
  .pay .orderInfo {background: #fff;}
  .pay .orderInfo .orderInfo-needPay {
    width: 100%;
    height: 1rem;
    padding: 0 0.32rem;
    font-size: 0.34rem;
    line-height: 1rem;
    color: #333;
    text-align: left;
    border-bottom: 0.01rem dashed #E5E5E5;
  }

  .pay .orderInfo .orderInfo-state {
    justify-content: space-between;
    display: flex;
    width: 100%;
    padding: 0.32rem;
  }

  .orderInfo-state-left {
    width: 1rem;
    height: 1rem;
    margin: .2rem 0.32rem 0 0;
  }

  .orderInfo-state-left .successSubOrder {
    width: 100%;
    height: 100%;
  }

  .orderInfo-state-right {width: 5.5rem;text-align: left;}
  .orderInfo-state-right p:nth-child(1){font-size: 0.32rem;color: #000;margin-bottom: .2rem}
  .orderInfo-state-right p:nth-child(2){font-size: 0.25rem;color: #666666;}
  .orderInfo-state-right p:nth-child(3){font-size: 0.25rem;color: #666666;}

  .orderInfo-orderInfo {
    width: 90%;
    margin:0 5% 5% 5%;
    padding: 0.24rem;
    background: #fafafa;
    text-align: left;
  }
  .orderInfo-orderInfo p{font-size: .25rem;color: #666666;line-height: .45rem}
  .orderInfo-orderInfo p span{font-size: .26rem}
  .orderInfo-orderInfo p:nth-child(3){padding-left: 1.1rem}

  .pay .orderInfo .payment {
    width: 7.5rem;
    background: #f0f0f0;
  }

  .pay .orderInfo .payment .chose-payment {
    padding: 0.2rem 0.32rem 0 0.32rem;
    font-size: 0.26rem;
    line-height: 0.68rem;
    color: #333;
    text-align: left;
  }

  .aliPay-left{
    -webkit-box-pack: justify;
    -moz-box-pack: justify;
    -ms-flex-pack: justify;
    -webkit-justify-content: flex-start;
    justify-content: flex-start;
    -webkit-box-align: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    -webkit-box-orient: horizontal;
    -moz-box-orient: horizontal;
    -webkit-box-direction: normal;
    -moz-box-direction: normal;
    -webkit-flex-direction: row;
    -moz-flex-direction: row;
    flex-direction: row;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
  }
  .aliPay-left span{font-size: .26rem}
  .aliPay-left img{width: 0.62rem;height: 0.62rem;margin-right: 0.25rem;}
  .mui-table-view-cell{display: flex;justify-content: space-between;align-items: center}
  .palyBtn{width:.42rem;height:.42rem;display: block;border: 1px solid #b2b2b2;border-radius: .4rem}
  .palyBtnBlu{width:.42rem;height:.42rem;display: block;border: 0;background: url(../../../static/img/shopping-bag/common_icon_chose@2x.png);background-size: 100% 100%;}
</style>



