<template>
  <div id="news_recommend">
    <header class="mui-bar mui-bar-nav" style="box-shadow: none;background-color: #F0F0F0;">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">{{titleName}}</h1>
    </header>
    <div class="news_main mui-content">
      <div class="titll">
        <div @click="renQiPaiXu">
          <span :class="{'spanHover':renqiOff}">人气</span>
        </div>
        <div style="display: flex;align-items: center" @click="jiaGePaiXu">
          <span :class="{'spanHover':jiageOff}">价格</span>
          <span class="classfygeshi" v-if="!jiageOff"></span>
          <span class="classfygeshi jiageUp" v-if="jiageOff" :class="{'jiageDown':jiageOnOff}"></span>
        </div>
        <div style="display: flex;align-items: center" @click="changeYuhui">
          <span :class="{'spanHover':shaiXuanOff}">筛选</span>
          <span class="classfygeshi shaiXuan" :class="{'shaiXuaned':shaiXuanOff}"></span>
        </div>
      </div>
      <div class="orderList-null" v-show="tipShow">
        <div class="orderList-null-icon"></div>
        <div class="orderList-null-title">该分类的商品还未上架，敬请期待！</div>
      </div>
      <div v-show="!tipShow"  class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <div class="mui-content collMain">
          <div class="collectShop" v-for="(item,index) in list">
            <router-link tag="div" :to="{path:'details',query:{ids:item.goodId}}" class="neirong">
              <img :src="item.goodPicUrl">
              <p class="biaoti">{{item.goodName}}</p>
              <p class="goodsPrice">￥ {{item.goodMinPrice | substr}}</p>
            </router-link>
          </div>
        </div>
      </div>
    </div>

    <mt-popup v-model="popupVisible" closeOnClickModal="false" position="bottom" class="pop">
      <div style="overflow: auto;max-height: 4.5rem;">
        <p class="popT">价格</p>
        <p class="popTqu">
          <input type="number" placeholder="最低价格" v-model="minPri"/> —— <input type="number" placeholder="最高价格"  v-model="maxPri"/>
        </p>
        <p class="popT" v-if="pinPaiList.length!=0">品牌</p>
        <p class="popTpai">
          <span @click="changePai(item,$event,item.brandid)" class="spanBl" :class="{'spanBlue':item.checked}" v-for="item in pinPaiList">{{item.brandName}}</span>
        </p>
      </div>
      <div style="height:1rem"></div>
      <div class="popEnter">
        <span class="popEnterR" @click="reSet">重置</span>
        <span class="popEnterW" @click="wanCheng">完成</span>
      </div>
    </mt-popup>
  </div>
</template>
<script type='es6'>
  import top_back from "../commom/top_back.vue";
  import Api from '../commom/API.js';
  import Vue from 'vue';
  import {Loadmore} from 'mint-ui';
  export default {
    data(){
      return {
        scrollMode:"auto",
        yId:this.$route.query.yid,
        eId:this.$route.query.eid,
        list:[],
        indenx:0,
        titleName:this.$route.query.name,
        tipShow:false,
        wrapperHeight: 0,
        popupVisible:false,
        jiageOnOff:false,
        shaiXuanOff:false,
        renqiOff:true,
        jiageOff:false,
        minPri:'',
        maxPri:'',
        pinPaiList:[],
        bandIdList:[]
      }
    },
    components:{
      top_back,
    },
    mounted(){
      this.getgoodlist();
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
    },
    methods:{
      // 返回上一级
      goBack(){
        this.$router.go(-1);
      },
      // 获取页面数据
      getgoodlist(){
          var url = Api.Apis+"/h5/h5good/goodList";
          var thats = this;
          this.$http.post(url,
            {oneLevelTypeId:this.yId,twoLevelTypeId:this.eId,index:thats.indenx,size:1500,popularSort:'asc'},
            {emulateJSON: true}).then(function(data){
            //判断接受数据是否成功
            if(data.status===200&&data.statusText==="OK"){
              var datas = data.body;
              console.log("数据",datas.data.goodBeanList.length);
              if(datas.data.goodBeanList.length == 0){
                  this.tipShow=true
              }else{
                this.tipShow=false;
                this.list = datas.data.goodBeanList;
                this.pinPaiList = datas.data.brandList;
              }
            }else{
              console.log("没有数据")
            }
          })
        },
      //筛选
      changeYuhui(){
        this.popupVisible = true;
        this.shaiXuanOff = true;
        this.jiageOff = false;
        this.renqiOff = false;

      },
      // 人气排序
      renQiPaiXu(){
        this.shaiXuanOff = false;
        this.renqiOff = true;
        this.jiageOff = false;
        this.getgoodlist();
      },
      // 价格排序
      jiaGePaiXu(){
        this.shaiXuanOff = false;
        this.renqiOff = false;
        this.jiageOff = true;
        this.jiageOnOff = !this.jiageOnOff;
        if(this.jiageOnOff){
          var url = Api.Apis+"/h5/h5good/goodList";
          var thats = this;
          this.$http.post(url,
            {oneLevelTypeId:this.yId,twoLevelTypeId:this.eId,index:thats.indenx,size:1500,priceSort:'desc'},
            {emulateJSON: true}).then(function(data){
            //判断接受数据是否成功
            if(data.status===200&&data.statusText==="OK"){
              var datas = data.body;
              console.log("数据",datas.data.goodBeanList.length);
              if(datas.data.goodBeanList.length == 0){
                this.tipShow=true
              }else{
                this.tipShow=false;
                this.list = datas.data.goodBeanList
                console.log("降序")
              }
            }
          })
        }else{
          var url = Api.Apis+"/h5/h5good/goodList";
          var thats = this;
          this.$http.post(url,
            {oneLevelTypeId:this.yId,twoLevelTypeId:this.eId,index:thats.indenx,size:1500,priceSort:'asc'},
            {emulateJSON: true}).then(function(data){
            //判断接受数据是否成功
            if(data.status===200&&data.statusText==="OK"){
              var datas = data.body;
              console.log("数据",datas.data.goodBeanList.length);
              if(datas.data.goodBeanList.length == 0){
                this.tipShow=true
              }else{
                this.tipShow=false;
                this.list = datas.data.goodBeanList
                console.log("升序")
              }
            }
          })
        }
      },
      // 品牌选择
      changePai(i,e,brandId){
        //事先注册checked
        if (typeof i.checked === 'undefined') {
          //注册checked属性
          this.$set(i, "checked", true)
        } else {
          i.checked = !i.checked
        }
        if(i.checked){
          //选中
          this.bandIdList.push(brandId)
          console.log('单点选中',this.bandIdList)
          this.bandIdList = [...new Set(this.bandIdList)]
        }else{
          //取消选中
          //删除数组中指定的元素
          Array.prototype.indexOf = function(val) {
            for (var i = 0; i < this.length; i++) {
              if (this[i] == val) return i;
            }
            return -1;
          };
          Array.prototype.remove = function(val) {
            var index = this.indexOf(val);
            if (index > -1) {
              this.splice(index, 1);
            }
          };
          this.bandIdList.remove(brandId);
          console.log('单点取消',this.bandIdList)
        }
      },
      // 重置
      reSet(){
        this.minPri = '';
        this.maxPri = '';
        this.bandIdList = [];
        this.getgoodlist();
        this.popupVisible = false;
        this.pinPaiList.forEach((value, index) => {
          value.checked = false
        })
      },
      // 完成
      wanCheng(){
        if(this.minPri != '' && this.maxPri != ''){
          if(this.minPri > this.maxPri){
            var jie = this.maxPri;
            this.maxPri = this.minPri;
            this.minPri = jie
          }
        }
        var brandidText = this.bandIdList.join(',');
        var url = Api.Apis+"/h5/h5good/goodList";
        var thats = this;
        this.$http.post(url,
          {oneLevelTypeId:thats.yId,twoLevelTypeId:thats.eId,index:thats.indenx,size:1500,minPriceText:thats.minPri*100,maxPriceText:thats.maxPri*100,brandidsText:brandidText},
          {emulateJSON: true}).then(function(data){
          //判断接受数据是否成功
          if(data.status===200&&data.statusText==="OK"){
            var datas = data.body;
            console.log("数据",datas.data.goodBeanList.length);
            if(datas.data.goodBeanList.length == 0){
              this.tipShow=true
            }else{
              this.tipShow=false;
              this.list = datas.data.goodBeanList;
              this.pinPaiList = datas.data.brandList;
              this.popupVisible = false;
            }
          }
        })
      }
    },
  }

</script>
<style type="text/css" scoped>
  #news_recommend .news_main ul li div{
    margin : 0 !important;
    height: 0.54rem !important;
    line-height: 0.54rem !important;
  }
</style>
<style lang='less' scoped>
  #news_recommend{
    .orderList-null {
      width: 6rem;
      margin: 0 auto;
      padding-top: 4rem;
    }
    .orderList-null .orderList-null-icon {
      width: 3rem;
      height: 3rem;
      background: url("../../../static/img/default-page/common@3x.png") no-repeat;
      background-size: 100%;
      margin: 0 auto;
    }
    .orderList-null .orderList-null-title {
      width: 100%;
      margin-top: 0.2rem;
      font-size: 0.28rem;
      line-height: 0.28rem;
      color: #666;
      text-align: center;
    }
    .mint-navbar .mint-tab-item.is-selected {
      border-bottom: 0;
      color: #26a2ff;
      margin-bottom: 0;
    }
    .mint-tab-item-label {
      color: inherit;
      font-size: 16px !important;
      line-height: 1;
    }
    .page-loadmore-wrapper {
      overflow: scroll
    }
    .mui-table-view.mui-grid-view .mui-table-view-cell {
      font-size: 17px;
      display: inline-block;
      margin-right: 0;
      padding: 10px 0 0 10px;
      text-align: left;
      vertical-align: middle;
      background: none;
    }
    .titll{height:0.8rem;width:100%;background: #fff;display: flex;justify-content: space-around;align-items: center}
    .titll span{font-size: 0.3rem}
    .spanHover{color:#007aff}
    .classfygeshi{width:.3rem;height: .3rem;margin-bottom: 0;display: inline-block;background: url(../../../static/img/warehouse/subnav_icon_price@3x.png);background-size: 100% 100%}
    .jiageUp{background: url(../../../static/img/warehouse/subnav_icon_price_up@3x.png);background-size: 100% 100%}
    .jiageDown{background: url(../../../static/img/warehouse/subnav_icon_price_down@3x.png);background-size: 100% 100%}
    .shaiXuan{background: url(../../../static/img/warehouse/icon_filter_n@3x.png);background-size: 100% 100%}
    .shaiXuaned{background: url(../../../static/img/warehouse/icon_filter_p@3x.png);background-size: 100% 100%}
    .pop{width:100%;padding:.3rem 0 0 0;height: 6rem;}
    .popT{font-size: .4rem;text-align: left;margin:0 0 .2rem .25rem}
    .popTqu{margin-bottom: .2rem;text-align: left;margin-left: .55rem}
    .popTqu input{padding:0;padding-left:10px;width:32%;font-size: .3rem;height:0.8rem;line-height: .8rem;background: #dddddd;border:none;margin: 0}
    .popTpai{text-align: left;margin-left: .55rem}
    .spanBl{padding:2px 6px;border: 1px solid #8f8f94;border-radius: 4px;color:#8f8f94;font-size: .35rem;display: inline-block;margin: 0 5px 8px 0}
    .popEnter{width:100%;position: fixed;bottom: 0;border-top:1px solid #999999;}
    .popEnter span{width:49%;height:1rem;font-size: .35rem;text-align: center;line-height: 1rem;display: inline-block}
    .spanBlue{color:#007aff;padding:2px 6px;border: 1px solid #8f8f94;border-radius: 4px;font-size: .35rem;display: inline-block;margin: 0 5px 8px 0;border-color:#007aff;}
    .popEnterR{color:#007aff}
    .popEnterW{background: #007aff;color:#fff}

    .collMain{width:100%;margin-top: 0.2rem;overflow: hidden;padding-left: .22rem}
    .collectShop{margin:0 .2rem .2rem 0   ;float:left;width:47%;position: relative;padding: 0.1rem 0.2rem 0.1rem .2rem;background: #fff;}
    .neirong, .neirong img{width:100%}
    .neirong img{height:3rem}
    .biaoti{font-size: .3rem;text-align: left;width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}
    .goodsPrice{color:red;text-align: left;font-size: .3rem;margin-top: .1rem}
  }

</style>



