<template>
  <div id="shopCar">
    <!-- 头部部分开始-->
    <div class="top_back clearfix">
      <header id="header" class="mui-bar mui-bar-nav">
        <h2 class="mui-title">购物袋</h2>
        <a class="mui-btn mui-btn-blue mui-btn-link mui-pull-right" @click="change_shop">{{btnIsShow?"编辑":"完成"}}
        </a>
        <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goback" style="color:#4595e5;"></a>
      </header>
    </div>
    <!-- 头部部分结束-->
    <!--购物车内容部分开始-->
    <div class="shopcar_shops">
      <div class="shopcar_shops_main" v-if="isShow">
        <!-- 没有物品在购物车中时-->
        <div class="shopcar_null">
          <img src="../../../static/img/shopping-bag/bag_icon_cat_empty@3x.png" alt=""/>
          <p>要不去添加点东西吧</p>
        </div>
      </div>
      <div class="shopcar_have" v-else>
        <ul class="shopcar_have_main">
          <li v-for="(item,indenx) in usergoodlist">
            <img src="../../../static/img/shopping-bag/sold-out@3x.png" class="xiaJia" v-if="!item.goodState"/>
            <img src="../../../static/img/shopping-bag/understock@3x.png" class="KuCun" v-if="item.goodState?item.skuStock==0:0"/>
            <span @click="isGobuys(item,item.priceNow,$event,item.goodState,item.skuStock)" :class="{'bac_blue':item.checked}"></span>
            <a @click="goDetail(item.goodState,item.goodid)" href="JavaScript:;">
              <img :src=item.goodDetailPicUrl alt=""/>
              <div v-if="changecont_isShow">
                <p>{{item.goodName}}</p>

                <p>款式：<span>{{item.valueText}}</span></p>

                <p>￥{{item.priceNow| substr}}<span> x{{item.count}}</span></p>
              </div>
              <div v-else class="">
                <p></p>

                <p style="text-align: right">款式：<span>{{item.valueText}}</span></p>

                <p class="change_count" v-if="item.goodState" v-show="item.skuStock!=0">￥{{item.priceNow| substr}}
                  <span @click="changecont(item,1)">+</span>
                  <span>{{item.count}}</span>
                  <span @click="changecont(item,-1)">-</span>
                </p>
              </div>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <!--购物车内容部分结束-->
    <!--猜你喜欢部分开始-->
    <guesslikes :guesslist='guess_list'></guesslikes>
    <!--猜你喜欢部分结束-->
    <!-- 底部结算部分开始-->
    <div class="shopingCar_nav">
      <div class="shopingCar_nav_main">
        <span @click="allChecked" :class="{'bac_blue1':!flage}"></span>
        <span>全选</span>
        <span>￥{{allPrices|substr}}</span>
        <button type="button" v-if="btnIsShow" @click="setAccounts">
          <!-- 跳转至结算页面-->
          <a style="color: #ffffff">结算</a>
        </button>
        <button type="button" v-else @click="setDelete">删除</button>
      </div>
    </div>
    <!-- 底部结算部分结束-->
  </div>
</template>
<script type='es6'>
  import Api from '../../components/commom/API.js'
  import guesslikes from '../commom/guess_you_like.vue'
  import {setCookie,getCookie} from '../commom/cookie.js'
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        guess_list: [],
        userid:"",
        isShow: true,
        usergoodlist: [],
        changecont_isShow: true,
        counts: 0,
        flage: true,
        isFlage: true,
        allPrices: 0,
        btnIsShow: true,
        skuids: "",
        addrid:0,
        editK:true,
        chk1:''
      }
    },
    components: {guesslikes},
    methods: {
      //返回上一级
      goback(){
        this.$router.push('/home')
      },
      //渲染数据
      getsfindcarts(){
        if(this.userid==''){
          this.$router.push('/login');
          return;
        }
        var url = Api.Apis + "/h5/h5goodcart/findUserGoodCart";
        let data = {"userid": this.userid};
        this.$http.post(url,data,{emulateJSON: true}).then(function (data) {
          var datas = data.body.data;
          if(data.body.state==="success") {
            //提示信息
            Toast({
              message: '已添加至购物车',
              iconClass: 'icon icon-success'
            });
          }
            //猜你喜欢
            this.guess_list = datas.guessGoodList;
            this.counts = datas.guessGoodList.count;
            if (datas.cartBeanList.length !== 0) {
              this.usergoodlist = datas.cartBeanList;
              console.log(this.usergoodlist)
              this.usergoodlist.forEach((value, index) => {
                if (value.goodState) {
                  if(value.skuStock){
                    this.chk1 ++;
                  }
                }
                console.log("chk1",this.chk1)
              });

              this.isShow = false
            } else {
              this.isShow = true
            }
          }, function (error) {
          Toast({message: '数据获取失败',position: 'bottom'});
          })
      },
      //点击编辑
      change_shop(){
        if (this.changecont_isShow === true) {
          this.editK = false;
          this.changecont_isShow = !this.changecont_isShow;
          this.btnIsShow = !this.btnIsShow;
          var chk2 = 0;
          this.usergoodlist.forEach((value, index) => {
            if (value.checked == true) {
              chk2 += 1;
            }
            console.log("chk2",chk2)
            if (chk2 != this.usergoodlist.length) {
              this.flage = true;
            }
          });
          console.log("完成")
        } else if (this.changecont_isShow === false) {
          this.editK = true;
          this.changecont_isShow = !this.changecont_isShow;
          this.btnIsShow = !this.btnIsShow
          console.log("编辑")
          this.usergoodlist.forEach((value, index)=> {
            value.checked = false;
          });
          this.flage = true;
        }
      },
      //点击选中需要添加至结算
      isGobuys(i, p, e,st,ssk){
        //事先注册checked
        if(this.editK){
          if(st) {
            if(ssk){
              if (typeof i.checked === 'undefined') {
                //注册checked属性
                this.$set(i, "checked", true)
              } else {
                i.checked = !i.checked
              }
              var chk = 0;
              this.usergoodlist.forEach((value, index) => {
                if (value.checked == true) {
                  chk += 1;
                }
                console.log("chk",chk)
                if (chk == this.chk1) {
                  this.flage = !this.flage;
                } else {
                  this.flage = true;
                }
              });
            }
          }
        }else{
          if (typeof i.checked === 'undefined') {
            //注册checked属性
            this.$set(i, "checked", true)
          } else {
            i.checked = !i.checked
          }
          var chk = 0;
          this.usergoodlist.forEach((value, index) => {
            if (value.checked == true) {
              chk += 1;
            }
            if (chk == this.usergoodlist.length) {
              this.flage = !this.flage;
            } else {
              this.flage = true;
            }
          });
        }
        this.calcTotalPrice()
      },
      //编辑购物车数量加
      changecont(item, conts){
        var thats = this;
        if (conts > 0) {
          item.count++;
          thats.calcTotalPrice();
          //判断库存还否足够
          if (item.count > item.skuStock) {
            item.count = item.skuStock;
            thats.calcTotalPrice();
            Toast({
              message: '亲，库存不够了哦^_^',
              position: 'middle',
              duration: 3000
            });
          } else {
            var url = Api.Apis + "/h5/h5goodcart/updateUserGoodCart";
            this.$http.post(url,
              {cartid: item.cartid, type: "plus", userid: thats.userid, skuid: item.skuid, count: item.count},
              {emulateJSON: true}
            ).then(function (data) {
              })
          }
        } else {
          item.count--;
          thats.calcTotalPrice();
          if (item.count < 1) {
            item.count = 1;
            thats.calcTotalPrice();
          } else {
            var url = Api.Apis + "/h5/h5goodcart/updateUserGoodCart";
            this.$http.post(url,
              {cartid: item.cartid, type: "less", userid: thats.userid, skuid: item.skuid, count: item.count},
              {emulateJSON: true}
            ).then(function (data) {
              })
          }
        }
      },
      //  计算金额
      calcTotalPrice(){
        this.allPrices = 0;
        this.usergoodlist.forEach((value, index)=> {
          if(value.goodState){
            if (value.checked) {
              if(value.skuStock){
                this.allPrices += value.priceNow * value.count;
                console.log(this.allPrices);
              }
            }
          }
        })
      },
      //  全选与取消
      allChecked(flages){
        this.usergoodlist.forEach((value, index)=> {
            console.log("数据",value.skuStock);
          if(this.editK){
            // 是否下架
            if(value.goodState){
              // 库存是否为空
              if(value.skuStock){
                console.log(value.skuStock);
                if (value.checked !== undefined) {
                  value.checked = this.flage;
                }
                else {
                  this.$set(value, "checked", true)
                }
              }
            }
          }else{
            if (value.checked !== undefined) {
              value.checked = this.flage;
            }
            else {
              this.$set(value, "checked", true)
            }
          }
        });
        this.flage = !this.flage;
        this.calcTotalPrice()
      },
      //  结算
      setAccounts(ids){
        var cartids = [];
        var sku = [];
        var cardID=[];
        this.usergoodlist.forEach((value, index)=> {
          if (value.checked) {
            cartids.push(value.cartid);
            sku.push(value.skuid)
            cardID.push(value.skuid)
          }
        });
        var ids = cartids.join(',');
        var skus = sku.join(',');
        if(ids!=''){
          window.localStorage.skuID = skus;
          window.localStorage.cartids = ids;
          window.localStorage.addrId = this.addrid;
          window.localStorage.userID = this.userid;
          window.localStorage.for = 'car';
          this.$router.push('/enterOrder')
        }else{
          Toast({message: '请选择要购买的商品',position: 'bottom'});
        }
      },
      //  删除
      setDelete(ids){
        var cartids = [];
        this.usergoodlist.forEach((value, index)=> {
          if (value.checked) {
            this.usergoodlist.splice(index,1);
            console.log('删除的',value.count);
            cartids.push(value.cartid);
          }
        });
        if(this.usergoodlist == 0){
          this.isShow = true
        }
        this.allPrices = 0;
        var ids = cartids.join(',');
        var url = Api.Apis + "/h5/h5goodcart/delUserGoodCart";
        this.$http.post(url,
          {cartids: ids, userid: this.userid},
          {emulateJSON: true}
        ).then((data)=> {
          console.log('删除')
        })
      },
      // 跳转详情
      goDetail(goodState,idd){
          if(this.btnIsShow){
            if(goodState){
              this.$router.push({path:'/details',query:{ids:idd}})
            }else{
              Toast({
                message: '亲，商品已下架了哦^_^',
                position: 'middle',
                duration: 2000
              });
            }
          }
		}
    },
    computed:{
      skuidss(){
        var ids = [];
        this.usergoodlist.forEach((value, index)=> {
          if (value.checked) {
            ids.push(value.skuid);
          }
        })
        this.skuids=ids.join(',');
        return this.skuids
      }
    },
    mounted(){
      this.userid = getCookie('userId');
      this.getsfindcarts();
    }
  }

</script>

<style lang='less' scoped>
  #shopCar {
    margin-top: 0.75rem;

  .top_back {

  .mui-bar-nav, .mui-bar {
    box-shadow: none;
    background-color: #ffffff;
  }

  .mui-title {
    line-height: 0.75rem;
    font-size: 0.32rem;

  }

  .mui-bar {
    height: 0.75rem;
  }

  .mui-bar .mui-btn-link {
    outline: none;
    height: 0.75rem;
    line-height: 0.75rem;
    font-size: 0.32rem;
  }

  .mui-bar .mui-btn-link:active {
    border: none;
  }

  #header {

  a {
    padding: 0;
    line-height: 0.75rem;
  }

  }
  }
  .shopcar_shops {
    background-color: #ffffff;

  .shopcar_shops_main {
    width: 100%;
    height: 4.53rem;

  .shopcar_null {
    padding-top: 1.1rem;
    text-align: center;

  img {
    width: 2.1rem;
    height: 2.1rem;
    padding: 0 0.42rem;
  }

  p {
    font-size: 0.22rem;
    color: #999999;
  }

  }
  }
  .shopcar_have {

  .shopcar_have_main {

  li {
    padding: 0 0.32rem;
    line-height: 2.25rem;
    position: relative;
    .xiaJia , .KuCun {
    width:1.2rem;
    height:0.8rem;
  }
  div {
    padding-left: 0.2rem;
    line-height: normal;
    vertical-align: middle;
    width: 4.42rem;
    height: 1.6rem;
    display: inline-block;

  p {
    text-align: left;
    font-size: 0.25rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  p:nth-of-type(1) {
    padding-bottom: 0.16rem;
  }

  p:nth-of-type(2) {
    font-size: 0.22rem;
    color: #999999;
    line-height: 0.37rem;
  }

  p:nth-of-type(3) {
    color: #E54560;
    font-size: 0.25rem;
    line-height: 0.87rem;

  span {
    color: #000000;
    float: right;
  }

  }
  }
  .bac_blue {
    background-image: url(../../../static/img/shopping-bag/common_icon_chose@3x.png);
    background-size: cover;
    border: none;
  }

  &
  >
  span {
    vertical-align: middle;
    display: inline-block;
    width: 0.42rem;
    height: 0.42rem;
    border-radius: 50%;
    border: 1px solid #999;
    margin-right: 0.2rem;
  }

  img {
    width: 1.6rem;
    height: 1.6rem;
    vertical-align: middle;
  }

  }
  .change_count {
    text-align: center;

  span {
    display: inline-block;
    width: 0.82rem;
    line-height: 0.57rem;
    border: 1px solid #999999;
  }

  span:nth-of-type(2) {
    border-left: none;
    border-right: none;
  }

  span:nth-of-type(3) {
    border-radius: 5px 0px 0px 5px;
  }

  span:nth-of-type(1) {
    border-radius: 0px 5px 5px 0px;
  }

  }
  }
  }
  }
  .shopingCar_nav {
    width: 100%;
    position: relative;
    margin-top: .96rem;

  .shopingCar_nav_main {
    display: flex;
    height: 0.96rem;
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #F7F7F7;
    border-top: 1px solid #999999;
    text-align: left;
    justify-content: space-between;
    align-items: center;

  span {
    font-size: .27rem;
  }

  span:nth-of-type(1) {
    margin-left: .32rem;
    float: left;
    width: .42rem;
    height: .42rem;
    border-radius: 50%;
    border: 1px solid #ccc;
  }

  span:nth-of-type(2) {

    margin-left: -1.7rem;
  }

  span:nth-of-type(3) {
    color: #E54560;
    margin-right: -1.8rem;
  }

  button {
    font-size: .26rem;
    height: 100%;
    line-height: .96rem;
    padding: 0;
    border: none;
    width: 2.28rem;
    background-color: #4595E5;
    color: #ffffff;
    text-align: center;
    border-radius: 0;
  }

  }
  }
  }
  .bac_blue1{background-image: url(../../../static/img/shopping-bag/common_icon_chose@3x.png);
    background-size: cover;
    border: none;
  }
  .xiaJia , .KuCun{position: absolute;bottom: 0.5rem;right: 10%;}
</style>



