<template>
    <div id="top_search">
      <header class="header clearfix" ref="header">
        <div class="header_main clearfix">
          <div class="fontsimg" @click="toYouhui">
            <div class="fontsimg_Coupons"></div>
            <span>优惠券</span>
          </div>
          <router-link tag="div" to="/seek" class="search">
	          <span class="mui-icon mui-icon-search"></span>
	          <!--<input type="text" class="to_search" placeholder="日本进口面膜" readonly='readonly'>-->
            <p class="seachText">{{ipt}}</p>
	      </router-link>
        </div>
      </header>
    </div>
</template>
<script type='es6'>
  import Api from '../../components/commom/API.js'
  import {setCookie,getCookie} from '../commom/cookie.js'
  export default {
    data(){
      return {
        ipt:'',
        resurts: 1
      }
    },
    mounted(){
      var that = this,
        apps = document.querySelector('#apps');
        apps.style.marginTop = that.$refs.header.offsetHeight + 'px'
        if (that.$refs.header.offsetHeight !== undefined) {
          window.document.body.onresize = function () {
            if (that.$refs.header) {
              apps.style.marginTop = that.$refs.header.offsetHeight + 'px'
            }
          }
        }
      this.hotSeek()
    },
    methods:{
      toYouhui(){
          if(getCookie('userId')){
            this.$router.push("/mycoupencc")
          }else{
            this.$router.push("/login")
          }
      },
      hotSeek(){
        var url = Api.Apis + '/h5/h5search/findHotSearchLogList'
        this.$http.post(url).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            this.ipt = datas.data.searchLogList[0].keyWord;
            console.log(this.ipt)
          }
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  #top_search{
  .header {
    padding-left: 1rem;
    padding-right: 0.7rem;
    width: 100%;
    position: fixed;
    z-index: 100;
    top: 0;
    background: #ffffff;
  .header_main{
    width:100%;
  .fontsimg{
    float:left;
    width:15%;
  span{
    margin: 0.08rem 0;
    font-size: 0.2rem;
  }
  .fontsimg_Coupons{
    margin:0 auto;
    width: 0.48rem;
    height: 0.48rem;
    background: url(../../../static/img/home_icon_ticket@3x.png);
    background-size: 100%;
  }
  }
  .search{
    padding-left: 0.3rem;
    position: relative;
    overflow: hidden;
    margin: 0 0.4rem;
    width: 3.86rem;
    height: 0.66rem;
    border: 0.01rem solid #d4d4d4;
    border-radius: 0.32rem;
    margin: 5px 0;
    float:right;
    background: #EBEBEB;
  .mui-icon{
    position: absolute;
    font-size: 0.3rem;
    left: 0;
    top: 29%;
    display: inline-flex;
    margin-left: 0.16rem;
    width: 0.32rem;
    height: 0.32rem;
    background-size: 100%;
  }
    .seachText{color: #8f8f94;height:0.66rem;line-height: 0.66rem;text-align: left;font-size: 0.3rem;padding-left: 0.3rem}
  }
  }

  }
  }
  #top_search .header .header_main .search input.to_search{
    padding-left: 0.2rem !important;
  }

</style>



