<template>
  <div class="coupen">
    <header class="mui-bar mui-bar-nav" style="background: #fff">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">限时抢购</h1>
    </header>
    <div class="mui-content">
      <div class="saleTittle">
        <strong @click="setYesterday" class="strong" :class="{'strongBlu':yesterOff}">
          昨日
        </strong>
        <strong @click="setToday" class="strong" :class="{'strongBlu':toOff}">
          今日
        </strong>
        <strong @click="setTomorrow" class="strong" :class="{'strongBlu':tomorOff}">
          明日
        </strong>
      </div>
      <div class="LunBo">
        <mt-swipe :auto="4000">
          <mt-swipe-item v-for="(item,key) in banerList" :key="key">
            <!-- 跳转至详情页-->
            <a><img :src=item.picUrl alt="item.title" @click="goWhere(item.dispatcherType,item.targetId,item.dispatcherUrlApp)"/></a>
          </mt-swipe-item>
        </mt-swipe>
      </div>
      <p style="padding: .15rem;background: #fff;font-size: .3rem;font-weight: bolder">
        距离本场结束 {{lefttimer | countDown}}
      </p>
      <ul class="saleList">
        <router-link :to="{path:'/details',query:{ids:item.goodId}}" tag="li" v-for="item in saleList">
          <div class="saleListImg">
            <img :src=item.goodPicUrl />
            <span class="goodsMask" v-if="item.stockCount == 0">
              <span class="goodsMaskText">已抢光</span>
            </span>
          </div>
          <div class="saleListShuoMi">
            <span class="saleListGoodsName">{{item.goodName}}</span>
            <p>限时价 ￥{{item.newPrice | substr}}</p>
            <p>原价 <span style="text-decoration: line-through">￥{{item.oldPrice | substr}}</span></p>
            <!--昨天-->
            <p v-if="yesterOff">
              <span>
                已结束
              </span>
              <span>去看看</span>
            </p>
            <!--今天-->
            <p v-if="toOff">
              <span>
                <strong style="position: absolute;top:0;left: 0;bottom: 0;right: 0;margin:auto;z-index: 50">剩余:{{parseInt(item.stockCount/item.sellCount * 100)}}%</strong>
               <span id="bar" :style="`width:${item.stockCount/item.sellCount*1.65}rem`"></span>
              </span>
              <span>马上抢</span>
            </p>
            <!--明天-->
            <p v-if="tomorOff">
              <span id="tomorSpan">
                限量{{item.stockCount}}件
              </span>
              <span>即将开始</span>
            </p>
          </div>
        </router-link>
      </ul>
    </div>

  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import { Toast } from 'mint-ui';
  export default{
    data(){
      return {
        selected:'today',
        banerList:[],
        saleList:[],
        yesterOff:false,
        toOff:true,
        tomorOff:false,
        lefttimer:'',
        goodState:''
      }
    },
    mounted(){
      this.getFlashSale(window.localStorage.ti);
      this.daojishi();
    },
    methods:{
      //获取限时购数据
      getFlashSale(Time){
        if(Time == "today"){
          this.yesterOff=false;
          this.toOff=true;
          this.tomorOff=false;
        }else if(Time == "tomorrow"){
          this.yesterOff=false;
          this.toOff=false;
          this.tomorOff=true;
        }else if(Time == "yesterday"){
          this.yesterOff=true;
          this.toOff=false;
          this.tomorOff=false;
        }
        /*页面挂载获取保存的cookie值*/
        let data = {'time':Time};
        /*接口请求*/
        var url = Api.Apis + '/h5/timegood/findTimeGoodListByTime';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              console.log(datas);
              this.banerList = datas.banners;
              this.saleList = datas.timeGoodBeanList;
              this.lefttimer = datas.endTime - datas.thisTime;
              console.log(this.lefttimer)
            }else{
              console.log('失败')
            }
          }
        })
      },
      //倒计时
      daojishi(){
        var TT = setInterval(function () {
          this.lefttimer-=1000;
          if(this.lefttimer<0){
            this.lefttimer=0;
            clearInterval(TT)
          }
        }.bind(this),1000)
      },
      //判断轮播图图片访问位置
      goWhere(state,goodId,imgUrl){
        if(state == 1){
          window.location.href = imgUrl
        }else{
          this.$router.push({path:'/details',query:{ids:goodId}})
        }
      },
      //返回上一级
      goBack(){
        this.$router.go(-1)
      },
      // 昨天
      setYesterday(){
        this.yesterOff=true;
        this.toOff=false;
        this.tomorOff=false;
        window.localStorage.ti = 'yesterday';
        this.getFlashSale('yesterday')
      },
      // 今天
      setToday(){
        this.yesterOff=false;
        this.toOff=true;
        this.tomorOff=false;
        this.getFlashSale('today');
        window.localStorage.ti = 'today';
      },
      // 明天
      setTomorrow(){
        this.yesterOff=false;
        this.toOff=false;
        this.tomorOff=true;
        this.getFlashSale('tomorrow');
        window.localStorage.ti = 'tomorrow';
      },
    }
  }
</script>

<style type="text/css" scoped>
  .saleTittle{height:.8rem;display: flex;width: 100%;justify-content: space-around;background: #fff}
  .strong{line-height: .8rem;font-size: .3rem;width: 33%}
  .strongBlu{border-bottom: 3px solid #2273dd;color: #2273dd;width: 33%}
  .LunBo{height:3.5rem;width: 100%;overflow: hidden}
  .LunBo img{height:100%;width:100%}
  .saleList{margin-top: .2rem;}
  .saleList li{padding:.2rem;background: #fff;display: flex;justify-content: flex-start;border-bottom: 1px solid #b6b6b6}
  .saleListImg{width:2.8rem;height:2.8rem;margin-right: 0.2rem;position: relative}
  .goodsMask{width:100%;height:100%;position: absolute;background: rgba(0,0,0,.1);z-index: 10;top:0;left: 0}
  .goodsMaskText{display: block;width:100%;height:2.8rem;line-height: 2.8rem;border-radius: 2.8rem;background:rgba(0,0,0,.3);font-weight: bolder;font-size: .4rem;color: #fff}
  .saleListImg img{height:100%;width:100%}
  .saleListShuoMi{width:4rem;text-align: left}
  .saleListGoodsName{margin-bottom: .15rem;font-weight: bolder;font-size:.3rem;text-align: left;overflow: hidden;text-overflow: ellipsis;  display: -webkit-box; -webkit-line-clamp: 2; -webkit-box-orient: vertical;}
  .saleListShuoMi p:nth-child(2){color:#e54560;font-weight: bolder;font-size:.3rem;}
  .saleListShuoMi p:nth-child(3){color:#9d9d9d;font-size:.2rem;margin: .1rem 0;}
  .saleListShuoMi p:nth-child(4){display: flex;align-items: center;justify-content: space-between}
  .saleListShuoMi p:nth-child(4) span:nth-child(1){text-align: center;line-height:0.55rem;width:1.65rem;height:.55rem;border: 2px solid #4595e5;display: block;color:#4595e5;background: #fff;border-radius: .5rem;overflow: hidden;position: relative}
  .saleListShuoMi p:nth-child(4) span:nth-child(2){text-align: center;line-height:0.65rem;width:1.65rem;height:.65rem;font-weight: bolder;display: block;color:#fff;background: #7aaae9;border-radius: 4px}
  #bar{ display: block;background: #dcebfa; float: left; height: 100%;text-align: center;line-height: 0.5rem; color: #FFF;border: none}
  #tomorSpan{text-align: left;line-height:0.55rem;width:1.65rem;height:.55rem;border: 0;display: block;color:#4595e5;font-weight: bolder;font-size: .3rem}
</style>




