<template>
  <div class="coupen">
   <header class="mui-bar mui-bar-nav" style="background: #fff">
    <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
    <h1 class="mui-title">我的优惠券</h1>
     <!--<a class="mui-pull-right" style="margin-top: .25rem;width: .5rem;height:.4rem;background-image: url('../../../static/img/saoyisao.png');background-size: 100% 100%"></a>-->
  </header>
    <div class="mui-content" style="background: #fff;height:100%">
      <mt-navbar v-model="selected">
        <mt-tab-item id="未使用">未使用</mt-tab-item>
        <mt-tab-item id="使用记录">使用记录</mt-tab-item>
        <mt-tab-item id="已过期">已过期</mt-tab-item>
      </mt-navbar>
      <!-- tab-container -->
      <mt-tab-container v-model="selected" style="padding:0 4%">
        <mt-tab-container-item id="未使用">
          <div class="redeem">
            <input type="text" v-model="redeemCode" class="inpredeem">
            <mt-button type="default" class="btnredeem" v-if="!redeemCode" @click.stop="cuoWu">兑换</mt-button>
            <mt-button type="primary" class="btnredeem" v-if="redeemCode" @click="redeemBtn">兑换</mt-button>
          </div>
          <div class="coupenStyle" v-for="(item,index) in availableCouponsList">
            <div class="usefultop">
              <div class="jiage"><span class="rmb">¥</span><span class="rmbNum">{{item.couponMoney}}</span></div>
              <div class="miaoshu">
                <p class="micheng">{{item.couponName}}(满{{item.couponLimitMoney}}元可用)</p>
                <p class="youxiaoqi">{{item.startDate}}-{{item.validate}}</p>
              </div>
            </div>
            <div class="usefulbont">
              <div class="shuoming" :class="{'shuomingBlue':item.checked}" >适用范围：{{item.couponDetail}}</div>
              <div class="gengDuo" @click="isGobuys(item,$event)"><p v-if="item.checked">︿</p><p v-if="!item.checked">﹀</p></div>
            </div>
          </div>
          <div class="orderList-null" v-if="tipsShowa">
            <div class="orderList-null-icon"></div>
            <div class="orderList-null-title">并没有可使用的优惠券</div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="使用记录">
          <div class="coupenStyle" v-for="(item,index) in usedCouponsList">
            <div class="top">
              <div class="jiage"><span class="rmb">¥</span><span class="rmbNum">{{item.couponMoney}}</span></div>
              <div class="miaoshu">
                <p class="micheng">{{item.couponName}}(满{{item.couponLimitMoney}}元可用)</p>
                <p class="youxiaoqi">{{item.startDate}}-{{item.validate}}</p>
              </div>
            </div>
            <div class="bont">
              <div class="shuoming" :class="{'shuomingBlue':item.checked}" >适用范围：{{item.couponDetail}}</div>
              <div class="gengDuo" @click="isGobuys(item,$event)"><p v-if="item.checked">︿</p><p v-if="!item.checked">﹀</p></div>
            </div>
          </div>
          <div class="orderList-null" v-if="usedCouponsList == 0">
            <div class="orderList-null-icon"></div>
            <div class="orderList-null-title">未有已使用的优惠券</div>
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="已过期">
          <div class="coupenStyle" v-for="(item,index) in timeOutCouponsList">
            <div class="top">
              <div class="jiage"><span class="rmb">¥</span><span class="rmbNum">{{item.couponMoney}}</span></div>
              <div class="miaoshu">
                <p class="micheng">{{item.couponName}}(满{{item.couponLimitMoney}}元可用)</p>
                <p class="youxiaoqi">{{item.startDate}}-{{item.validate}}</p>
              </div>
            </div>
            <div class="bont">
              <div class="shuoming" :class="{'shuomingBlue':item.checked}" >适用范围：{{item.couponDetail}}</div>
              <div class="gengDuo" @click="isGobuys(item,$event)"><p v-if="item.checked">︿</p><p v-if="!item.checked">﹀</p></div>
            </div>
          </div>
          <div class="orderList-null" v-if="timeOutCouponsList.length == 0">
            <div class="orderList-null-icon"></div>
            <div class="orderList-null-title">未有已过期的优惠券</div>
          </div>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>

  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
  import { Toast } from 'mint-ui';
  export default{
    data(){
      return {
        selected:'未使用',
        userID:'',
        redeemCode:'',
        availableCouponsList:[],
        timeOutCouponsList:[],
        usedCouponsList:[],
        tipsShowa:false,
      }
    },
    beforeCreate () {
      document.querySelector('body').setAttribute('style', 'background:#fff')
    },
    beforeDestroy () {
      document.querySelector('body').setAttribute('style', '')
    },
    mounted(){
      this.getCoupen()
    },
    methods:{
      //获取优惠券数据
      getCoupen(){
        /*页面挂载获取保存的cookie值*/
        this.userID = getCookie('userId');
        let data = {'userId':this.userID};
        /*接口请求*/
        var url = Api.Apis + '/h5/h5coupon/myCoupon';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              console.log(datas);
              this.availableCouponsList = datas.usefulCoupon; //未使用
              if(this.availableCouponsList.length==0){
                this.tipsShowa = true;
              }
              this.timeOutCouponsList = datas.validateCoupon;  // 已过期
              this.usedCouponsList = datas.usedCoupon; //已使用
            }else{
              console.log('失败')
            }
          }
        })
      },
      //兑换码为空时点击无效
      cuoWu() {
        Toast({message: '兑换码不能为空',position: 'bottom'});
      },
      // 兑换优惠券
      redeemBtn() {
        let data = {'userId':this.userID,'exchange':this.redeemCode};
        /*接口请求*/
        /*this.$store.dispatch('showLoading');*/
        var url = Api.Apis + '/h5/h5coupon/couponExchange';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          /*this.$store.dispatch('hideLoading');*/
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            console.log(datas)
            if (datas.state == "success") {
              Toast({message: datas.msg,position: 'bottom'});
              this.getCoupen();
              this.tipsShowa = false;
              this.redeemCode = '';
            }else{
              Toast({message: datas.msg,position: 'bottom'});
            }
          }
        })
      },
      // 点击显示更多
      isGobuys(i,e){
        //事先注册checked
        if (typeof i.checked === 'undefined') {
          //注册checked属性
          this.$set(i, "checked", true)
        } else {
          i.checked = !i.checked
        }
      },
      //扫一扫
      shaoMa(){

      },
      //返回上一级
      goBack(){
          this.$router.go(-1)
      }
    },
  }
</script>

<style type="text/css" scoped>
 /* mt-navbar{position: fixed;background: #fff;height:100%;width:100%;}*/
  p{text-align: left;font-size: .25rem}
  .mui-media img{margin-top:.2rem}
  .orderList-null {
    width: 3rem;
    margin: 3rem auto 0 auto;
  }
  .orderList-null .orderList-null-icon {
    width: 3rem;
    height: 3rem;
    background: url("http://yukicomic-pic.oss-cn-hangzhou.aliyuncs.com/yukicomicNew/m.yuki/icon_order@3x.png") no-repeat;
    background-size: 100%;
  }
  .orderList-null .orderList-null-title {
    width: 100%;
    margin-top: 0.2rem;
    font-size: 0.28rem;
    line-height: 0.28rem;
    color: #666;
    text-align: center;
  }
  .redeem{width:100%;padding:0 2%;overflow: hidden;margin-bottom: .2rem}
  .redeem .inpredeem{width:75%;float: left;background: #e6e6e6;border:0;border-radius: .15rem;text-align: center;height:.7rem;margin-bottom: 0}
  .redeem .btnredeem{width:20%;float: right;height:.7rem;font-size: .3rem;}
  .coupenStyle{
    margin-bottom: 0.3rem;
  }
  .top{height:2rem;display: flex;justify-content: flex-start;background:url("../../../static/img/images/coupenBG_top.jpg") no-repeat;background-size: 100% 100%;}
  .usefultop{height:2rem;display: flex;justify-content: flex-start;background:url("../../../static/img/images/useful_03.jpg") no-repeat;background-size: 100% 100%;}
  .jiage{margin:.75rem 0 0 .45rem}
  .jiage .rmb{font-size: .4rem;color:#fff;}
  .jiage .rmbNum{font-size: 0.8rem;color:#fff}
  .miaoshu{margin:0.4rem 0 0 0.4rem}
  .miaoshu .micheng{color:#fff;font-size: 0.3rem;margin-bottom: .2rem;}
  .miaoshu .youxiaoqi{color:#fff;font-size: 0.3rem}
  .bont{
    background: #b6babf;
    border-radius: 0 0 6px 6px;
    width:100%;
    display:flex;
    padding:0.05rem 0 0.1rem .35rem
  }
  .usefulbont{
    background: #e0bb7b;
    border-radius: 0 0 6px 6px;
    width:100%;
    display:flex;
    padding:0.05rem 0 0.1rem .35rem
  }
  .shuoming{
    font-size: .3rem;
    color:#fff;
    width:90%;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    text-align: left;
  }
  .shuomingBlue{
    font-size: .3rem;
    color:#fff;
    width:90%;
    white-space:normal;
    text-align: left;
  }
  .gengDuo{
    width:.35rem;
    height:.35rem;
    margin-left:.1rem
  }
  .gengDuo p{font-size: .3rem; color:#fff;}
  .mint-navbar .mint-tab-item {
    padding: 10px 0;
  }
  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid #26a2ff;
    color: #26a2ff;
    width:0.3rem;
    margin-bottom: 3px;
  }
</style>




