<template>
  <div id="navbar">
      <nav class="nav clearfix" ref="navbar">
        <div class="nav_main clearfix">
          <router-link tag="div" to="/home" class="fontsimg">
            <div class="fontsimg_Coupons fuli" v-if="!homeShow"></div>
            <div class="fontsimg_Coupons fuliH" v-if="homeShow"></div>
            <p>福利仓</p>
          </router-link>
          <router-link tag="div" to="/classify" class="fontsimg">
            <div class="fontsimg_Coupons fenlei" v-if="!classifyShow"></div>
            <div class="fontsimg_Coupons fenleiH" v-if="classifyShow"></div>
            <p>分类</p>
          </router-link>
          <router-link tag="div" to="/shopingCar" class="fontsimg">
            <div class="fontsimg_Coupons gouwu"></div>
            <p>购物袋</p>
          </router-link>
          <router-link tag="div" :to=url class="fontsimg">
            <div class="fontsimg_Coupons wode" v-if="!MyShow"></div>
            <div class="fontsimg_Coupons wodeH" v-if="MyShow"></div>
            <p>我的</p>
          </router-link>
          <!--<div class="fontsimg" @click="toMy">
            <div class="fontsimg_Coupons wode"></div>
            <p>我的</p>
          </div>-->
        </div>
      </nav>
    </div>
</template>
<script type='es6'>
  import {setCookie,getCookie} from '../commom/cookie.js'
  export default {
    data(){
      return {
        resurts: 1,
        url:'',
        homeShow:false,
        classifyShow:false,
        MyShow:false
      }
    },
    mounted(){
      this.toMy();
      this.lenRout(this.$route.path)
    },
    methods:{
      toMy(){
        var uname = getCookie('userId')
        /*如果cookie不存在，则跳转到登录页*/
        if (uname == "") {
          this.url='/My'
        }else{
          this.url='/userLogin'
        }
      },
      lenRout(lath){
          lath=lath.substring(1);
          if(lath == 'home'){
            this.homeShow = true
          }else{
            this.homeShow = false
          }
          if(lath.indexOf('classify/goods')!=-1){
            this.classifyShow = true
          }else{
            this.classifyShow = false
          }
          if(lath=='My'||lath=='userLogin'){
            this.MyShow = true
          }else{
            this.MyShow = false
          }
      }
    },
    watch:{
      $route(to){
        this.lenRout(to.path)
      }
    }
  }
</script>
<style lang="less" scoped>
  #navbar{
    .nav {
      border-top:1px solid #6d6d72;
      width: 100%;
      position: fixed;
      z-index: 100;
      bottom: 0;
      background: #ffffff;
      .nav_main{
        width:100%;
        display: flex;
        align-items: center;
        .fontsimg{
          width:25%;
          p{
            font-size: 0.25rem;
            display: block;
          }
          .fontsimg_Coupons{
            width: 0.45rem;
            height: 0.45rem;
            margin:0 auto;
          }
          .fuli{background: url(../../../static/img/tabbar_icon_home_n@3x.png);background-size: 100%;}
          .fuliH{background: url(../../../static/img/tabbar_icon_home_p@3x.png);background-size: 100%;}
          .fenlei{background: url(../../../static/img/tabbar_icon_column_n@3x.png);background-size: 100%;}
          .fenleiH{background: url(../../../static/img/tabbar_icon_column_p@3x.png);background-size: 100%;}
          .gouwu{background: url(../../../static/img/tabbar_icon_bag_n@3x.png);background-size: 100%;}
          .gouwuH{background: url(../../../static/img/tabbar_icon_bag_p@3x.png);background-size: 100%;}
          .wode{background: url(../../../static/img/tabbar_icon_me_n@3x.png);background-size: 100%;}
          .wodeH{background: url(../../../static/img/tabbar_icon_me_p@3x.png);background-size: 100%;}
        }
      }

    }
  }
  .router-link-active p{color:#4595e5}
</style>



