<template>
  <div class="pZiliao">
    <header class="mui-bar mui-bar-nav">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goBack" style="color:#4595e5"></a>
      <h1 class="mui-title">个人资料</h1>
    </header>
    <div class="mui-content">
      <ul class="mui-table-view">
        <router-link tag="li" to="/paizhao" class="mui-table-view-cell" id="facecell" style="position: relative">
          <a class="mui-navigate-right">
             <p class="ziliaogeshi"><span class="iconface">头像</span></p>
          </a>
          <span class="userImg"><img :src="userImg"/></span>
        </router-link>
        <router-link to="amendNick" tag="li" class="mui-table-view-cell">
          <a class="mui-navigate-right">
            <p class="ziliaogeshi"><span>昵称</span><span>{{niCheng}}</span></p>
          </a>
        </router-link>
        <li class="mui-table-view-cell" @click="changeSex">
          <a class="mui-navigate-right">
            <p class="ziliaogeshi"><span>性别</span><span>{{sex}}</span></p>
          </a>
        </li>
        <mt-popup v-model="popupVisible" closeOnClickModal="false" style="width:80%;" popup-transition="popup-fade">
          <ul class="mui-table-view">
            <li class="mui-table-view-li" @click="sexNan">
              <span class="palyBtn" :class="{'palyBtnBlu':sex=='男'}"></span>
              <span>男</span>
            </li>
            <li class="mui-table-view-li" @click="sexNv">
              <span class="palyBtn" :class="{'palyBtnBlu':sex=='女'}"></span>
              <span>女</span>
            </li>
          </ul>
        </mt-popup>

        <li class="mui-table-view-cell" @click="setDate">
          <a class="mui-navigate-right">
           <p class="ziliaogeshi"><span>年龄</span><span>{{age}}</span></p>
          </a>
        </li>
      </ul>
      <ul class="mui-table-view" style="margin: .2rem 0 0.5rem">
        <router-link tag="li" to="/changPhone" class="mui-table-view-cell">
          <a class="mui-navigate-right">
            <p class="ziliaogeshi"><span class="liL">手机号码</span><span class="liR">{{userPhone}}</span></p>
          </a>
        </router-link>
      </ul>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  import {setCookie,getCookie} from '../commom/cookie.js'
 	import { Toast } from 'mint-ui';
  export default{
    data(){
      return {
        userImg:'',
        niCheng:'',
        userID:'',
        sex:'',
        age:'',
        userPhone:'',
        birthDay:'',
        popupVisible:false
      }
    },
    mounted(){
      this.getInformation()
      },
    methods: {
      //修改年龄
      setDate(){
   					this.$calendar.show({
   						startYear:2001,
        			endYear:2017,
              onOk: (date) =>{
                this.birthDay = date.split('-').join('');
                let myDate = new Date();
								myDate.getFullYear(); //获取完整的年份
                let nowYear = myDate.toLocaleDateString().split('/').join('');
                let ageT = myDate.getFullYear() - date.substring(0,4) + 1;
								if(nowYear - this.birthDay < 0 ){
									Toast({message: '你选择的年份不对哦 ^_^',position: 'bottom'});
								}else{
									this.age = ageT;
                  let data = {'userId': this.userID,'birth':this.birthDay,'age':this.age};
                  /*接口请求*/
                  var url = Api.Apis + '/h5/h5MyInfo/updateAge';
                  this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
                  ).then((data) => {
                    if (data.status == 200 && data.ok == true) {
                      var datas = data.body.data;
                      if (datas.state == "success") {
                        Toast({message: '修改成功',position: 'bottom'});
                      }
                    }
                  })
								}
              }
          });
   			},
      // 返回上一级
    	goBack:function () {
          this.$router.push('/setting')
        },
      // 获取页面数据
      getInformation(){
          /*页面挂载获取保存的cookie值，渲染到页面上*/
          this.userID = getCookie('userId');
          /*如果cookie不存在，则跳转到登录页*/
          if (this.userID == "") {
            this.$router.push('/login')
          }else{
            let data = {'userId': this.userID};
            /*接口请求*/
            var url = Api.Apis + '/h5/h5MyInfo/getUserInfo';
            this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
            ).then((data) => {
              if (data.status == 200 && data.ok == true) {
                var datas = data.body.data;
                if (datas.state == "success") {
                  this.userPhone = datas.user.userPhone.substr(0, 3) + '****' + datas.user.userPhone.substr(7);
                  this.niCheng = datas.user.userNickName;
                  if(datas.user.userPhone === datas.user.userNickName){
                    this.niCheng = this.userPhone
                  }
                  this.userImg = datas.user.userAvatar;
                  this.age = datas.user.age;
                  this.sex = datas.user.userSex;
                }
              }
            })
          }
        },
      // 调出修改性别
      changeSex(){
          this.popupVisible = true
      },
      //修改女
      sexNv(){
        this.sex = '女';
        let data = {'userId': this.userID,'sex':this.sex};
        /*接口请求*/
        var url = Api.Apis + '/h5/h5MyInfo/updateSex';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              console.log("修改成功");
              this.popupVisible = false
            }
          }
        })
      },
      //修改男
      sexNan(){
        this.sex = '男';
        let data = {'userId': this.userID,'sex':this.sex};
        /*接口请求*/
        var url = Api.Apis + '/h5/h5MyInfo/updateSex';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              console.log("修改成功");
              this.popupVisible = false
            }
          }
        })
      }
    }

  }
</script>

<style type="text/css" scoped>
  .ziliaogeshi{width:94%;display: flex;justify-content: space-between;}
  .userImg{display: inline-block ;height:.7rem;width:.7rem;border-radius: 1rem;overflow: hidden;position: absolute;top:.1rem;right: .6rem}
  .userImg img{width:100%;height:100%}
  .palyBtn{width:.42rem;height:.42rem;display: block;border: 1px solid #b2b2b2;border-radius: .4rem}
  .palyBtnBlu{width:.42rem;height:.42rem;display: block;border: 0;background: url(../../../static/img/shopping-bag/common_icon_chose@2x.png);background-size: 100% 100%;}
  .mui-table-view-li{overflow: hidden;padding: .3rem;border-top: 1px solid #dddddd}
  .mui-table-view-li span:nth-child(1){float: left;}
  .mui-table-view-li span:nth-child(2){float: left;margin-left: .8rem;font-size: .3rem}
</style>



